import { PORTAL_SHARING_ENDPOINT } from '../esri.constants';
import { getPortalUrl } from './get-portal-url.function';
import { join } from '../../core/url/join.function';


export function getPortalSharingUrl(portalUrl: string): string {
  const basePortalUrl = getPortalUrl(portalUrl);
  return join(basePortalUrl, PORTAL_SHARING_ENDPOINT);
}
