import { toSinglePartPointArray } from './to-single-part-point.function';
import { SinglePoint } from './single-point';
import { getLastItem } from '../../core/array/get-last-item.function';
import { AnyPoint } from './any-point';
import { SORTED_BY_MEASURE_PROPERTY_FLAG } from '../geometry.constants';
import { toMultiPartPoint } from './to-multi-part-point.function';


export function getSortResultForPoints(firstPoint: SinglePoint, secondPoint: SinglePoint): number {
  const first: number[] = toSinglePartPointArray(firstPoint);
  const second: number[] = toSinglePartPointArray(secondPoint);
  const firstCoordinateMeasure: number = getLastItem(first);
  const secondCoordinateMeasure: number = getLastItem(second);

  return firstCoordinateMeasure - secondCoordinateMeasure;
}

export function sortPointsByMeasure(points: AnyPoint, cacheSort = false): void {
  if (points[SORTED_BY_MEASURE_PROPERTY_FLAG]) {
    return;
  }

  const coordinates: number[][] = toMultiPartPoint(points);
  const coordinatesHaveMeasureValue = Boolean(
    coordinates.length
    && coordinates[0].length > 2
  );

  if (coordinatesHaveMeasureValue) {
    coordinates.sort(getSortResultForPoints);
    if (cacheSort) {
      points[SORTED_BY_MEASURE_PROPERTY_FLAG] = true;
    }
  }
}
