// 3rd Party.
import { Point } from 'geojson';

// TSS.
import { SinglePoint } from './single-point';
import { isArray } from '../../core/type-check/is-array.function';


export function toSinglePartPointArray(point: SinglePoint): number[] {
  return isArray(point)
    ? (point as number[])
    : (point as Point).coordinates;
}
