import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { BaseResourceService } from '../../core/resource/base-resource.service';
import { Privilege } from './privilege';
import { SecurityConfig } from '../security-config';
import { join } from '../../core/url/join.function';


@Injectable({
  providedIn: 'root'
})
export class PrivilegeService extends BaseResourceService<Privilege> {
  constructor(protected http: HttpClient, config: SecurityConfig) {
    super(http, {
      url: join(config.serviceUrl, config.privilegesEndpoint)
    });
  }
}
