import { QueueWorkerToken } from './queue-worker-token';
import { WorkQueueStatus } from './work-queue-status';
import { average } from '../core/stats/average.function';


export class WorkQueueStatusTracker implements WorkQueueStatus {
  totalQueueSize = 0;

  protected readonly workers: QueueWorkerToken[] = [];

  get workerCount(): number {
    return this.workers.length;
  }

  get percentComplete(): number {
    return this.totalQueueSize >= 0
      ? Math.round(this.totalProcessed / this.totalQueueSize * 100) || 0
      : 0;
  }

  get totalComplete(): number {
    return this.workers.reduce((accumulator, current) => accumulator + current.totalComplete, 0) || 0;
  }

  get totalFailed(): number {
    return this.workers.reduce((accumulator, current) => accumulator + current.totalFailed, 0) || 0;
  }

  get totalProcessed(): number {
    return this.totalComplete + this.totalFailed;
  }

  get isWorking(): boolean {
    return Boolean(this.runningWorkerCount);
  }

  get isComplete(): boolean {
    return this.startTime && !this.isWorking;
  }

  get isCanceled(): boolean {
    const canceledWorkersCount = this.workers.filter(worker => worker.isCanceled).length;
    return Boolean(canceledWorkersCount && canceledWorkersCount === this.workers.length);
  }

  get runningWorkerCount(): number {
    return this.workers.filter(worker => worker.isWorking).length;
  }

  get startTime(): Date {
    const startTimes: number[] = this.workers
      .filter(worker => worker.startTime)
      .map(worker => worker.startTime
        ? worker.startTime.getTime()
        : null
      );

    return new Date(Math.min(...startTimes));
  }

  get endTime(): Date {
    const startTimes: number[] = this.workers
      .filter(worker => worker.startTime)
      .map(worker => worker.startTime
        ? worker.startTime.getTime()
        : null
      );

    return new Date(Math.max(...startTimes));
  }

  get remainingQueueSize(): number {
    return this.totalQueueSize - this.totalProcessed;
  }

  get estimatedSecondsLeft(): number {
    const remainingCount = this.remainingQueueSize;
    return remainingCount
      ? average(
        this.workers
          .map(worker => worker.calculateRemainingSeconds(remainingCount))
      )
      : 0;
  }

  get averageRecordsProcessedPerMinute(): number {
    return average(
      this.workers
        .map(worker => worker.recordsProcessedPerMinute)
    );
  }

  setQueueSize(totalQueueSize: number): WorkQueueStatusTracker {
    this.totalQueueSize = totalQueueSize;
    return this;
  }

  complete(): WorkQueueStatusTracker {
    this.workers.splice(0, this.workers.length);
    this.totalQueueSize = 0;
    return this;
  }

  addWorker(...workers: QueueWorkerToken[]): WorkQueueStatusTracker {
    workers.forEach(worker => {
      this.workers.push(worker);
    });

    return this;
  }

  getStatus(): WorkQueueStatus {
    return {
      isWorking: this.isWorking,
      isCanceled: this.isCanceled,
      isComplete: this.isComplete,
      totalProcessed: this.totalProcessed,
      startTime: this.startTime,
      endTime: this.endTime,
      percentComplete: this.percentComplete,
      totalQueueSize: this.totalQueueSize,
      remainingQueueSize: this.remainingQueueSize,
      totalComplete: this.totalComplete,
      totalFailed: this.totalFailed,
      estimatedSecondsLeft: this.estimatedSecondsLeft,
      workerCount: this.workers.length,
    };
  }
}
