// 3rd Party.
import { FeatureCollection } from 'geojson';

// TSS>
import { LrsFeatureCollection } from '../lrs-feature-collection';
import { EventFeature } from './event-feature';
import { EventLayer } from './event-layer';
import { WGS_84_SRID } from '../../geometry/geometry.constants';


export class EventFeatureCollection extends LrsFeatureCollection {
  type: 'FeatureCollection';
  features: Array<EventFeature>;

  constructor(collection: FeatureCollection, layer: EventLayer, spatialReferenceId = WGS_84_SRID) {
    super(collection);
    this.features = collection.features
      .map(f => new EventFeature(f, layer, spatialReferenceId));
  }

  static fromFeatures(layer: EventLayer, ...features: EventFeature[]): EventFeatureCollection {
    return new EventFeatureCollection({
      type: 'FeatureCollection',
      features
    }, layer);
  }
}
