// Angular.
import { Injectable } from '@angular/core';

// TSS.
import { PersistentStorage } from '../core/storage/persistent-storage.service';


export const CACHE_STORAGE_KEY_PREFIX = 'http-cache:';


/**
 * Configuration class for storing global data caching settings.
 */
@Injectable({
  providedIn: 'root'
})
export class PersistentCacheStorage extends PersistentStorage {
  constructor() {
    super();
    this.keyPrefix = CACHE_STORAGE_KEY_PREFIX;
  }
}
