import { DataSource } from '../data-source/data-source';
import { FeatureClassLayer } from '../maps/feature-class-layer';
import { IntersectionLayer } from './intersections/intersection-layer';
import { EventLayer } from './events/event-layer';
import { NetworkLayer } from './networks/network-layer';
import { RedlineLayer } from './redlines/redline-layer';
import { CenterlineLayer } from './centerlines/centerline-layer';
import { CalibrationPointLayer } from './calibration-points/calibration-point-layer';
import { MapServerMetadata } from '../maps/map-server-metadata';
import { getGeometryField, getIdField, LayerFieldDomain } from '../maps/fields/layer-field';
import { MapLayer } from '../maps/map-layer';
import { LrsLayer } from './lrs-layer';
import { LrsLayerDetail } from './lrs-layer-detail';
import { LrsInfo } from './lrs-info';
import { findLayer } from './layer-search';
import { mergeMapMetadata, mergeLrsMetadata, deepMergeMapMetadata } from './lrs-metadata-merge';
import { LRS_METADATA_VALIDATIONS } from './lrs-metadata-validations';
import { NonLrsLayer } from './non-lrs/non-lrs-layer';
import { Cloneable } from '../core/clone/cloneable';
import { isNotValue } from '../core/type-check/is-not-value.function';
import { clone } from '../core/clone/clone.function';
import { ValidationResult } from '../core/validation/validation-results';


function find(layerId: number, ...layerList: MapLayer[][]): MapLayer {
  return findLayer(layerId, ...layerList);
}


export class LrsMetadata extends MapServerMetadata implements Cloneable<LrsMetadata> {
  dataSource: DataSource;
  dataSourceId: string;
  name: string;
  serviceHasLrsMeta: boolean;
  networkLayers: NetworkLayer[] = [];
  eventLayers: EventLayer[] = [];
  redlineLayers: RedlineLayer[] = [];
  centerlineLayers: CenterlineLayer[] = [];
  calibrationPointLayers: CalibrationPointLayer[] = [];
  intersectionLayers: IntersectionLayer[] = [];
  nonLRSLayers: FeatureClassLayer[] = [];
  lrs: LrsInfo[] = [];
  domains: LayerFieldDomain[] = [];

  constructor(dataSource?: DataSource) {
    super();

    if (dataSource) {
      this.dataSource = dataSource;
      this.dataSourceId = dataSource.id;
    }
  }

  /**
   * Gets a layer by ID - searching across all types (event layers, network layers, etc).
   */
  getLayer(layerId: number): MapLayer {
    return find(layerId, this.getAllLayers());
  }

  getLrsLayer(layerId: number): LrsLayer {
    return (
      find(layerId, this.networkLayers, this.eventLayers, this.redlineLayers)
    ) as LrsLayer;
  }

  getLayerDetail(layerId: number): LrsLayerDetail {
    // TODO: Come up with a better way to check these layer types.
    // NOTE: We are doing all of these boolean checks instead of just simply calling
    // isEventLayer = this.isEventLayer(layerId) because of performance reasons.
    const isLrsLayer = this.isLrsLayer(layerId);
    const isEventLayer = this.isEventLayer(layerId);
    const isNetworkLayer = !isEventLayer && this.isNetworkLayer(layerId);
    const isCalibrationPointLayer = !isEventLayer && !isNetworkLayer && this.isCalibrationPointLayer(layerId);
    const isCenterlineLayer = !isEventLayer && !isNetworkLayer && !isCalibrationPointLayer && this.isCenterlineLayer(layerId);
    const isIntersectionLayer = !isEventLayer && !isNetworkLayer && !isCalibrationPointLayer && !isCenterlineLayer
      && this.isIntersectionLayer(layerId);
    const isRedlineLayer = !isEventLayer && !isNetworkLayer && !isCalibrationPointLayer && !isCenterlineLayer && !isIntersectionLayer
      && this.isRedlineLayer(layerId);
    const isTable = !isEventLayer && !isNetworkLayer && !isCalibrationPointLayer && !isCenterlineLayer && !isIntersectionLayer
      && !isRedlineLayer && this.isTable(layerId);
    const isNonLRSLayer = !isEventLayer && !isNetworkLayer && !isCalibrationPointLayer && !isCenterlineLayer && !isIntersectionLayer
      && !isRedlineLayer && !isTable && this.isNonLRSLayer(layerId);
    const layer: MapLayer = this.getLayer(layerId);
    const shapeField = layer ? getGeometryField(layer.fields) : null;
    const idField = layer ? getIdField(layer.fields) : null;

    return {
      layer,
      layerId,
      isEventLayer,
      isNetworkLayer,
      isCalibrationPointLayer,
      isCenterlineLayer,
      isIntersectionLayer,
      isRedlineLayer,
      isTable,
      isNonLRSLayer,
      isLrsLayer,
      shapeField,
      idField,
    };
  }

  isLrsLayer(layerId: number): boolean {
    return Boolean(
      this.getLrsLayer(layerId)
    );
  }

  isNetworkLayer(layerId: number): boolean {
    return Boolean(find(layerId, this.networkLayers));
  }

  isEventLayer(layerId: number): boolean {
    return Boolean(find(layerId, this.eventLayers));
  }

  isIntersectionLayer(layerId: number): boolean {
    return Boolean(find(layerId, this.intersectionLayers));
  }

  isRedlineLayer(layerId: number): boolean {
    return Boolean(find(layerId, this.redlineLayers));
  }

  isCenterlineLayer(layerId: number): boolean {
    return Boolean(find(layerId, this.centerlineLayers));
  }

  isCalibrationPointLayer(layerId: number): boolean {
    return Boolean(find(layerId, this.calibrationPointLayers));
  }

  isNonLRSLayer(layerId: number): boolean {
    return Boolean(find(layerId, this.nonLRSLayers));
  }

  isTable(layerId: number): boolean {
    return Boolean(find(layerId, this.tables));
  }

  /**
   * Merges map server general information into this LRS metadata instance.
   */
  mergeMapMetadata(mapMetadata: MapServerMetadata): LrsMetadata {
    mergeMapMetadata(this, mapMetadata);
    return this;
  }

  deepMergeMapMetadata(mapMetadata: MapServerMetadata): LrsMetadata {
    deepMergeMapMetadata(this, mapMetadata);
    return this;
  }

  /**
   * Merges other LRS metadata into this LRS metdata instance.
   */
  mergeLrsMetadata(lrsMetadata: LrsMetadata): LrsMetadata {
    mergeLrsMetadata(this, lrsMetadata);
    return this;
  }

  getNetworkLayer(layerId: number): NetworkLayer {
    return find(layerId, this.networkLayers) as NetworkLayer;
  }

  getEventLayer(layerId: number): EventLayer {
    return find(layerId, this.eventLayers) as EventLayer;
  }

  getIntersectionLayer(layerId: number): IntersectionLayer {
    return find(layerId, this.intersectionLayers) as IntersectionLayer;
  }

  getLrsLayers(): LrsLayer[] {
    return (this.eventLayers as LrsLayer[])
      .concat(this.networkLayers)
      .concat(this.redlineLayers);
  }

  getFeatureClassLayers(): FeatureClassLayer[] {
    return (this.eventLayers as FeatureClassLayer[])
      .concat(this.networkLayers)
      .concat(this.redlineLayers)
      .concat(this.calibrationPointLayers)
      .concat(this.centerlineLayers)
      .concat(this.nonLRSLayers);
  }

  getAllLayers(): MapLayer[] {
    return (this.getFeatureClassLayers() as MapLayer[])
      .concat(this.tables)
      .concat(this.layers);
  }

  getNetworkIdForLayer(layer: LrsLayer): number | null {
    let networkId: number = null;
    if (this.isEventLayer(layer.id)) {
      const eventLayer = layer as EventLayer;
      networkId = eventLayer.parentNetwork ? eventLayer.parentNetwork.id : null;
    }

    if (isNotValue(networkId)) {
      this.networkLayers.forEach(network => {
        if (network.eventLayers.find(l => l.id === layer.id)) {
          networkId = network.id;
        }
      });
    }

    return networkId;
  }

  getEventLayersByNetwork(networkLayerId: number): EventLayer[] {
    const networkLayer = this.getNetworkLayer(networkLayerId);
    return this.eventLayers.filter(layer =>
      layer.parentNetwork
      && layer.parentNetwork.id === networkLayerId
      || networkLayer.eventLayers
      && networkLayer.eventLayers.find(l => l.id === layer.id)
    );
  }

  rebuildLayers(): this {
    this.eventLayers = this.eventLayers.map(eventLayer => new EventLayer(find(eventLayer.id, this.layers), eventLayer));
    this.networkLayers = this.networkLayers.map(networkLayer => new NetworkLayer(find(networkLayer.id, this.layers), networkLayer));
    this.redlineLayers = this.redlineLayers.map(redlineLayer => new RedlineLayer(find(redlineLayer.id, this.layers), redlineLayer));
    this.centerlineLayers = this.centerlineLayers.map(centerlineLayer => new CenterlineLayer(centerlineLayer));
    this.calibrationPointLayers = this.calibrationPointLayers.map(
      calibrationPointLayer => new CalibrationPointLayer(calibrationPointLayer)
    );
    this.intersectionLayers = this.intersectionLayers.map(intersectionLayer => new IntersectionLayer(intersectionLayer));
    this.nonLRSLayers = this.nonLRSLayers.map(nonLrsLayer => new NonLrsLayer(find(nonLrsLayer.id, this.layers), nonLrsLayer));

    return this.assignCodedValueDomainsToFields();
  }

  assignCodedValueDomainsToFields(): this {
    if (this.domains.length) {
      this.getFeatureClassLayers()
        .forEach(layer => layer.applyFieldDomains(this.domains));
    }

    return this;
  }

  assign(source: any): this {
    Object.assign(this, source);
    return this;
  }

  clone(): LrsMetadata {
    const copy = clone(this);
    return new LrsMetadata()
      .assign(copy)
      .rebuildLayers();
  }

  validate(): ValidationResult {
    return LRS_METADATA_VALIDATIONS
      .check(this);
  }
}
