import { LengthUnit } from './length-unit';
import { MeterToUnitConversionFactor } from './meter-to-unit-conversion-factor';


function getMeterConversionFactors(): MeterToUnitConversionFactor {
  const factors: MeterToUnitConversionFactor = {};
  factors[LengthUnit.Foot] = 0.3048;
  factors[LengthUnit.Kilometer] = 1000;
  factors[LengthUnit.Meter] = 1;
  factors[LengthUnit.Mile] = 1609.34;

  return factors;
}

export const LENGTH_UNIT_METER_CONVERSION_FACTOR = getMeterConversionFactors();

