import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { AppConfig } from './app.config';
import { AppProfile } from './app-profile';
import { BaseResourceService } from '../core/resource/base-resource.service';
import { join } from '../core/url/join.function';


@Injectable({
  providedIn: 'root'
})
export class AppProfileService extends BaseResourceService<AppProfile> {
  constructor(protected http: HttpClient, config: AppConfig) {
    super(http, {
      url: join(config.serviceUrl, config.appProfileEndpoint),
      defaultRouteTokens: {
        appId: config.appId
      }
    });
  }
}

