import { Injectable } from '@angular/core';
import { ResourceConfig } from '../core/resource.config';
import { AutoConfig } from '../core/auto-config.decorator';


/**
 * Configuration class for storing global ESRI-related settings.
 */
@Injectable({
  providedIn: 'root'
})
@AutoConfig('DataSourceConfig')
export class DataSourceConfig extends ResourceConfig {
  dataSourcesEndpoint = 'api/data/sources/{id}';
  tablesEndpoint = 'api/data/sources/{id}/tables';
  columnsEndpoint = 'api/data/sources/{id}/tables/{table}/columns/{column}/{action}';
  tableActionEndpoint = 'api/data/sources/{id}/tables/{table}/{action}/{subaction}';
  editDataEndpoint = 'api/data/sources/{id}/tables/{table}/edit';
  applyEditsEndpoint = 'api/data/sources/{id}/applyedits';
  attachmentsEndpoint = 'api/attachments/{id}/{action}';
  skipColumnQuotes = false;
}
