import { STANDARDIZED_QUERY_DATE_FORMAT, ORACLE_DATE_FORMAT, DEFAULT_DATE_FORMAT } from '../core/date-time/date-time.constants';
import { isOracleProvider } from './is-oracle-provider.function';
import { DataSourceProviderType } from './data-source-provider-type';


export function getDateFormatForProvider(providerType: DataSourceProviderType): string {
  if (!providerType) {
    return STANDARDIZED_QUERY_DATE_FORMAT;
  } else if (isOracleProvider(providerType)) {
    return ORACLE_DATE_FORMAT;
  } else {
    return DEFAULT_DATE_FORMAT;
  }
}
