import { FeatureClassLayer } from './feature-class-layer';
import { MapTable } from './map-table';
import { clone } from '../core/clone/clone.function';
import { Cloneable } from '../core/clone/cloneable';


export class MapServerMetadata implements Cloneable<MapServerMetadata> {
  layers: FeatureClassLayer[];
  tables: MapTable[];

  assign(source: any): MapServerMetadata {
    Object.assign(this, source);
    return this;
  }

  clone(): MapServerMetadata {
    const copy = clone(this);
    return new MapServerMetadata()
      .assign(copy);
  }
}
