// Angular.
import { Injectable } from '@angular/core';

// 3rd party.
import { Observable } from 'rxjs';

// TSS.
import { CoreConfig } from '../core.config';
import { NotifyType } from './notify-type';
import { Notify } from './notify';
import { Logger } from '../logging/logger.service';
import { NotificationSubscriptionTracker } from './notification-subscription-tracker';
import { NotificationPromiseTracker } from './notification-promise-tracker';
import { NotifyResponse } from './notify-response';
import { format } from '../formatting/format.function';


/**
 * Notification service.
 */
@Injectable({ providedIn: 'root' })
export class NotifyService implements Notify {
  constructor(protected config: CoreConfig, protected log: Logger) {
  }

  notify(type: NotifyType, message: string, title: string, config?: any): NotifyResponse {
    // Add actual notification implementation here.
    return {};
  }

  clear(notifyId?: string, containerId?: string): void {
  }

  with<T>(source: Observable<T>): NotificationSubscriptionTracker<T> {
    return new NotificationSubscriptionTracker<T>(source, this, this.log);
  }

  promise<T>(source: Promise<T>): NotificationPromiseTracker<T> {
    return new NotificationPromiseTracker<T>(source, this, this.log);
  }

  wait(message: string, title = this.config.defaultNotifyWaitMsg, config?: any): NotifyResponse {
    return this.notify(NotifyType.Wait, message, title, config);
  }

  success(message: string, title = this.config.defaultNotifySuccessMsg, config?: any): NotifyResponse {
    return this.notify(NotifyType.Success, message, title, config);
  }

  error(message: string, title = this.config.defaultNotifyErrorMsg, error: any = null, config?: any): NotifyResponse {
    return this.notify(NotifyType.Error, format(message, { error }), title, config);
  }

  warn(message: string, title = this.config.defaultNotifyWarnMsg, error: any = null, config?: any): NotifyResponse {
    return this.notify(NotifyType.Warn, format(message, { error }), title, config);
  }

  info(message: string, title = this.config.defaultNotifyInfoMsg, config?: any): NotifyResponse {
    return this.notify(NotifyType.Info, message, title, config);
  }
}
