import { isValue } from '../type-check/is-value.function';
import { isObject } from '../type-check/is-object.function';


export function parametersToString(queryParameters: {}) {
  return Object
    .keys(queryParameters)
    .filter(key => isValue(queryParameters[key]))
    .map(key => {
      const value = isObject(queryParameters[key])
        ? JSON.stringify(queryParameters[key])
        : queryParameters[key];

      return `${key}=${value}`;
    })
    .join('&');
}
