import { LayerRendererSymbol } from './layer-renderer-symbol';


export function isClassBreakInfoEmpty(info: ClassBreakInfo): boolean {
  return !info
    || !info.classMaxValue
    && !info.label
    && !info.description
    && !Object.keys(info.symbol || {}).length;
}


export interface ClassBreakInfo {
  symbol: LayerRendererSymbol;
  label: string;
  classMaxValue: number;
  description?: string;
}
