import { PORTAL_SHARING_ENDPOINT } from '../esri.constants';
import { stripTrailingSlash } from '../../core/url/strip-trailing-slash.function';


export function getPortalUrl(portalUrlComponent: string): string {
  const [basePortalUrl] = (portalUrlComponent || '').split(PORTAL_SHARING_ENDPOINT);
  return basePortalUrl.endsWith('/')
    ? stripTrailingSlash(basePortalUrl)
    : basePortalUrl;
}
