import { DEFAULT_POINT_EQUALITY_PRECISION } from './equality.constants';

/**
 * Returns true if the two arguments are equal within the given precision.
 */
export function almostEqual(number1: number, number2: number, precision = DEFAULT_POINT_EQUALITY_PRECISION): boolean {
  const tolerance = 0.5 * Math.pow(10, -precision);
  return Math.abs(number1 - number2) < tolerance;
}

