// Angular.
import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

// 3rd Party.
import { Observable } from 'rxjs';
import { tap, map } from 'rxjs/operators';

// TSS.
import { getPortalUrl } from './get-portal-url.function';
import { getEsriMapServiceHttOptions, AngularHttpOption, addParamsToHttpOptions } from '../esri-map-service-request';
import { EsriServerToken } from '../esri-server-token';
import { EsriConfig } from '../esri-config';
import { DataSourceConfig } from '../../data-source/data-source-config';
import { BaseEsriService } from '../base-esri.service';
import { Logger } from '../../core/logging/logger.service';
import { EsriPortalAccessToken } from './esri-portal-access-token';
import { toFormData } from '../../http/to-form-data.function';
import { toEsriPortalAccessToken } from './to-esri-portal-access-token.function';
import { toEsriServerToken } from '../to-esri-server-token.function';
import {
  PORTAL_GENERATE_TOKEN_ENDPOINT,
  PORTAL_OAUTH_TOKEN_ENDPOINT
} from '../esri.constants';
import { removePortalAccessTokenFromHash } from './remove-portal-access-token-from-hash.function';
import { join } from '../../core/url/join.function';


@Injectable({
  providedIn: 'root'
})
export class EsriPortalService extends BaseEsriService {
  constructor(http: HttpClient, config: EsriConfig, dataConfig: DataSourceConfig, log: Logger) {
    super(http, config, dataConfig, log);
  }

  getTokenByCode(portalUrl: string, clientId: string, code: string, bodyData?: {}, params?: HttpParams): Observable<EsriPortalAccessToken> {
    const data = Object.assign({
      client_id: clientId,
      code,
      redirect_uri: removePortalAccessTokenFromHash(window.location.href),
      grant_type: 'authorization_code'
    }, bodyData);
    const formData = toFormData(data);

    return this.doPost(portalUrl, PORTAL_OAUTH_TOKEN_ENDPOINT, formData, params)
      .pipe(
        map(rawToken => toEsriPortalAccessToken(rawToken, portalUrl))
      );
  }

  refreshToken(
    portalUrl: string,
    clientId: string,
    refreshToken: string,
    bodyData?: {},
    params?: HttpParams
  ): Observable<EsriPortalAccessToken> {
    const data = Object.assign({
      client_id: clientId,
      refresh_token: refreshToken,
      grant_type: 'refresh_token'
    }, bodyData);
    const formData = toFormData(data);

    return this.doPost(portalUrl, PORTAL_OAUTH_TOKEN_ENDPOINT, formData, params)
      .pipe(
        map(rawToken => toEsriPortalAccessToken(rawToken, portalUrl))
      );
  }

  getServerToken(portalUrl: string, portalAccessToken: string, serverUrl: string, params?: HttpParams): Observable<EsriServerToken> {
    const additionalParams = {
      request: 'getToken',
      serverUrl,
      token: portalAccessToken,
      referer: window.location.host
    };

    return this.doGet(portalUrl, PORTAL_GENERATE_TOKEN_ENDPOINT, additionalParams, params)
      .pipe(
        map(token => toEsriServerToken(token))
      );
  }

  protected doGet<T>(portalUrl: string, endpoint: string, queryParams: {}, params?: HttpParams): Observable<T> {
    const basePortalUrl = getPortalUrl(portalUrl);
    const url = join(basePortalUrl, endpoint);
    const httpOption: AngularHttpOption = getEsriMapServiceHttOptions(params);

    addParamsToHttpOptions(httpOption, queryParams);
    return this.http
      .get<T>(url, httpOption)
      .pipe(
        tap((response: T) => this.handleEsriResponseError(response, url))
      );
  }

  protected doPost<T>(portalUrl: string, endpoint: string, formData: FormData, params?: HttpParams): Observable<T> {
    const basePortalUrl = getPortalUrl(portalUrl);
    const url = join(basePortalUrl, endpoint);
    const httpOption: AngularHttpOption = getEsriMapServiceHttOptions(params);

    return this.http
      .post<T>(url, formData, httpOption)
      .pipe(
        tap((response: T) => this.handleEsriResponseError(response, url))
      );
  }
}
