import { Orderable } from './orderable';
import { isNumber } from '../type-check/is-number.function';


export function sortByOrder<T extends Orderable>(list: T[]) {
  let compareResult = -1;
  list
    .sort((compare1: T, compare2: T) => {
      if (!isNumber(compare2.order) || compare1.order < compare2.order) {
        compareResult = -1;
      } else if (!isNumber(compare1.order) || compare1.order > compare2.order) {
        compareResult = 1;
      } else if (compare1.order === compare2.order) {
        compareResult = 0;
      }

      return compareResult;
    });
}
