// Angular.
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';

// 3rd Party.
import { Observable } from 'rxjs';

// TSS.
import { LrsConfig } from './lrs.config';
import { LrsMetadata } from './lrs-metadata';
import { join } from '../core/url/join.function';
import { BaseResourceService } from '../core/resource/base-resource.service';


@Injectable({
  providedIn: 'root'
})
export class DataSourceLrsMetadataService extends BaseResourceService<LrsMetadata> {
  constructor(protected http: HttpClient, config: LrsConfig) {
    super(http, {
      url: join(config.serviceUrl, config.dataSourceLrsMetadataEndpoint),
      idFieldName: 'dataSourceId'
    });
  }

  create(entity: LrsMetadata, routeTokens?: any, params?: HttpParams): Observable<LrsMetadata> {
    routeTokens = routeTokens || {};
    routeTokens.id = entity.dataSourceId;

    return super.create(entity, routeTokens, params);
  }
}
