export const FIRST_LETTER_SEPERATED_BY_SPACE_MATCH_REGEX = /^[a-z]|\s[a-z]/gm;
export const PASCAL_CASE_FIRST_LETTER_MATCH_REGEX = /(?=[A-Z])/;
export const LEADING_AND_TRAILING_UNDERSCORE_MATCH_REGEX = /^[\s_]+|[\s_]+$/gm;
export const LEADING_AND_TRAILING_HYPHEN_MATCH_REGEX = /^[\s-]+|[\s-]+$/gm;
export const UNDERSCORE_MATCH_REGEX = /[_\s]+/gm;
export const HYPHEN_MATCH_REGEX = /[-\s]+/gm;

