import { replaceTokens } from '../tokens/replace-tokens.function';
import { replaceToken } from '../tokens/replace-token.function';


// TODO: Expand this to have the full functionality of:
// https://github.com/davidchambers/string-format
// -RG.
export function format(template: string, tokens: any, removeEmptyTokens = false): string {
  Object.keys(tokens || {})
    .forEach(key => template = replaceToken(template, key, tokens[key]));

  if (removeEmptyTokens) {
    template = replaceTokens(template);
  }

  return template;
}
