import { EsriMapServiceGeometryToMeasureOptions } from './esri-geometry-to-measure-options';
import { DEFAULT_GEOMETRY_TO_MEASURE_OPTIONS } from './geometry-to-measure.constants';
import { isDate } from 'util';
import { toUtcEpochDate } from '../../core/date-time/conversion/to-utc-epoch-date.function';


export function getGeometryToMeasureOptions(...options: EsriMapServiceGeometryToMeasureOptions[]) {
  const mergedOption = Object.assign({}, DEFAULT_GEOMETRY_TO_MEASURE_OPTIONS);
  (options || [])
    .forEach(option => Object.assign(mergedOption, option));

  if (isDate(mergedOption.temporalViewDate)) {
    mergedOption.temporalViewDate = toUtcEpochDate(mergedOption.temporalViewDate as Date);
  }

  return mergedOption;
}
