import { getMeasure } from './get-measure.function';
import { LineGeometry } from './line-geometry';
import { SinglePoint } from '../point/single-point';
import { GetMeasureOptions } from './get-measure-options';


export function getMeasureFromFlatCoordinateSystem(line: LineGeometry, point: SinglePoint, options?: GetMeasureOptions): number {
  return getMeasure(line, point, Object.assign({ coordinateSystem: 'flat' }, options));
}
