import { SortDirection } from './sort-direction';
import { SortPropertyGetter } from './sort-property-getter';
import { isFunction } from '../type-check/is-function.function';
import { isString } from '../type-check/is-string.function';


export function sortAscending<T>(list: T[], ...sortProperties: SortPropertyGetter<T>[]): T[] {
  return sort(list, SortDirection.Ascending, ...sortProperties);
}

export function sortDescending<T>(list: T[], ...sortProperties: SortPropertyGetter<T>[]): T[] {
  return sort(list, SortDirection.Descending, ...sortProperties);
}

export function sort<T>(list: T[], direction: SortDirection = SortDirection.Ascending, ...sortProperties: SortPropertyGetter<T>[]): T[] {
  list.sort((item1: T, item2: T) => {
    let sortResponse = 0;

    if (sortProperties && sortProperties.length) {
      sortProperties.forEach(property => {
        if (sortResponse === 0) {
          const value1 = getPropertyValue(item1, property);
          const value2 = getPropertyValue(item2, property);
          sortResponse = getSortResultForString(value1, value2, direction);
        }
      });
    } else {
      sortResponse = getSortResultForString(item1, item2, direction);
    }

    return sortResponse;
  });

  return list;
}

function getPropertyValue<T>(item: T, property: SortPropertyGetter<T>): any {
  return isFunction(property)
    ? (property as (item: T) => string)(item)
    : item[property as string];
}

function getSortResultForString(value1: any, value2: any, direction: SortDirection): number {
  return direction === SortDirection.Ascending
    ? getSortResultForStringAscending(value1, value2)
    : getSortResultForStringDescending(value1, value2);
}

function ensureValue(value: any): any {
  return value === null || value === undefined ? '' : value;
}

function getSortResultForStringAscending(value1: any, value2: any): number {
  value1 = ensureValue(value1);
  value2 = ensureValue(value2);
  const compare1 = isString(value1) ? value1.toUpperCase() : value1;
  const compare2 = isString(value2) ? value2.toUpperCase() : value2;
  let compareResult = 0;

  if (compare1 < compare2) {
    compareResult = -1;
  } else if (compare1 > compare2) {
    compareResult = 1;
  }

  // NOTE: 0 = equals, 1 = greater, -1 less than.
  return compareResult;
}

function getSortResultForStringDescending(value1: string, value2: string): number {
  const compare1 = isString(value1) ? value1.toUpperCase() : value1;
  const compare2 = isString(value2) ? value2.toUpperCase() : value2;
  let compareResult = 0;

  if (compare1 > compare2) {
    compareResult = -1;
  } else if (compare1 < compare2) {
    compareResult = 1;
  }

  // NOTE: 0 = equals, 1 = greater, -1 less than.
  return compareResult;
}
