import { LayerRendererSymbol } from './layer-renderer-symbol';


export function isUniqueValueInfoEmpty(info: UniqueValueInfo): boolean {
  return !info
    || !info.value
    && !info.label
    && !info.description
    && !Object.keys(info.symbol || {}).length;
}

export interface UniqueValueInfo {
  symbol: LayerRendererSymbol;
  value: string;
  label: string;
  description?: string;
}
