// Angular.
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

// 3rd Party.
import { Observable, of } from 'rxjs';
import { tap } from 'rxjs/operators';

// TSS.
import { DataSourceService } from '../data-source/data-source.service';
import { DataSourceLrsMetadataService } from './data-source-lrs-metadata.service';
import { Logger } from '../core/logging/logger.service';
import { NotifyService } from '../core/notify/notify.service';
import { LrsMetadata } from './lrs-metadata';
import { LrsMetadataRetriever } from './lrs-metadata-retriever';
import { DataSource } from '../data-source/data-source';
import { LrsConfig } from './lrs.config';
import { EsriMapServerService } from '../esri/esri-map-server.service';
import { getDataSourceId } from '../data-source/get-data-source-id.function';


@Injectable({
  providedIn: 'root'
})
export class LrsMetadataService {
  lrsCache: { dataSourceId: string, metaData: LrsMetadata }[] = [];

  constructor(
    protected http: HttpClient,
    protected mapServer: EsriMapServerService,
    protected dataSourceService: DataSourceService,
    protected dataSourceLrsMetadataService: DataSourceLrsMetadataService,
    protected notify: NotifyService,
    protected config: LrsConfig,
    protected log: Logger) {
  }

  get(dataSource: string | DataSource, lrsMetadata?: LrsMetadata): Observable<LrsMetadata> {
    const dataSourceId = getDataSourceId(dataSource);
    const cachedMeta = this.lrsCache.find(cache => cache.dataSourceId === dataSourceId);

    if (cachedMeta) {
      return of(cachedMeta.metaData);
    }

    return new LrsMetadataRetriever(
      this.http,
      this.mapServer,
      this.dataSourceService,
      this.dataSourceLrsMetadataService,
      this.notify,
      this.log
    )
      .withDataSource(dataSource)
      .withLrsMetadata(lrsMetadata)
      .retrieve()
      .pipe(
        tap(meta => this.tryCacheLrsMetadata(meta))
      );
  }

  private tryCacheLrsMetadata(meta: LrsMetadata) {
    if (this.config.cacheInSessionLrsMetadata) {
      this.lrsCache.push({
        dataSourceId: meta.dataSourceId,
        metaData: meta
      });
    }
  }
}
