// 3rd Party.
import { Feature, LineString, MultiLineString, Geometry } from 'geojson';

// TSS.
import { LrsFeature } from '../lrs-feature';
import { NetworkLayer } from './network-layer';
import { toFeature } from '../feature';
import { SinglePoint } from '../../geometry/point/single-point';
import { LineGeometry } from '../../geometry/line/line-geometry';
import { getPoint } from '../../geometry/line/get-point.function';
import { getMeasure } from '../../geometry/line/get-measure.function';
import { WGS_84_SRID } from '../../geometry/geometry.constants';
import { Cloneable } from '../../core/clone/cloneable';
import { clone } from '../../core/clone/clone.function';


export class NetworkFeature extends LrsFeature implements Cloneable<NetworkFeature> {
  layer: NetworkLayer;

  constructor(feature: Feature, layer: NetworkLayer, spatialReferenceId = WGS_84_SRID) {
    super(feature, layer, spatialReferenceId);

    this.routeId = this.getValue(layer.compositeRouteIdFieldName);
    this.displayValue = this.displayValue || this.routeId;
    this.setMeasureFromGeometry();
  }

  static create(
    layer: NetworkLayer,
    properties: { [name: string]: any; } = {},
    geometry: LineString | MultiLineString = null
  ): NetworkFeature {
    const feature: Feature = toFeature(properties, geometry as Geometry);
    return new NetworkFeature(feature, layer);
  }

  getMeasure(point: SinglePoint): number {
    return getMeasure(this.geometry as LineGeometry, point);
  }

  getPoint(measure: number): SinglePoint {
    return getPoint(this.geometry as LineGeometry, measure);
  }

  clone(): NetworkFeature {
    const copyFeature = clone(this.toFeature());
    return new NetworkFeature(copyFeature, this.layer);
  }
}
