// 3rd Party.
import { MultiLineString, LineString } from 'geojson';

// TSS.
import { isArray } from '../../core/type-check/is-array.function';
import { LineGeometry } from './line-geometry';
import { isMultiPartLine } from './is-multi-part.function';


export function toMultiPartPaths(line: LineGeometry): number[][][] {
  if (isArray(line)) {
    const lineAsArray = (line as any[]);
    return isMultiPartLine(lineAsArray)
      ? lineAsArray
      : [lineAsArray];
  } else {
    const lineGeometry = line as LineString | MultiLineString;
    return lineGeometry.type === 'LineString'
      ? [lineGeometry.coordinates]
      : lineGeometry.coordinates;
  }
}

