// Angular.
import { DatePipe } from '@angular/common';

// TSS.
import { DEFAULT_DATE_FORMAT, STANDARDIZED_QUERY_DATE_FORMAT, ORACLE_DATE_FORMAT } from '../date-time.constants';
import { format } from '../../formatting/format.function';


export function formatDate(date: Date, dateFormat: string = DEFAULT_DATE_FORMAT): string {
  const dateTransformer = new DatePipe('en-US');

  if (dateFormat === STANDARDIZED_QUERY_DATE_FORMAT || dateFormat === ORACLE_DATE_FORMAT) {
    return format(dateFormat, {
      date: dateTransformer.transform(date, DEFAULT_DATE_FORMAT)
    });
  }

  const formattedDate = dateTransformer.transform(date, dateFormat);
  return `${formattedDate}`;
}
