// Angular.
import { Injectable } from '@angular/core';

// TSS.
import { AutoConfig } from '../core/auto-config.decorator';


/**
 * Configuration class for storing orientation-related settings.
 */
@Injectable({
  providedIn: 'root'
})
@AutoConfig('OrientationConfig')
export class OrientationConfig {
  allowOrientationMocking = true;
  allowHeadingMocking = true;
}
