import { NotifyService } from './notify.service';
import { Logger } from '../logging/logger.service';
import { BaseNotificationTracker } from './base-notification-tracker';
import { NotificationConfig } from './notification-config';


export class NotificationPromiseTracker<T> extends BaseNotificationTracker<T> {
  constructor(public source: Promise<T>, notify: NotifyService, log?: Logger, config: NotificationConfig = {}) {
    super(notify, log, config);
  }

  wait(message: string, title?: string): NotificationPromiseTracker<T> {
    super.wait(message, title);
    this.source = this.source
      .then((result: T) => {
        this.onSuccess(result);
        return result;
      })
      .catch((error: any) => {
        this.onFail(error);
        throw error;
      });
    return this;
  }

  waitIf(predicate: boolean | (() => boolean), message: string, title?: string): NotificationPromiseTracker<T> {
    return super.waitIf(predicate, message, title) as NotificationPromiseTracker<T>;
  }

  success(message: string, title?: string): NotificationPromiseTracker<T> {
    return super.success(message, title) as NotificationPromiseTracker<T>;
  }

  error(message: string, title?: string): NotificationPromiseTracker<T> {
    return super.error(message, title) as NotificationPromiseTracker<T>;
  }

  warn(message: string, title?: string): NotificationPromiseTracker<T> {
    return super.warn(message, title) as NotificationPromiseTracker<T>;
  }

  info(message: string, title?: string): NotificationPromiseTracker<T> {
    return super.info(message, title) as NotificationPromiseTracker<T>;
  }

  finally(onDone: (value: T | undefined | null | PromiseLike<T>) => void): NotificationPromiseTracker<T> {
    return super.finally(onDone) as NotificationPromiseTracker<T>;
  }

  then(onfulfilled?: ((value: T) => T | PromiseLike<T>) | undefined | null): Promise<T> {
    return this.source.then(onfulfilled);
  }

  catch(onrejected?: ((reason: any) => T | PromiseLike<T>) | undefined | null): Promise<T> {
    return this.source.catch(onrejected);
  }
}
