// 3rd Party.
import { MultiLineString } from 'geojson';

// TSS.
import { LineGeometry } from './line-geometry';
import { toMultiPartPaths } from './to-multi-part-paths.function';


export function toMultiLineString(line: LineGeometry): MultiLineString {
  const coordinates = toMultiPartPaths(line);

  return {
    bbox: null,
    type: 'MultiLineString',
    coordinates
  };
}
