import { STANDARDIZED_QUERY_DATE_FORMAT, ORACLE_DATE_FORMAT, DEFAULT_DATE_FORMAT } from '../date-time.constants';
import { formatDate } from './format-date.function';


export function formatDateForQuery(date: Date, dateFormat: string = DEFAULT_DATE_FORMAT): string {
  let quoteDate = true;
  if (dateFormat === STANDARDIZED_QUERY_DATE_FORMAT || dateFormat === ORACLE_DATE_FORMAT) {
    quoteDate = false;
  }

  const formattedDate = formatDate(date, dateFormat);
  return quoteDate ? `'${formattedDate}'` : `${formattedDate}`;
}
