// 3rd Party.
import { Feature, Geometry, LineString, Point, MultiLineString } from 'geojson';

// TSS.
import { LrsFeature } from '../lrs-feature';
import { EventLayer } from './event-layer';
import { LrsReferenceFeature } from '../lrs-reference-feature';
import { toFeature } from '../feature';
import { WGS_84_SRID } from '../../geometry/geometry.constants';
import { Cloneable } from '../../core/clone/cloneable';
import { clone } from '../../core/clone/clone.function';


export class EventFeature extends LrsFeature implements LrsReferenceFeature, Cloneable<EventFeature> {
  layer: EventLayer;

  constructor(feature: Feature, layer: EventLayer, spatialReferenceId = WGS_84_SRID) {
    super(feature, layer, spatialReferenceId);

    this.routeId = this.getValue(layer.routeIdFieldName);
    this.fromMeasure = this.getValue(layer.fromMeasureFieldName);
    this.toMeasure = this.getValue(layer.toMeasureFieldName);
  }

  static create(
    layer: EventLayer,
    properties: { [name: string]: any; } = {},
    geometry: Point | LineString | MultiLineString = null
  ): EventFeature {
    const feature: Feature = toFeature(properties, geometry as Geometry);
    return new EventFeature(feature, layer);
  }

  setRouteId(routeId: string): this {
    this.routeId = routeId;
    return this.setValue(routeId, this.layer.routeIdFieldName);
  }

  setFromMeasure(measure: number): this {
    this.fromMeasure = measure;
    return this.setValue(this.layer.fromMeasureFieldName, measure);
  }

  setToMeasure(measure: number): this {
    if (!this.isLinear) {
      throw new Error('Cannot set to measure for a point-based feature');
    }

    this.toMeasure = measure;
    return this.setValue(this.layer.toMeasureFieldName, measure);
  }

  setMeasureExtent(fromMeasure: number, toMeasure: number): this {
    return this
      .setFromMeasure(fromMeasure)
      .setToMeasure(toMeasure);
  }

  clone(): EventFeature {
    const copyFeature = clone(this.toFeature());
    return new EventFeature(copyFeature, this.layer);
  }

  protected setGeometryTypeProperties(): this {
    this.isLinear = Boolean(this.layer.toMeasureFieldName);
    this.isPoint = !this.isLinear;
    this.isPolygon = false;
    return this;
  }
}
