// 3rd Party.
import { Feature } from 'geojson';

// TSS.
import { MapFeature } from '../../maps/map-feature';


export const ALL_FIELD_CRITERIA = '*';

export function extractFeatureDataByLayer<T>(layerId: number, field: string = '*', ...features: MapFeature[]): T[] {
  return extractFeatureData<T>(field, ...features.filter(feature => feature.layerId === layerId));
}

export function extractFeatureData<T>(field: string = ALL_FIELD_CRITERIA, ...features: Feature[]): T[] {
  return features.map(feature => field === ALL_FIELD_CRITERIA
    ? feature.properties
    : feature.properties[field]);
}
