import { ValidationResult } from './validation-results';


let BASE_ID = 1;

export class ValidationCheck<T> {
  id: number;
  description: string;

  private subChecks: ValidationCheck<T>[] = [];
  private passCheck?: (value: T) => boolean;
  private failCheck?: (value: T) => boolean;
  private warnCheck?: (value: T) => boolean;
  private onlyIfCheck?: (value: T) => boolean;

  constructor(description: string, passCheck?: (value: any) => boolean) {
    this.description = description;
    this.passCheck = passCheck;
    this.id = BASE_ID++;
  }

  hasId(id: number): ValidationCheck<T> {
    this.id = id;
    return this;
  }

  message(description: string): ValidationCheck<T> {
    this.description = description;
    return this;
  }

  pass(passCheck?: (value: T) => boolean): ValidationCheck<T> {
    this.passCheck = passCheck;
    return this;
  }

  fail(failCheck?: (value: T) => boolean): ValidationCheck<T> {
    this.failCheck = failCheck;
    return this;
  }

  warn(warnCheck?: (value: T) => boolean): ValidationCheck<T> {
    this.warnCheck = warnCheck;
    return this;
  }

  onlyIf(onlyIfCheck?: (value: T) => boolean): ValidationCheck<T> {
    this.onlyIfCheck = onlyIfCheck;
    return this;
  }

  add(...innerChecks: ValidationCheck<T>[]): ValidationCheck<T> {
    innerChecks.forEach(inner => this.subChecks.push(inner));
    return this;
  }

  check(target: T): ValidationResult {
    const result = new ValidationResult();

    if (this.onlyIfCheck && !this.onlyIfCheck(target)) {
      result.skipped = true;
      return result;
    }

    if (this.passCheck) {
      result.passed = this.passCheck(target);
    }

    if (this.failCheck) {
      result.passed = !this.failCheck(target);
    }

    if (this.warnCheck) {
      result.warned = !this.warnCheck(target);
    }

    if (this.subChecks) {
      this.subChecks.forEach(c => {
        const innerResult = c.check(target);
        result.add(innerResult);
      });
    }

    return result;
  }
}
