import { DataSource } from './data-source';
import { isString } from '../core/type-check/is-string.function';


export function getDataSourceId(dataSource: string | DataSource): string {
  let dataSourceId;
  if (isString(dataSource)) {
    dataSourceId = dataSource as string;
  } else {
    const src = dataSource as DataSource;
    if (src) {
      dataSourceId = src.id;
    }
  }

  return dataSourceId;
}
