// Angular.
import { Injectable, Injector } from '@angular/core';

// 3rd party.
import { Observable, forkJoin, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';

// TSS.
import { GeolocatorSource } from './geolocator-source';
import { GEOLOCATOR_REGISTRY } from '../geolocation.constants';
import { Geolocator } from './geolocator';


@Injectable({ providedIn: 'root' })
export class GeolocatorSourceService {

  constructor(private readonly injector: Injector) {
  }

  getGeolocator(id: string): Geolocator {
    const registry = GEOLOCATOR_REGISTRY.find(registedSource => registedSource.id === id);
    return this.getLocatorFromRegistry(registry);
  }

  getGeolocators(): Geolocator[] {
    return GEOLOCATOR_REGISTRY
      .map(registry => this.getLocatorFromRegistry(registry));
  }

  getGeolocatorSource(id: string): Observable<GeolocatorSource> {
    return this.getGeolocatorSources()
      .pipe(
        map(devices => devices.find(device => device.id === id))
      );
  }

  getAvailableGeolocatorSources(): Observable<GeolocatorSource[]> {
    return this.getGeolocatorSources()
      .pipe(
        map(devices => devices.filter(device => device.isAvailable))
      );
  }

  getGeolocatorSources(): Observable<GeolocatorSource[]> {
    const availableObservables = GEOLOCATOR_REGISTRY
      .map(registry => {
        const locator = this.getLocatorFromRegistry(registry);
        const source: GeolocatorSource = {
          id: registry.id,
          name: locator.name,
          description: locator.description,
          accuracyUnit: locator.accuracyUnit,
          capabilities: locator.capabilities,
          isAvailable: false,
        };

        return locator.isAvailable()
          .pipe(
            tap(available => source.isAvailable = available),
            map(() => source)
          );
      });

    return availableObservables.length === 0
      ? of([])
      : forkJoin(availableObservables);
  }

  private getLocatorFromRegistry(registry: { id: string, constructor: any }): Geolocator {
    const instance: Geolocator = this.injector.get(registry.constructor);
    return instance;
  }
}
