// Angular.
import { Injectable } from '@angular/core';

// 3rd party.
import { Subject, Observable } from 'rxjs';


declare const window: any;

@Injectable({ providedIn: 'root' })
export class OnlineOfflineService {
  readonly connectionChanged: Observable<boolean>;
  private readonly internalConnectionChanged: Subject<boolean>;

  get isOnline() {
    return !!window.navigator.onLine;
  }

  constructor() {
    this.internalConnectionChanged = new Subject<boolean>();
    this.connectionChanged = this.internalConnectionChanged.asObservable();

    window.addEventListener('online', () => this.updateOnlineStatus());
    window.addEventListener('offline', () => this.updateOnlineStatus());
  }

  private updateOnlineStatus() {
    this.internalConnectionChanged.next(window.navigator.onLine);
  }
}
