import { getLastItem } from '../../core/array/get-last-item.function';
import { toMultiPartPoint } from './to-multi-part-point.function';
import { AnyPoint } from './any-point';
import { MeasureExtent } from '../line/measure-extent';
import { sortPointsByMeasure } from './sort-points.function';
import { getMeasureFromPoint } from './get-measure-from-point.function';


export function getMeasureExtentFromPoint(point: AnyPoint): MeasureExtent {
  const points: number[][] = toMultiPartPoint(point);
  const firstPoint = points[0];
  const lastPoint = getLastItem(points);
  let min;
  let max;

  sortPointsByMeasure(points);

  min = getMeasureFromPoint(firstPoint);
  max = getMeasureFromPoint(lastPoint);

  return { min, max };
}
