import { DataColumn } from './data-column';


export function stripTableName(tableName: string): string {
  return (tableName || '').split('.').slice(-1)[0];
}


export interface DataTable {
  columns: DataColumn[];
  dataSourceId: string;
  page: number;
  pageSize: number;
  primaryKeys: string[];
  records: any[];
  table: string;
}
