import { LayerField, LayerFieldCodedValueCode } from './layer-field';
import { getCodedValueName } from './get-coded-value-name.function';
import { isValue } from '../../core/type-check/is-value.function';


export function getCodedValueNameOrDefault(field: LayerField, value: LayerFieldCodedValueCode): string {
  const name = getCodedValueName(field, value);

  return isValue(name)
    ? name
    : String(value);
}
