// 3rd Party.
import { LineString, Feature, MultiPoint, Point, Polygon, MultiLineString, MultiPolygon } from 'geojson';
import { Observable, Subscription } from 'rxjs';
import destination from '@turf/destination';
import bearing from '@turf/bearing';

// TSS.
import { PositionPathBuilderOptions } from './position-path-builder-options';
import { Disposable } from '../core/disposable';
import { isPositionAcceptable } from './is-position-acceptable.function';
import { GpsPosition } from './gps-position/gps-position';
import { PositionPathUnfilteredGpsPosition } from './position-path-unfiltered-gps-position';
import { setMeasuresByGeometricLength } from '../geometry/geometry/set-measures-by-geometric-length.function';
import { isMultiPartLine } from '../geometry/line/is-multi-part.function';
import { PositionPathCalibrationPosition } from './position-path-calibration-position';
import { CalibrationPosition } from '../geometry/line/calibration/calibration-point';
import { PositionPathBuilderSummaryPosition } from './positon-path-builder-position-summary';
import { getMeasureFromPoint } from '../geometry/point/get-measure-from-point.function';
import { PATH_BUILDER_EMPTY_CALIBRATION_MEASURE_VALUE, PATH_BUILDER_CALCULATED_M_DEVICE_SOURCE } from './geolocation.constants';
import { getDistanceBetweenLatLng } from '../geometry/point/get-distance-between-points.function';
import { convertLengthUnits } from '../core/units/convert-length-units.function';
import { LengthUnit } from '../core/units/length-unit';
import { sumValues } from '../core/stats/sum-values.function';
import { getLastItem } from '../core/array/get-last-item.function';
import { isNumber } from '../core/type-check/is-number.function';
import { pointsAlmostEqual } from '../core/equality/points-almost-equal.function';


const UNPLACED_CALIBRATION_MEASURE_KEY = 'pending';

const DEFAULT_OPTIONS: PositionPathBuilderOptions = {
  gpsRequiredAccuracyMeters: 10,
  requireAccuracy: true,
  pointEquivalencyPrecision: 8,
  returnZValues: true,
  returnMValues: true,
  mValueUnit: 'miles',
  storeUnfilteredPath: false,
  calibrationMeasureSnappingDistanceDelta: 2,
  calibrationMeasureSnappingDistanceDeltaUnit: LengthUnit.Foot
};


export class PositionPathBuilder implements Disposable {
  readonly parts: GpsPosition[][];
  readonly unfilteredPath: GpsPosition[];
  readonly pathBreaks: {}[];
  readonly calibratedMeasures: Map<string, PositionPathCalibrationPosition>;
  private readonly options: PositionPathBuilderOptions;
  private trackSubscription: Subscription;

  get positionCount(): number {
    const counts = this.parts.map(part => part.length);
    return sumValues(counts);
  }

  get currentPartIndex(): number {
    return this.parts.length - 1;
  }

  get currentPart(): GpsPosition[] {
    return this.parts[this.currentPartIndex];
  }

  get lastPosition(): GpsPosition {
    return getLastItem(this.currentPart);
  }

  get hasPosition(): boolean {
    return Boolean(this.lastPosition);
  }

  get isEmpty(): boolean {
    return this.currentPart.length === 0;
  }

  get isValidPath(): boolean {
    return this.currentPart.length > 1;
  }

  get isSinglePoint(): boolean {
    return this.parts.length === 1 && this.currentPart.length === 1;
  }

  constructor(options?: PositionPathBuilderOptions) {
    this.parts = [[]];
    this.unfilteredPath = [];
    this.calibratedMeasures = new Map<string, PositionPathCalibrationPosition>();
    this.options = Object.assign({}, DEFAULT_OPTIONS, options);
  }

  trackPosition(positionStream: Observable<GpsPosition>): this {
    this.trackSubscription = positionStream.subscribe(position => this.addPosition(position));
    return this;
  }

  addPosition(position: GpsPosition, calibrationMeasure?: number): this {
    const accuracyCriteriaMet = !this.options.requireAccuracy
      || isPositionAcceptable(position, this.options.gpsRequiredAccuracyMeters);
    const positionNotAcceptable = !position || !accuracyCriteriaMet;

    if (positionNotAcceptable) {
      return this.tryStoreUnfilteredPath(position);
    }

    if (this.isEmpty) {
      return this.forceAddPosition(position);
    }

    const pointsAtSameLocation = this.arePositionsEquivalent(this.lastPosition, position);
    const bothPointsAtStandStill = this.lastPosition.speed === 0 && position.speed === 0 || pointsAtSameLocation;
    if (bothPointsAtStandStill) {
      this
        .tryStoreUnfilteredPath(position)
        .replaceLastPositionIfBetter(position);

      return this;
    }

    this.forceAddPosition(position);
    if (isNumber(calibrationMeasure)) {
      this.addCalibrationMeasureToLastPosition(calibrationMeasure);
    }

    return this;
  }

  startNewPart(): this {
    this.parts.push([]);
    return this;
  }

  removePositionByIndex(vertexIndex: number, partIndex: number = 0): this {
    const part = this.parts[partIndex];
    if (!part) {
      throw new Error(`Invalid part index" ${partIndex}`);
    }

    part.splice(vertexIndex, 1);

    const key = this.getCalibrationMeasureLookupKey(partIndex, vertexIndex);
    if (this.calibratedMeasures.has(key)) {
      this.calibratedMeasures.delete(key);
    }

    return this;
  }

  removePosition(position: GpsPosition): this {
    let partIndex = -1;
    let vertexIndex = -1;

    this.parts.forEach((part, index) => {
      const foundPositionIndex = part.indexOf(position);
      if (foundPositionIndex > -1) {
        partIndex = index;
        vertexIndex = foundPositionIndex;
      }
    });

    if (partIndex > -1 && vertexIndex > -1) {
      return this.removePositionByIndex(vertexIndex, partIndex);
    }

    return this;
  }

  removeCalibrationMeasure(measure: number): this {
    Array.from(this.calibratedMeasures.values())
      .filter(calibration => calibration.measure === measure)
      .forEach(calibration =>
        this.removeCalibrationMeasureByIndex(calibration.vertexIndex, calibration.partIndex)
      );

    return this;
  }

  removeCalibrationMeasureByIndex(vertexIndex: number, partIndex: number = 0): this {
    const key = this.getCalibrationMeasureLookupKey(partIndex, vertexIndex);
    if (this.calibratedMeasures.has(key)) {
      this.calibratedMeasures.delete(key);
    }

    return this;
  }

  addCalibrationMeasure(measure: number): this {
    const timestamp = new Date().getTime();
    const [lastVertex] = this.currentPart.slice(-1);
    if (lastVertex && lastVertex.timestamp && lastVertex.timestamp === timestamp) {
      return this.addCalibrationMeasureToLastPosition(measure);
    }

    const partIndex = this.currentPartIndex;
    const key = UNPLACED_CALIBRATION_MEASURE_KEY;
    this.calibratedMeasures.set(key, {
      measure,
      partIndex,
      vertexIndex: null,
      timestamp
    });

    return this;
  }

  addCalibrationMeasureToLastPosition(measure: number): this {
    const partIndex = this.currentPartIndex;
    const lastVertexIndex = this.currentPart.length - 1;
    return this.addCalibrationMeasureAtIndex(measure, lastVertexIndex, partIndex);
  }

  addCalibrationMeasureAtIndex(measure: number, vertexIndex: number, partIndex = 0): this {
    const key = this.getCalibrationMeasureLookupKey(partIndex, vertexIndex);
    this.calibratedMeasures.set(key, {
      measure,
      partIndex,
      vertexIndex,
      timestamp: new Date().getTime()
    });

    return this;
  }

  forceAddPosition(position: GpsPosition): this {
    this.tryStoreUnfilteredPath(position);
    this.addPositionToPath(position);
    return this;
  }

  toPointFeature(properties = {}): Feature<Point | MultiPoint> {
    return {
      type: 'Feature',
      geometry: this.toPointGeometry(),
      properties
    };
  }

  toLineFeature(properties = {}): Feature<LineString | MultiLineString> {
    return {
      type: 'Feature',
      geometry: this.toLineGeometry(),
      properties
    };
  }

  toSingleLineFeature(properties = {}): Feature<LineString> {
    return {
      type: 'Feature',
      geometry: this.toSingleLineGeometry(),
      properties
    };
  }

  toMultiLineFeature(properties = {}): Feature<MultiLineString> {
    return {
      type: 'Feature',
      geometry: this.toMultiLineGeometry(),
      properties
    };
  }

  toPolygonFeature(properties = {}): Feature<Polygon | MultiPolygon> {
    return {
      type: 'Feature',
      geometry: this.toPolygonGeometry(),
      properties
    };
  }

  toMultiPolygonFeature(properties = {}): Feature<MultiPolygon> {
    return {
      type: 'Feature',
      geometry: this.toMultiPolygonGeometry(),
      properties
    };
  }

  toSinglePolygonFeature(properties = {}): Feature<Polygon> {
    return {
      type: 'Feature',
      geometry: this.toSinglePolygonGeometry(),
      properties
    };
  }

  toFeature(): Feature<LineString | MultiLineString | Point | MultiPoint> {
    return this.isSinglePoint
      ? this.toPointFeature() as Feature<Point>
      : this.toLineFeature();
  }

  toGeometry(): LineString | MultiLineString | Point {
    return this.isSinglePoint
      ? this.toPointGeometry() as Point
      : this.toLineGeometry();
  }

  toSingleLineGeometry(calibrateMeasures = true): LineString {
    const line = this.toLineGeometry(calibrateMeasures);
    if (isMultiPartLine(line.coordinates)) {
      throw new Error('Cannot convert MultiLineString to LineString. Data would be lost');
    }

    return line as LineString;
  }

  toMultiLineGeometry(calibrateMeasures = true): MultiLineString {
    let line = this.toLineGeometry(calibrateMeasures);
    const isSinglePartLine = !isMultiPartLine(line.coordinates);
    if (isSinglePartLine) {
      line = {
        type: 'MultiLineString',
        bbox: line.bbox,
        coordinates: [line.coordinates as number[][]]
      };
    }

    return line as MultiLineString;
  }

  toLineGeometry(calibrateMeasures = true): LineString | MultiLineString {
    const parts: number[][][] = this.toMultiPartArray();
    const line: LineString | MultiLineString = parts.length > 1
      ? {
        type: 'MultiLineString',
        coordinates: parts
      }
      : {
        type: 'LineString',
        coordinates: parts[0]
      };

    if (this.options.returnMValues) {
      const calibrationPositions: CalibrationPosition[] = calibrateMeasures
        ? Array.from(
          this.calibratedMeasures.values()
        )
        : [];

      setMeasuresByGeometricLength(line, {
        units: this.options.mValueUnit,
        hasZValues: this.options.returnZValues,
        calibrationPositions
      });
    }

    return line;
  }

  toMultiPolygonGeometry(): MultiPolygon {
    let poly = this.toPolygonGeometry();
    const isSinglePolygon = poly.type === 'Polygon';

    if (isSinglePolygon) {
      poly = {
        type: 'MultiPolygon',
        bbox: poly.bbox,
        coordinates: [poly.coordinates as number[][][]]
      };
    }

    return poly as MultiPolygon;
  }

  toSinglePolygonGeometry(): Polygon {
    const poly = this.toPolygonGeometry();
    const isMultiPart = poly.type === 'MultiPolygon';

    if (isMultiPart) {
      throw new Error('Cannot convert MultiPolygon to Polygon. Data would be lost');
    }

    return poly as Polygon;
  }

  toPolygonGeometry(): Polygon | MultiPolygon {
    const parts: number[][][] = this.toMultiPartArray();
    parts.forEach(part => this.ensurePolygonIsClosed(part));

    return parts.length > 1
      ? {
        type: 'MultiPolygon',
        coordinates: [parts]
      }
      : {
        type: 'Polygon',
        coordinates: parts
      };
  }

  toPointGeometry(): Point | MultiPoint {
    const parts: number[][][] = this.toMultiPartArray();

    return parts.length > 1
      ? { type: 'MultiPoint', coordinates: parts.map(part => part[0]) }
      : { type: 'Point', coordinates: parts[0][0] };
  }

  replaceLastPositionIfBetter(position: GpsPosition): boolean {
    let wasPositionAdded = true;
    if (this.isEmpty) {
      this.addPositionToPath(position);
      return wasPositionAdded;
    }

    const newPositionHasBetterAccuracy = this.lastPosition.accuracy > position.accuracy;
    if (newPositionHasBetterAccuracy) {
      this.replaceLastPosition(position);
    } else {
      wasPositionAdded = false;
    }

    return wasPositionAdded;
  }

  replaceLastPosition(position: GpsPosition): this {
    this.currentPart[this.currentPart.length - 1] = position;

    return this;
  }

  getBestPosition(): GpsPosition {
    throw new Error('Not implemented yet');
  }

  getUnfilteredPath(): PositionPathUnfilteredGpsPosition[] {
    return this.unfilteredPath.map(position => {
      const unfilteredGpsPosition: PositionPathUnfilteredGpsPosition = Object.assign({
        included: this.pathContains(position)
      }, position);

      return unfilteredGpsPosition;
    });
  }

  getGeometryPathSummary(): PositionPathBuilderSummaryPosition[][] {
    const line = this.toMultiLineGeometry(false);

    return line.coordinates.map(
      (part: number[][], partIndex: number) => this.getGeometryLinePartSummary(part, partIndex)
    );
  }

  pathContains(position: GpsPosition): boolean {
    const found = this.parts.find(part =>
      part.indexOf(position) > -1
    );

    return Boolean(found);
  }

  getPositionByIndex(partIndex: number, vertexIndex: number): GpsPosition {
    return this.parts[partIndex][vertexIndex];
  }

  dispose(): void {
    if (this.trackSubscription) {
      this.trackSubscription.unsubscribe();
    }

    this.parts.length = 0;
  }

  private getGeometryLinePartSummary(linePart: number[][], partIndex: number): PositionPathBuilderSummaryPosition[] {
    return linePart.map((vertex, vertexIndex) => {
      const position = this.getPositionByIndex(partIndex, vertexIndex);
      const calibrationKey = this.getCalibrationMeasureLookupKey(partIndex, vertexIndex);
      const calibrationPointMeasureValue = this.calibratedMeasures.has(calibrationKey)
        ? this.calibratedMeasures.get(calibrationKey).measure
        : PATH_BUILDER_EMPTY_CALIBRATION_MEASURE_VALUE;

      return Object.assign({
        geometricMeasure: getMeasureFromPoint(vertex),
        partIndex,
        vertexIndex,
        calibrationPointMeasureValue
      }, position);
    });
  }

  private getCalibrationMeasureLookupKey(partIndex: number, vertexIndex: number): string {
    return `${partIndex}_${vertexIndex}`;
  }

  private toMultiPartArray(): number[][][] {
    const parts: number[][][] = [];
    this.parts.forEach(part => {
      const coordinates: number[][] = part.map(
        position => this.options.returnZValues
          ? [position.longitude, position.latitude, position.altitude || 0]
          : [position.longitude, position.latitude]
      );

      parts.push(coordinates);
    });

    return parts;
  }

  private ensurePolygonIsClosed(polygonPoints: number[][]): void {
    if (polygonPoints.length > 1) {
      const [first] = polygonPoints;
      const last: number[] = getLastItem(polygonPoints);
      const [firstLng, firstLat, firstZ] = first;
      const [lastLng, lastLat] = last;
      const doesPolygonNotClose = !this.arePointsEquivalent(firstLng, firstLat, lastLng, lastLat);

      if (doesPolygonNotClose) {
        const closingPoint = [firstLng, firstLat, firstZ || 0];
        polygonPoints.push(closingPoint);
      }
    }
  }

  private addPositionToPath(position: GpsPosition): void {
    this.currentPart.push(position);

    if (this.calibratedMeasures.has(UNPLACED_CALIBRATION_MEASURE_KEY)) {
      this.addPendingCalibrationPoint();
    }
  }

  private addPendingCalibrationPoint(): void {
    const calibratonPosition = this.calibratedMeasures.get(UNPLACED_CALIBRATION_MEASURE_KEY);
    const calibrationTimestamp = calibratonPosition.timestamp;
    const [secondToLastPosition, lastPosition] = this.currentPart.slice(-2);
    const isTimestampBetweenVertices = calibrationTimestamp > secondToLastPosition.timestamp
      && calibrationTimestamp < lastPosition.timestamp;

    if (isTimestampBetweenVertices) {
      const totalTimeDelta = Math.abs(lastPosition.timestamp - secondToLastPosition.timestamp);
      const offsetTimeDelta = Math.abs(calibrationTimestamp - secondToLastPosition.timestamp);
      const percentOfDistance = offsetTimeDelta / totalTimeDelta;
      const distanceBetweenVerticesKilometers = getDistanceBetweenLatLng(
        secondToLastPosition.latitude,
        secondToLastPosition.longitude,
        lastPosition.latitude,
        lastPosition.longitude,
        LengthUnit.Mile
      );

      const distanceToPreviousVertexKilometers = percentOfDistance * distanceBetweenVerticesKilometers;
      const didSnapCalibrationMeasureToVertes = this.trySnapCalibrationMeasureToVertex(
        calibratonPosition.measure,
        distanceBetweenVerticesKilometers,
        distanceToPreviousVertexKilometers);

      if (didSnapCalibrationMeasureToVertes) {
        return;
      }

      const heading = bearing(
        [secondToLastPosition.longitude, secondToLastPosition.latitude],
        [lastPosition.longitude, lastPosition.latitude]
      );

      const newPoint: Feature<Point> = destination(
        [secondToLastPosition.longitude, secondToLastPosition.latitude],
        distanceToPreviousVertexKilometers,
        heading, { units: 'kilometers' }
      );

      const spliceIndexForSecondToLastPosition = this.currentPart.length - 1;
      const newPosition: GpsPosition = Object.assign({}, lastPosition, {
        longitude: newPoint.geometry.coordinates[0],
        latitude: newPoint.geometry.coordinates[1],
        deviceSource: PATH_BUILDER_CALCULATED_M_DEVICE_SOURCE
      });

      this.calibratedMeasures.delete(UNPLACED_CALIBRATION_MEASURE_KEY);
      this.addCalibrationMeasureAtIndex(calibratonPosition.measure, spliceIndexForSecondToLastPosition, this.currentPartIndex);
      this.currentPart.splice(spliceIndexForSecondToLastPosition, 0, newPosition);
    } else if (calibrationTimestamp >= lastPosition.timestamp) {
      this.addCalibrationMeasureToLastPosition(calibratonPosition.measure);
    } else {
      throw new Error(`Calibration measure timestamp '${calibratonPosition.timestamp}' can not be located between`
        + ` '${secondToLastPosition.timestamp}' and '${lastPosition.timestamp}'`);
    }
  }

  private trySnapCalibrationMeasureToVertex(
    calibrationMeasure: number,
    totalVerticeDistanceKilometers: number,
    distanceToPreviousVertexKilometers: number
  ): boolean {
    const distanceToNextVertexKilometers = totalVerticeDistanceKilometers - distanceToPreviousVertexKilometers;
    const distanceThresholdKilometers = convertLengthUnits(
      this.options.calibrationMeasureSnappingDistanceDelta,
      this.options.calibrationMeasureSnappingDistanceDeltaUnit,
      LengthUnit.Kilometer
    );

    const isPointCloseEnoughToPrevious = distanceToPreviousVertexKilometers < distanceThresholdKilometers;
    const isPointCloseEnoughToNext = distanceToNextVertexKilometers < distanceThresholdKilometers;

    if (isPointCloseEnoughToPrevious || isPointCloseEnoughToNext) {
      const previousVertexIndex = this.currentPart.length - 2;
      const nextVertexIndex = this.currentPart.length - 1;
      const vertexIndex = distanceToPreviousVertexKilometers < distanceToNextVertexKilometers
        ? previousVertexIndex
        : nextVertexIndex;

      this.addCalibrationMeasureAtIndex(calibrationMeasure, vertexIndex, this.currentPartIndex);
      this.calibratedMeasures.delete(UNPLACED_CALIBRATION_MEASURE_KEY);
      return true;
    }

    return false;
  }

  private arePositionsEquivalent(position1: GpsPosition, position2: GpsPosition): boolean {
    return this.arePointsEquivalent(
      position1.longitude,
      position1.latitude,
      position2.longitude,
      position2.latitude
    );
  }

  private arePointsEquivalent(longitude1: number, latitude1: number, longitude2: number, latitude2: number): boolean {
    return pointsAlmostEqual(
      longitude1,
      latitude1,
      longitude2,
      latitude2,
      this.options.pointEquivalencyPrecision
    );
  }

  private tryStoreUnfilteredPath(position: GpsPosition): this {
    if (this.options.storeUnfilteredPath) {
      this.unfilteredPath.push(position);
    }

    return this;
  }
}
