import { LengthUnit } from './length-unit';
import { LENGTH_UNIT_METER_CONVERSION_FACTOR } from './unit-conversion.constants';
import { isNumber } from '../type-check/is-number.function';


export function fromMeters(value: number, toUnit: LengthUnit): number {
  const conversionFactor = LENGTH_UNIT_METER_CONVERSION_FACTOR[toUnit];
  if (!isNumber(conversionFactor)) {
    throw new Error(`Can not convert unsupported unit: '${toUnit}'`);
  }

  return value / conversionFactor;
}
