// Angular.
import { HttpParams } from '@angular/common/http';

// TSS.
import { EsriMapServiceQueryOptions } from './esi-map-service-query-options';
import { isValue } from '../../core/type-check/is-value.function';


export function toHttpQueryParams(options: EsriMapServiceQueryOptions, httpParams?: HttpParams) {
  httpParams = httpParams || new HttpParams();
  Object.keys(options)
    .forEach(k => {
      const value = options[k];
      if (isValue(value)) {
        httpParams = httpParams.set(k, value);
      }
    });

  return httpParams;
}
