import { MapLayerType, QueryType, MapLayer } from './map-layer';
import { LayerRelationship } from './layer-relationship';
import { LayerField, getFieldAlias, getField, LayerFieldDomain } from './fields/layer-field';
import { LayerIndex } from './layer-index';
import { GeometryExtent } from './geometry-extent';
import { LayerDrawingInfo } from './drawing/layer-drawing-info';
import { StyleableLayer } from './drawing/styleable-layer';
import { Cloneable } from '../core/clone/cloneable';
import { clone } from '../core/clone/clone.function';


export type GeometryType = 'point' | 'esriGeometryPoint'
  | 'line' | 'esriGeometryPolyline'
  | 'polygon' | 'esriGeometryPolygon';

export const POINT_GEOMETRY_TYPES: GeometryType[] = ['point', 'esriGeometryPoint'];
export const LINEAR_GEOMETRY_TYPES: GeometryType[] = ['line', 'esriGeometryPolyline'];
export const POLYGON_GEOMETRY_TYPES: GeometryType[] = ['polygon', 'esriGeometryPolygon'];

export function isPointGeometryType(geometryType: GeometryType) {
  return POINT_GEOMETRY_TYPES.indexOf(geometryType) > -1;
}

export function isLinearGeometryType(geometryType: GeometryType) {
  return LINEAR_GEOMETRY_TYPES.indexOf(geometryType) > -1;
}

export function isPolygonGeometryType(geometryType: GeometryType) {
  return POLYGON_GEOMETRY_TYPES.indexOf(geometryType) > -1;
}

export class FeatureClassLayer implements MapLayer, StyleableLayer, Cloneable<FeatureClassLayer> {
  id: number;
  name?: string;
  type?: MapLayerType;
  parentLayer?: number;
  subLayers?: { id: string, name: string }[];
  description?: string;
  hasAttachments?: boolean;
  currentVersion?: number;
  geometryType?: GeometryType;
  sourceSpatialReference?: { wkid: number, latestWkid?: number };
  minScale?: number;
  maxScale?: number;
  extent?: GeometryExtent;
  displayField?: string;
  typeIdField?: string;
  fields?: LayerField[];
  indexes?: LayerIndex[];
  relationships?: LayerRelationship[];
  maxRecordCount?: number;
  supportsStatistics?: boolean;
  supportsAdvancedQueries?: boolean;
  hasZ?: boolean;
  hasM?: boolean;
  supportedQueryFormats?: QueryType[];
  supportsDatumTransformation?: boolean;
  isDataVersioned?: boolean;
  drawingInfo?: LayerDrawingInfo;
  order?: number;

  get isPoint(): boolean {
    return isPointGeometryType(this.geometryType);
  }

  get isLinear(): boolean {
    return isLinearGeometryType(this.geometryType);
  }

  get isPolygon(): boolean {
    return isPolygonGeometryType(this.geometryType);
  }

  get displayFieldAlias(): string {
    return this.getFieldAlias(this.displayField);
  }

  constructor(...sources: MapLayer[]) {
    Object.assign(this, ...sources);

    this.subLayers = this.subLayers || [];
    this.fields = this.fields || [];
    this.indexes = this.indexes || [];
    this.relationships = this.relationships || [];
    this.supportedQueryFormats = this.supportedQueryFormats || [];
    this.drawingInfo = new LayerDrawingInfo(this.drawingInfo);
  }

  getField(fieldName: string): LayerField | null {
    return getField(fieldName, this.fields);
  }

  getFieldAlias(fieldName: string): string | null {
    return getFieldAlias(fieldName, this.fields);
  }

  applyFieldDomains(domains: LayerFieldDomain[]): this {
    if (domains.length) {
      this.fields
        .filter(f => f.domainName)
        .forEach(field =>
          field.domain = domains.find(domain => domain.name === field.domainName) || field.domain
        );
    }

    return this;
  }

  clone(): FeatureClassLayer {
    const copy = clone(this);
    return new FeatureClassLayer(copy);
  }
}
