import { LrsReferenceLayer, LrsLayer } from '../lrs-layer';


export class RedlineLayer extends LrsLayer implements LrsReferenceLayer {
  routeIdFieldName: string;
  routeNameFieldName: string;
  fromMeasureFieldName: string;
  toMeasureFieldName: string;
  activityTypeFieldName: string;
  networkFieldName: string;

  get effectiveDateFieldName(): string {
    return this.fromDateFieldName;
  }

  set effectiveDateFieldName(effectiveDateFieldName: string) {
    this.fromDateFieldName = effectiveDateFieldName;
  }
}
