import { base64MimeType } from './base64-mime-type.function';
import { base64toByteArray } from './base64-to-byte-array.function';


export function base64toBlob(base64Data: string, contentType: string = null) {
  contentType = contentType || base64MimeType(base64Data);
  return new Blob(base64toByteArray(base64Data), { type: contentType });
}
