// 3rd Party.
import { LineString } from 'geojson';

// TSS.
import { SingleLine } from './line-geometry';
import { isArray } from '../../core/type-check/is-array.function';


export function toLineString(line: SingleLine): LineString {
  return isArray(line)
    ? {
      bbox: null,
      type: 'LineString',
      coordinates: line as number[][]
    }
    : line as LineString;
}
