import { PipeTransform, Pipe } from '@angular/core';
import { TimeUnit } from '../time-unit';
import { toMilliseconds } from '../conversion/to-milliseconds.function';
import { toReadableTime } from './to-readable-time.function';


@Pipe({ name: 'readableTime' })
export class ReadableTimePipe implements PipeTransform {
  transform(time: number, unit: TimeUnit = 'milliseconds'): any {
    const millis = toMilliseconds(time, unit);
    return toReadableTime(millis);
  }
}
