import { FeatureCollection } from 'geojson';
import { NetworkFeature } from './network-feature';
import { LrsFeatureCollection } from '../lrs-feature-collection';
import { NetworkLayer } from './network-layer';
import { WGS_84_SRID } from '../../geometry/geometry.constants';


export class NetworkFeatureCollection extends LrsFeatureCollection {
  type: 'FeatureCollection';
  features: Array<NetworkFeature>;

  constructor(collection: FeatureCollection, layer: NetworkLayer, spatialReferenceId = WGS_84_SRID) {
    super(collection);
    this.features = collection.features
      .map(f => new NetworkFeature(f, layer, spatialReferenceId));
  }
}
