/**
 * Represents all potential states of a queued item.
 * * Queued: The item is in the queue, waiting for the next available worker to retrieve it.
 * * InProgress: The item has been picked up by a worker and currently being processed.
 * * Complete: The item has been sucesfully completed.
 * * Canceled: The item was canceled before completion.
 * * Failed: The item failed to process.
 */
export enum QueueItemStatus {
  Queued,
  InProgress,
  Complete,
  Canceled,
  Failed,
}
