// Angular.
import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';

// 3rd Party.
import { PersistentCacheStorage } from './cache.storage';
import { CacheContent } from './cache-content';


/**
 * Cache Service is an observables based in-memory cache implementation
 * Keeps track of in-flight observables and sets a default expiry for cached values
 */
@Injectable({
  providedIn: 'root'
})
export class CacheService {
  constructor(private cache: PersistentCacheStorage) {
  }

  get(key: string): HttpResponse<any> {
    const cacheItem: CacheContent = this.cache.getObject(key);
    const now = new Date();
    let cacheData: HttpResponse<any>;

    if (cacheItem && cacheItem.expires && cacheItem.expires.getTime() < now.getTime()) {
      this.cache.remove(key);
    } else if (cacheItem) {
      // NOTE: We can't just pull the serialized data, because
      // we need an actual instance of the HttpResponse class.
      cacheData = Object.assign(new HttpResponse<any>(), cacheItem.value);
    } else {
      cacheData = null;
    }

    return cacheData;
  }

  set(key: string, value: HttpResponse<any>, timeToLive: number = null) {
    const expires = timeToLive ? this.getExpireDate(timeToLive) : null;
    const cacheData: CacheContent = { expires, value };

    this.cache.setObject(key, cacheData);
  }

  has(key: string): boolean {
    return this.cache.has(key);
  }

  private getExpireDate(timeToLive: number = null): Date {
    const expires = new Date();
    expires.setSeconds(expires.getSeconds() + timeToLive);

    return expires;
  }
}
