// Angular.
import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

// 3rd Party.
import { Feature } from 'geojson';
import { Observable } from 'rxjs';

// TSS.
import { ApplyEditsSeed } from './apply-edits-seed';
import { DataSourceConfig } from '../data-source-config';
import { ApplyEditsResult } from './apply-edits-result';
import { ApplyEditsPod } from './apply-edits-pod';
import { BaseEmptyResourceService } from '../../core/resource/base-empty-resource.service';
import { EMPTY_PARAM_VALUE } from '../../core/resource/resource.constants';


@Injectable({
  providedIn: 'root'
})
export class DataSourceEditService extends BaseEmptyResourceService<ApplyEditsSeed> {
  constructor(protected http: HttpClient, private dsConfig: DataSourceConfig) {
    super(http, {
      url: dsConfig.serviceUrl
    });
  }

  applyEdits(data: ApplyEditsPod, params?: HttpParams): Observable<ApplyEditsResult> {
    if (!data) {
      throw new Error('Missing or invalid apply edits seed data provided');
    } else if (!data.dataSourceId) {
      throw new Error(`Missing 'dataSourceId' from the apply edits seed data`);
    }

    const url = this.getUrlWithId(data.dataSourceId, this.dsConfig.applyEditsEndpoint, {});
    return this.http.post<ApplyEditsResult>(url, data, { params });
  }

  createRecord(
    dataSourceId: string,
    table: string,
    feature: Feature,
    version?: string,
    params?: HttpParams
  ): Observable<ApplyEditsResult> {
    const url = this.getUrlWithId(dataSourceId, this.dsConfig.editDataEndpoint, { table });
    params = (params || new HttpParams())
      .set('version', version || EMPTY_PARAM_VALUE);

    return this.http.post<ApplyEditsResult>(url, feature, { params });
  }
}
