// Angular.
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

// 3rd Party.
import { Observable } from 'rxjs';
import { map, tap } from 'rxjs/operators';

// TSS.
import { Logger } from '../core/logging/logger.service';

import { NetworkFeature } from '../lrs/networks/network-feature';
import { NetworkLayer } from '../lrs/networks/network-layer';
import { EventLayer } from '../lrs/events/event-layer';
import { LrsMetadata } from '../lrs/lrs-metadata';
import { EventFeature } from '../lrs/events/event-feature';
import { EventFeatureCollection } from '../lrs/events/event-feature-collection';
import { NetworkFeatureCollection } from '../lrs/networks/network-feature-collection';

import { MapServerMetadata } from '../maps/map-server-metadata';
import { CriteriaOperator } from '../maps/where-clause-builder';

import { EsriMapServiceQueryOptions } from './query/esi-map-service-query-options';
import { EsriConfig } from './esri-config';
import { getEsriMapServiceHttOptions, AngularHttpOption, addParamsToHttpOptions } from './esri-map-service-request';
import { EsriMapServiceErrorResponse } from './esri-map-server-error-response';
import { FeatureCollection } from 'geojson';

import { MapLayer } from '../maps/map-layer';
import { LayerField, toFieldName } from '../maps/fields/layer-field';
import { DataSourceConfig } from '../data-source/data-source-config';
import { GeodatbaseVersion } from '../maps/geodatabase-version';
import { LrsInfo } from '../lrs/lrs-info';
import { EsriApplyEditsResponse } from './lrs-server/esri-apply-edits-response';
import { EsriEventEdits } from './lrs-server/esri-event-edits';
import { toFormData } from '../http/to-form-data.function';
import {
  MAP_LAYERS_SERVICE_ENDPOINT,
  LRS_LAYERS_SERVICE_ENDPOINT,
  MAP_LAYER_QUERY_SERVICE_ENDPOINT,
  LRS_SERVER_META_ENDPOINT,
  LRS_SERVER_APPLY_EDITS_ENDPOINT,
  LRS_GEOMETRY_TO_MEASURE,
  MAP_LAYER_SERVICE_ENDPOINT
} from './esri.constants';
import { EsriMapServiceGeometryToMeasureOptions } from './geometry-to-measure/esri-geometry-to-measure-options';
import { getGeometryToMeasureOptions } from './geometry-to-measure/get-geometry-to-measure-options.function';
import { EsriGeometryToMeasureResult } from './geometry-to-measure/esri-geometry-to-measure-result';
import { getMapQueryOptions } from './query/get-map-query-options.function';
import { toHttpQueryParams } from './query/to-http-query-params.function';
import { tryStripGdbVersion } from './try-strip-gdb-version.function';
import { toLayerId } from '../maps/to-layer-id.function';
import { BaseEsriService } from './base-esri.service';
import { joinAndFomatWithLayer } from './join-and-format-with-layer.function';
import { join } from '../core/url/join.function';
import { format } from '../core/formatting/format.function';
import { FeatureClassLayer } from '../maps/feature-class-layer';


@Injectable({
  providedIn: 'root'
})
export class EsriMapServerService extends BaseEsriService {
  constructor(http: HttpClient, config: EsriConfig, dataConfig: DataSourceConfig, log: Logger) {
    super(http, config, dataConfig, log);
  }

  /**
   * Retrieves the map service metadata for all layers.
   */
  getMapLayers(serviceUrl: string): Observable<MapServerMetadata> {
    const url = join(serviceUrl, MAP_LAYERS_SERVICE_ENDPOINT);
    return this.http
      .get<MapServerMetadata>(url, getEsriMapServiceHttOptions())
      .pipe(
        tap((response: any) => this.handleEsriResponseError(response, serviceUrl))
      );
  }

  /**
   * Retrieves the map service metadata for a given layer (by it's ID).
   */
  getMapLayer(serviceUrl: string, layerId: number): Observable<FeatureClassLayer> {
    const url = joinAndFomatWithLayer(layerId, {}, serviceUrl, MAP_LAYER_SERVICE_ENDPOINT);
    return this.http
      .get<FeatureClassLayer>(url, getEsriMapServiceHttOptions())
      .pipe(
        tap((response: any) => this.handleEsriResponseError(response, serviceUrl)),
        map(mapLayerMetadata => new FeatureClassLayer(mapLayerMetadata))
      );
  }

  /**
   * Retrieves the LRS Layers Metadata information from the map service
   */
  getLrsLayers(serviceUrl: string): Observable<LrsMetadata> {
    const url = join(serviceUrl, LRS_LAYERS_SERVICE_ENDPOINT);
    return this.http
      .get<LrsMetadata>(url, getEsriMapServiceHttOptions())
      .pipe(
        tap((response: any) => this.handleEsriResponseError(response, serviceUrl))
      );
  }

  /**
   * Retrieves the LRS Server Metadata information from the map service
   */
  getLrsServer(serviceUrl: string): Observable<LrsMetadata> {
    const url = join(serviceUrl, LRS_SERVER_META_ENDPOINT);
    return this.http
      .get<LrsMetadata>(url, getEsriMapServiceHttOptions())
      .pipe(
        tap((response: any) => this.handleEsriResponseError(response, serviceUrl))
      );
  }

  getVersions(serviceUrl: string, lrsId?: string): Observable<GeodatbaseVersion[]> {
    return this.getLrsServer(serviceUrl)
      .pipe(
        map((metadata: LrsMetadata) => {
          let lrs: LrsInfo = metadata ? metadata.lrs[0] : null;
          if (!lrs) {
            throw new Error(`No LRS infos found`);
          }

          if (lrsId) {
            lrs = metadata.lrs.find(lrsInfo => lrsInfo.id === lrsId);
            if (!lrs) {
              throw new Error(`No LRS info found with ID '${lrsId}'`);
            }
          }

          return lrs.versions;
        })
      );
  }

  singleGeometryToMeasure(
    serviceUrl: string,
    networkLayerId: number,
    routeId: string,
    x: number,
    y: number,
    viewDate?: Date,
    inSpatialReferenceId = 4326,
    outputSpatialReferenceId = 4326
  ): Observable<{ m: number, x: number, y: number, z?: number }> {
    const locations = [
      { routeId, geometry: { x, y } }
    ];

    return this.geometryToMeasure(serviceUrl, networkLayerId, {
      locations,
      temporalViewDate: viewDate,
      inSR: inSpatialReferenceId,
      outSR: outputSpatialReferenceId,
    })
      .pipe(
        map(result => {
          const match = { m: null, x: null, y: null, z: null };
          if (result.locations.length && result.locations[0].results.length) {
            const [found] = result.locations[0].results;
            match.m = found.measure;
            match.x = found.geometry.x;
            match.y = found.geometry.y;
          }

          return match;
        })
      );
  }

  geometryToMeasure(
    serviceUrl: string,
    networkLayerId: number,
    options: EsriMapServiceGeometryToMeasureOptions
  ): Observable<EsriGeometryToMeasureResult> {
    const endpoint = format(LRS_GEOMETRY_TO_MEASURE, { layerId: networkLayerId });
    const url = join(serviceUrl, endpoint);
    const httpOption: AngularHttpOption = getEsriMapServiceHttOptions();
    const serviceOptions = getGeometryToMeasureOptions(options);
    const useGetMethod = !this.shouldUsePostMethod(serviceOptions);

    if (useGetMethod) {
      addParamsToHttpOptions(httpOption, serviceOptions);
    }

    return (
      useGetMethod
        ? this.http.get<EsriGeometryToMeasureResult>(url, httpOption)
        : this.http.post<EsriGeometryToMeasureResult>(url, toFormData(serviceOptions), httpOption)
    )
      .pipe(
        tap(response => this.handleEsriResponseError(response, serviceUrl)),
      );
  }

  applyEdits(serviceUrl: string, edits: EsriEventEdits[], gdbVersion?: string): Observable<EsriApplyEditsResponse> {
    const url = join(serviceUrl, LRS_SERVER_APPLY_EDITS_ENDPOINT);
    const options = getEsriMapServiceHttOptions();
    const useGetMethod = !this.shouldUsePostMethod(edits);

    if (useGetMethod) {
      options.params = options.params
        .set('edits', JSON.stringify(edits))
        .set('gdbVersion', gdbVersion);
    }

    return (
      useGetMethod
        ? this.http.get<EsriApplyEditsResponse>(url, options)
        : this.http.post<EsriApplyEditsResponse>(url, toFormData({ edits, gdbVersion }), options)
    )
      .pipe(
        tap(response => this.handleEsriResponseError(response, serviceUrl)),
      );
  }

  getUniqueValues<T>(
    serviceUrl: string,
    layer: MapLayer | number,
    field: string | LayerField,
    options?: EsriMapServiceQueryOptions
  ): Observable<T[]> {
    const fieldName = toFieldName(field);
    const queryOptions: EsriMapServiceQueryOptions = getMapQueryOptions({
      outFields: fieldName,
      returnDistinctValues: true,
      returnGeometry: false,
    }, options);

    return this.query(serviceUrl, toLayerId(layer), queryOptions)
      .pipe(
        map(collection => {
          return collection.features.map(f => f.properties[fieldName]);
        })
      );
  }

  getQueryCount(
    serviceUrl: string,
    layer: MapLayer | number,
    whereClause: string,
    options?: EsriMapServiceQueryOptions
  ): Observable<number> {
    const queryOptions: EsriMapServiceQueryOptions = getMapQueryOptions({
      returnCountOnly: true,
      returnGeometry: false,
      where: whereClause,
    }, options);

    return this.query(serviceUrl, toLayerId(layer), queryOptions)
      .pipe(
        map((result: any) => {
          return (result || { count: 0 }).count;
        })
      );
  }

  getEvent(
    serviceUrl: string,
    layer: EventLayer,
    eventId: string | number,
    options?: EsriMapServiceQueryOptions
  ): Observable<EventFeature> {
    tryStripGdbVersion(layer, options);
    throw new Error('Not implemented yet');
  }

  queryEvents(
    serviceUrl: string,
    layer: EventLayer,
    routeId: string | number,
    fromMeasure: number,
    toMeasure: number,
    viewDate?: Date,
    options?: EsriMapServiceQueryOptions
  ): Observable<EventFeatureCollection> {
    tryStripGdbVersion(layer, options);
    const where = this.getWhereBuilder()
      .withEventLayer(layer)
      .withRouteId(routeId)
      .withViewDate(viewDate)
      .betweenMeasures(fromMeasure, toMeasure)
      .toWhereClause();

    const queryOptions: EsriMapServiceQueryOptions = getMapQueryOptions({ where, returnGeometry: false }, options);

    return this
      .query(serviceUrl, layer.id, queryOptions)
      .pipe(
        map(featureCollection => new EventFeatureCollection(featureCollection, layer))
      );
  }

  searchEventsByText(
    serviceUrl: string,
    layer: EventLayer,
    searchText: string,
    options?: EsriMapServiceQueryOptions
  ): Observable<EventFeatureCollection> {
    return this.searchByText(serviceUrl, layer.id, searchText, options)
      .pipe(
        tap(response => this.handleEsriResponseError(response, serviceUrl)),
        map(featureCollection => new EventFeatureCollection(featureCollection, layer))
      );
  }

  getRoute(
    serviceUrl: string,
    routeId: string | number,
    layer: NetworkLayer,
    viewDate?: Date,
    options?: EsriMapServiceQueryOptions
  ): Observable<NetworkFeature> {
    tryStripGdbVersion(layer, options);
    const where = this.getWhereBuilder()
      .withNetworkLayer(layer)
      .where(layer.compositeRouteIdFieldName, CriteriaOperator.Equals, routeId)
      .withViewDate(viewDate)
      .toWhereClause();

    const queryOptions: EsriMapServiceQueryOptions = getMapQueryOptions({
      where
    }, options);

    return this
      .query(serviceUrl, layer.id, queryOptions)
      .pipe(
        map(x => {
          return x && x.features && x.features.length
            ? new NetworkFeature(x.features[0], layer)
            : null;
        })
      );
  }

  queryRoutes(
    serviceUrl: string,
    layer: NetworkLayer,
    whereClause: string,
    options?: EsriMapServiceQueryOptions
  ): Observable<NetworkFeatureCollection> {
    const queryOptions: EsriMapServiceQueryOptions = getMapQueryOptions({
      where: whereClause
    }, options);

    return this.query(serviceUrl, layer.id, queryOptions)
      .pipe(
        map(featureCollection => new NetworkFeatureCollection(featureCollection, layer, options.outSR))
      );
  }

  searchRoutesByText(
    serviceUrl: string,
    layer: NetworkLayer,
    searchText: string,
    options?: EsriMapServiceQueryOptions
  ): Observable<NetworkFeatureCollection> {
    return this.searchByText(serviceUrl, layer.id, searchText, options)
      .pipe(
        map(featureCollection => new NetworkFeatureCollection(featureCollection, layer))
      );
  }

  searchByText(
    serviceUrl: string,
    layerId: number | MapLayer,
    searchText: string,
    options?: EsriMapServiceQueryOptions
  ): Observable<FeatureCollection> {
    const queryOptions: EsriMapServiceQueryOptions = getMapQueryOptions({
      text: searchText
    }, options);

    // The where clause must be deleted because if a where clause is specified it will override the search text.
    delete queryOptions.where;

    return this.query(serviceUrl, layerId, queryOptions);
  }

  query(serviceUrl: string, layerId: number | MapLayer, options: EsriMapServiceQueryOptions): Observable<FeatureCollection> {
    const url = joinAndFomatWithLayer(layerId, options, serviceUrl, MAP_LAYER_QUERY_SERVICE_ENDPOINT);
    const httpOption = getEsriMapServiceHttOptions();
    const usePostMethod = this.shouldUsePostMethod(options.where);

    if (!usePostMethod) {
      httpOption.params = toHttpQueryParams(options, httpOption.params);
    }

    const source = usePostMethod
      ? this.http.post<FeatureCollection>(url, options, httpOption)
      : this.http.get<FeatureCollection>(url, httpOption);

    return source.pipe(
      map((response: FeatureCollection | EsriMapServiceErrorResponse) => {
        this.handleEsriResponseError(response, url);
        return response as FeatureCollection;
      })
    );
  }
}
