// 3rd Party.
import { LineString, MultiLineString } from 'geojson';

// TSS.
import { LineGeometry } from './line-geometry';
import { isArray } from '../../core/type-check/is-array.function';
import { isMultiPartLine } from './is-multi-part.function';
import { toMultiLineString } from './to-multi-line-string.function';
import { toLineString } from './to-line-string.function';


export function toLineOrMultiLineString(line: LineGeometry): LineString | MultiLineString {
  const isCoordinatesArray = isArray(line);
  if (!isCoordinatesArray) {
    return line as (LineString | MultiLineString);
  }

  const coordinates = line as (number[][] | number[][][]);
  return isMultiPartLine(coordinates)
    ? toMultiLineString(coordinates)
    : toLineString(coordinates as number[][]);
}
