import { LengthUnit } from './length-unit';
import { toMeters } from './to-meters.function';
import { fromMeters } from './from-meters.function';


export function convertLengthUnits(fromValue: number, fromUnit: LengthUnit, toUnit: LengthUnit): number {
  const fromValueInMeters = toMeters(fromValue, fromUnit);

  return fromMeters(fromValueInMeters, toUnit);
}
