import { hasTokens } from '../../core/tokens/has-tokens.function';
import { isString } from '../../core/type-check/is-string.function';
import { canNotBeNumber } from '../../core/type-check/can-not-be-number.function';
import { Orderable } from '../../core/sort/orderable';


export type DomainType = 'codedValue';
export type MergePolicyType = 'esriMPTDefaultValue' | 'esriMPTAreaWeighted' | 'esriMPTSumValues';
export type SplitPolicyType = 'esriSPTDefaultValue' | 'esriSPTDuplicate' | 'esriSPTGeometryRatio';

export type FieldType = 'id' | 'esriFieldTypeOID' | 'id-string'
  | 'string' | 'esriFieldTypeString'
  | 'number' | 'esriFieldTypeNumber'
  | 'double' | 'esriFieldTypeDouble'
  | 'int' | 'esriFieldTypeSmallInteger'
  | 'date' | 'esriFieldTypeDate'
  | 'geometry' | 'esriFieldTypeGeometry'
  | 'xml' | 'esriFieldTypeXML'
  | 'blob' | 'esriFieldTypeBlob'
  | 'guid' | 'esriFieldTypeGUID'
  | 'globalId' | 'esriFieldTypeGlobalID'
  | 'integer' | 'esriFieldTypeInteger'
  | 'raster' | 'esriFieldTypeRaster'
  | 'single' | 'esriFieldTypeSingle';

export type FieldRole = 'identity'
  | 'latitude'
  | 'longitude'
  | 'x'
  | 'y'
  | 'accuracy'
  | 'altitude'
  | 'altitude-accuracy'
  | 'heading'
  | 'speed'
  | 'username'
  | 'user-email'
  | 'user-firstname'
  | 'user-lastname'
  | 'date-now'
  | 'measure'
  | 'from-measure'
  | 'to-measure';

export type LayerFieldCodedValueCode = string | number;

export const FIELD_TYPE_OID: FieldType = 'id';
export const FIELD_TYPE_OID_STRING: FieldType = 'id-string';
export const FIELD_TYPE_STRING: FieldType = 'string';
export const FIELD_TYPE_NUMBER: FieldType = 'number';
export const FIELD_TYPE_XML: FieldType = 'xml';
export const FIELD_TYPE_BLOB: FieldType = 'blob';
export const FIELD_TYPE_DATE: FieldType = 'date';
export const FIELD_TYPE_DOUBLE: FieldType = 'double';
export const FIELD_TYPE_GUID: FieldType = 'guid';
export const FIELD_TYPE_GEOMETRY: FieldType = 'geometry';
export const FIELD_TYPE_GLOBALID: FieldType = 'globalId';
export const FIELD_TYPE_INTEGER: FieldType = 'integer';
export const FIELD_TYPE_RASTER: FieldType = 'raster';
export const FIELD_TYPE_SINGLE: FieldType = 'single';
export const FIELD_TYPE_SMALL_INTEGER: FieldType = 'int';

export const ESRI_FIELD_TYPE_OID: FieldType = 'esriFieldTypeOID';
export const ESRI_FIELD_TYPE_STRING: FieldType = 'esriFieldTypeString';
export const ESRI_FIELD_TYPE_NUMBER: FieldType = 'esriFieldTypeNumber';
export const ESRI_FIELD_TYPE_XML: FieldType = 'esriFieldTypeXML';
export const ESRI_FIELD_TYPE_BLOB: FieldType = 'esriFieldTypeBlob';
export const ESRI_FIELD_TYPE_DATE: FieldType = 'esriFieldTypeDate';
export const ESRI_FIELD_TYPE_DOUBLE: FieldType = 'esriFieldTypeDouble';
export const ESRI_FIELD_TYPE_GUID: FieldType = 'esriFieldTypeGUID';
export const ESRI_FIELD_TYPE_GEOMETRY: FieldType = 'esriFieldTypeGeometry';
export const ESRI_FIELD_TYPE_GLOBALID: FieldType = 'esriFieldTypeGlobalID';
export const ESRI_FIELD_TYPE_INTEGER: FieldType = 'esriFieldTypeInteger';
export const ESRI_FIELD_TYPE_RASTER: FieldType = 'esriFieldTypeRaster';
export const ESRI_FIELD_TYPE_SINGLE: FieldType = 'esriFieldTypeSingle';
export const ESRI_FIELD_TYPE_SMALL_INTEGER: FieldType = 'esriFieldTypeSmallInteger';

export const FIELD_METADATA: FieldMetadata[] = [
  { alias: 'ID', isNumeric: true, types: [FIELD_TYPE_OID, ESRI_FIELD_TYPE_OID] },
  { alias: 'ID', isNumeric: false, types: [FIELD_TYPE_OID_STRING] },
  { alias: 'string', isNumeric: false, types: [FIELD_TYPE_STRING, ESRI_FIELD_TYPE_STRING] },
  { alias: 'number', isNumeric: true, types: [FIELD_TYPE_NUMBER, ESRI_FIELD_TYPE_NUMBER] },
  { alias: 'double', isNumeric: true, types: [FIELD_TYPE_DOUBLE, ESRI_FIELD_TYPE_DOUBLE] },
  { alias: 'int', isNumeric: true, types: [FIELD_TYPE_INTEGER, ESRI_FIELD_TYPE_INTEGER, ESRI_FIELD_TYPE_SMALL_INTEGER] },
  { alias: 'date', isNumeric: false, types: [FIELD_TYPE_DATE, ESRI_FIELD_TYPE_DATE] },
  { alias: 'geometry', isNumeric: false, types: [FIELD_TYPE_GEOMETRY, ESRI_FIELD_TYPE_GEOMETRY] },
  { alias: 'xml', isNumeric: false, types: [FIELD_TYPE_XML, ESRI_FIELD_TYPE_XML] },
  { alias: 'blob', isNumeric: false, types: [FIELD_TYPE_BLOB, ESRI_FIELD_TYPE_BLOB] },
  { alias: 'guid', isNumeric: false, types: [FIELD_TYPE_GUID, ESRI_FIELD_TYPE_GUID] },
  { alias: 'globalId', isNumeric: false, types: [FIELD_TYPE_GLOBALID, ESRI_FIELD_TYPE_GLOBALID] },
  { alias: 'integer', isNumeric: true, types: [FIELD_TYPE_INTEGER, ESRI_FIELD_TYPE_INTEGER] },
  { alias: 'raster', isNumeric: false, types: [FIELD_TYPE_RASTER, ESRI_FIELD_TYPE_RASTER] },
  { alias: 'single', isNumeric: false, types: [FIELD_TYPE_SINGLE, ESRI_FIELD_TYPE_SINGLE] },
];

export interface FieldMetadata {
  alias: string;
  isNumeric: boolean;
  types: FieldType[];
}

export function isFieldGeometryType(fieldType: FieldType): boolean {
  return fieldType === 'esriFieldTypeGeometry' || fieldType === 'geometry';
}

export function isObjectIdType(fieldType: FieldType): boolean {
  return fieldType === 'esriFieldTypeOID' || fieldType === 'id';
}

export function getGeometryField(fields: LayerField[]): LayerField {
  return (fields || []).find(f => isFieldGeometryType(f.type));
}

export function getGeometryFieldName(fields: LayerField[]): string {
  const field = getGeometryField(fields);
  return field ? field.name : null;
}

export function getIdField(fields: LayerField[]): LayerField {
  return (fields || []).find(f => isObjectIdType(f.type));
}

export function getIdFieldFieldName(fields: LayerField[]): string {
  const field = getIdField(fields);
  return field ? field.name : null;
}

export function isFieldExpression(fieldNameOrExpression: string): boolean {
  return hasTokens(fieldNameOrExpression);
}

export function toFieldType(field: FieldType | LayerField): FieldType {
  return isString(field)
    ? field as FieldType
    : (field as LayerField).type;
}

export function toFieldName(field: string | LayerField): string {
  return isString(field) ? field as string : (field as LayerField).name;
}

export function getField(fieldName: string, fields: LayerField[]): LayerField | null {
  return fields.find(f => f.name === fieldName) || null;
}

export function getFieldAlias(fieldName: string, fields: LayerField[]): string | null {
  let fieldAlias = null;
  const field = getField(fieldName, fields);
  if (field && field.alias) {
    fieldAlias = field.alias;
  }

  return fieldAlias;
}

export function isFieldNumeric(field: FieldType | LayerField): boolean {
  const fieldType = toFieldType(field);
  const match = FIELD_METADATA
    .filter(f => f.isNumeric)
    .find(f => f.types.indexOf(fieldType) > -1);

  return Boolean(match);
}

export function isFieldValueNumeric(field: FieldType | LayerField, value: any): boolean {
  const fieldType = toFieldType(field);
  if (isObjectIdType(fieldType) && canNotBeNumber(value)) {
    return false;
  }

  return isFieldNumeric(field);
}

export interface LayerFieldCodedValue {
  name: string;
  code: LayerFieldCodedValueCode;
  order?: number;
}

export interface LayerFieldDomain {
  type: DomainType;
  name: string;
  codedValues: LayerFieldCodedValue[];
  mergePolicy: MergePolicyType;
  splitPolicy: SplitPolicyType;
}

export interface LayerField extends Orderable {
  name: string;
  type: FieldType;
  alias?: string;
  length?: number;
  editable?: boolean;
  nullable?: boolean;
  defaultValue?: any;
  domain?: LayerFieldDomain;
  domainName?: string;
  hidden?: boolean;
  order?: number;
  role?: FieldRole;
  placeholder?: string;
  min?: number;
  max?: number;
}
