import { toMultiPartPaths } from './to-multi-part-paths.function';
import { LineGeometry } from './line-geometry';
import { addRange } from '../../core/array/add-range.function';
import { PointComparisonDetails } from '../point/point-comparison-details';
import { getMeasureFromPoint } from '../point/get-measure-from-point.function';


export function getPoint(line: LineGeometry, measure: number): number[] {
  const pointComparisionDetails: PointComparisonDetails[] = [];
  const paths: number[][][] = toMultiPartPaths(line);

  paths.forEach((coordinates: number[][]) => {
    const details = comparePathWithMeasure(coordinates, measure);
    addRange(pointComparisionDetails, details);
  });

  const minPoint = pointComparisionDetails.reduce((accumulator, value) => minPointCloserToMeasure(accumulator, value, measure));
  const maxPoint = pointComparisionDetails.reduce((accumulator, value) => maxPointCloserToMeasure(accumulator, value, measure));
  const measureDeltaPercent = calculateMeasureDelta(minPoint.pointMeasure, maxPoint.pointMeasure, measure);
  const x = interpolateX(minPoint.pointCoordinate, maxPoint.pointCoordinate, measureDeltaPercent);
  const y = interpolateY(minPoint.pointCoordinate, maxPoint.pointCoordinate, measureDeltaPercent);

  return [x, y, 0, measure];
}

function interpolateX([minX]: number[], [maxX]: number[], delta: number): number {
  return minX + delta * (maxX - minX);
}

function interpolateY([, minY]: number[], [, maxY]: number[], delta: number): number {
  return minY + delta * (maxY - minY);
}

function calculateMeasureDelta(minMeasure: number, maxMeasure: number, measure: number): number {
  if (maxMeasure === minMeasure) {
    return 0;
  }

  return (measure - minMeasure) / (maxMeasure - minMeasure);
}

function comparePathWithMeasure(coordinates: number[][], measureToCompare: number): PointComparisonDetails[] {
  const pointComparisionDetailsFromPath: PointComparisonDetails[] = coordinates
    .map(
      (coordinate: number[], index: number) => compareCoordinateWithMeasure(coordinate, measureToCompare, index)
    );

  return pointComparisionDetailsFromPath;
}

function compareCoordinateWithMeasure(coordinate: number[], measureToCompare: number, index: number): PointComparisonDetails {
  const measureFromCoordinate = getMeasureFromPoint(coordinate);
  const pointDetails: PointComparisonDetails = {
    pointCoordinate: coordinate,
    distance: Math.abs(measureFromCoordinate - measureToCompare),
    pointMeasure: measureFromCoordinate,
    index
  };

  return pointDetails;
}

function minPointCloserToMeasure(
  accumulator: PointComparisonDetails,
  value: PointComparisonDetails,
  measureToCompare: number
): PointComparisonDetails {
  const isPointCloserToMeasure = value.distance <= accumulator.distance;
  const isPointLessThanMeasure = value.pointMeasure <= measureToCompare;

  return isPointCloserToMeasure && isPointLessThanMeasure
    ? value
    : accumulator;
}

function maxPointCloserToMeasure(
  point1: PointComparisonDetails,
  point2: PointComparisonDetails,
  measureToCompare: number
): PointComparisonDetails {
  const isPointCloserToMeasure = point1.distance <= point2.distance;
  const isPointGreaterThanMeasure = point1.pointMeasure >= measureToCompare;

  return isPointCloserToMeasure && isPointGreaterThanMeasure
    ? point1
    : point2;
}
