import { HttpHeaders, HttpParams } from '@angular/common/http';
import { EsriQueryResponseFormat } from './esri-response-format';
import { toFormDataString } from '../http/to-form-data-string.function';
import { isValue } from '../core/type-check/is-value.function';


export interface AngularHttpOption {
  headers?: HttpHeaders;
  observe?: 'body';
  params?: HttpParams;
  reportProgress?: boolean;
  responseType?: 'json';
  withCredentials?: boolean;
}

export function getEsriMapServiceHttOptions(params?: HttpParams, format: EsriQueryResponseFormat = 'json'): AngularHttpOption {
  const headers = new HttpHeaders();
  params = params || new HttpParams();

  if (format) {
    params = params.set('f', format);
  }

  headers.append('Content-Type', 'application/json');
  headers.append('Access-Control-Allow-Origin', '*');

  const httpOptions = {
    withCredentials: true,
    headers,
    params,
  };

  return httpOptions;
}

export function addParamsToHttpOptions(httpOption: AngularHttpOption, params: {}): void {
  Object
    .keys(params)
    .filter(key => isValue(params[key]))
    .forEach(key => {
      const value = params[key];
      httpOption.params = httpOption.params.set(key, toFormDataString(value, false));
    });
}
