import { Injectable } from '@angular/core';
import { SecurityConfig } from './security-config';
import { isString } from '../core/type-check/is-string.function';
import { isArray } from '../core/type-check/is-array.function';
import { isValue } from '../core/type-check/is-value.function';
import { isNumber } from '../core/type-check/is-number.function';


@Injectable({
  providedIn: 'root'
})
export class RoleManagerService {
  private readonly roles = new Set();
  private readonly roleSplitterExpr: RegExp;

  constructor(private config: SecurityConfig) {
    this.roleSplitterExpr = new RegExp(this.config.roleSplitterExp);
  }

  get enabled(): boolean {
    return this.config.rolesEnabled;
  }

  get roleCount(): number {
    return this.roles.size;
  }

  getRoles(): string[] {
    return Array.from(this.roles);
  }

  setRoles(roles: string | string[] | number | number[]): void {
    this.clearRoles();
    this.addRoles(roles);
  }

  addRoles(roles: string | string[] | number | number[]): void {
    if (isString(roles) && (roles as string).includes(',')) {
      roles = this.toArray((roles as string));
    }

    if (isArray(roles)) {
      (roles as string[]).forEach(this.addRole, this);
    } else {
      this.addRole(roles as string);
    }
  }

  addRole(role: string | number): void {
    this.roles.add(String(role).trim());
  }

  clearRoles(): void {
    this.roles.clear();
  }

  hasRole(role: string | number): boolean {
    if (!this.enabled) {
      return true;
    }

    if (!isValue(role)) {
      return false;
    }

    return this.hasRoles(String(role));
  }

  hasRoles(roles: string | number): boolean {
    let splitRoles;

    if (!this.enabled) {
      return true;
    }

    if (!isValue(roles)) {
      return false;
    }

    splitRoles = this.toArray(roles);
    return String(roles).includes(',')
      ? this.hasAllRoles(splitRoles)
      : this.hasAnyRoles(splitRoles);
  }

  hasAllRoles(roles: string | string[] | number | number[]): boolean {
    if (!this.enabled) {
      return true;
    }

    if (!isValue(roles)) {
      return false;
    }

    const roleList: string[] = this.toStringArray(roles);
    return roleList.every(this.forRealHasRole.bind(this));
  }

  hasAnyRoles(roles: string | string[] | number | number[]): boolean {
    if (!this.enabled) {
      return true;
    }

    if (!isValue(roles)) {
      return false;
    }

    const roleList: string[] = this.toStringArray(roles);
    return roleList.some(this.forRealHasRole.bind(this));
  }

  private forRealHasRole(role: string | number): boolean {
    return this.roles.has(String(role).trim());
  }

  private toStringArray(roles: string | string[] | number | number[]): string[] {
    let rolesList: string[] = [];
    if (isString(roles)) {
      rolesList = this.toArray(roles as string);
    } else if (isNumber(roles)) {
      rolesList.push(String(roles));
    } else {
      rolesList = roles as string[];
    }

    return rolesList;
  }

  private toArray(roles: string | number): string[] {
    return String(roles).split(this.roleSplitterExpr);
  }
}

