// 3rd Party.
import { Feature, Geometry, GeoJsonProperties, BBox } from 'geojson';

// TSS.
import { getMeasureExtent } from '../geometry/geometry/get-measure-extent.function';
import { isLine } from '../geometry/geometry/is-line.function';
import { getIdFieldFieldName, isFieldExpression } from './fields/layer-field';
import { getCodedValueNameOrDefault } from './fields/get-coded-value-name-or-default.function';
import { evaluteFeatureDataExpression } from '../lrs/data/evaluate-feature-data-expression.function';
import { FeatureClassLayer } from './feature-class-layer';
import { isPoint } from '../geometry/geometry/is-point.function';
import { isPolygon } from '../geometry/geometry/is-polygon.function';
import { WGS_84_SRID } from '../geometry/geometry.constants';
import { Cloneable } from '../core/clone/cloneable';
import { clone } from '../core/clone/clone.function';


export class MapFeature implements Feature, Cloneable<MapFeature> {
  id?: string | number;
  geometry: Geometry;
  properties: GeoJsonProperties;
  bbox: BBox;
  type: 'Feature';

  routeId: string;
  fromMeasure: number;
  toMeasure: number;
  layer: FeatureClassLayer;
  displayValue: any;
  styleValue: any;
  isLinear: boolean;
  isPoint: boolean;
  isPolygon: boolean;
  viewDate?: Date;
  gdbVersion?: string;
  spatialReferenceId?: number;

  get layerId(): number {
    return this.layer ? this.layer.id : null;
  }

  get hasZ(): boolean {
    return this.layer.hasZ;
  }

  get hasM(): boolean {
    return this.layer.hasM;
  }

  constructor(feature: Feature, layer: FeatureClassLayer, spatialReferenceId = WGS_84_SRID) {
    this.id = feature.id;
    this.properties = feature.properties;
    this.type = feature.type;
    this.bbox = feature.bbox;
    this.layer = layer;
    this.spatialReferenceId = spatialReferenceId;

    const idFieldName = this.getIdFieldName();

    this.id = this.id || this.getValue(idFieldName);
    this.displayValue = this.getValue(layer.displayField);
    this.setGeometry(feature.geometry);
  }

  getIdFieldName(): string {
    return getIdFieldFieldName(this.layer.fields);
  }

  getValue(fieldNameOrExpression: string): any {
    return isFieldExpression(fieldNameOrExpression)
      ? evaluteFeatureDataExpression(fieldNameOrExpression, this)
      : this.properties && this.properties[fieldNameOrExpression];
  }

  getCodedValueNameOrDefault(fieldName: string): any {
    const value = this.getValue(fieldName);
    const field = this.layer.getField(fieldName);
    return getCodedValueNameOrDefault(field, value);
  }

  setValue(fieldName: string, value: any): this {
    this.properties[fieldName] = value;
    return this;
  }

  setId(id: string | number): this {
    this.id = id;
    this.setValue(getIdFieldFieldName(this.layer.fields), id);
    return this;
  }

  setGeometry(geometry: Geometry): this {
    this.geometry = geometry;
    return this.setGeometryTypeProperties();
  }

  setMeasureFromGeometry(): this {
    if (this.geometry) {
      const extent = getMeasureExtent(this.geometry);
      this.fromMeasure = extent.min;
      this.toMeasure = extent.max;
    }
    return this;
  }

  isSpatialReferenceWgs84(): boolean {
    return this.spatialReferenceId === WGS_84_SRID;
  }

  clone(): MapFeature {
    const copyFeature = clone(this.toFeature());
    return new MapFeature(copyFeature, this.layer);
  }

  toFeature(): Feature {
    const feature: Feature = {
      type: 'Feature',
      geometry: this.geometry,
      properties: this.properties,
      id: this.id,
      bbox: this.bbox,
    };

    return feature;
  }

  toGeoJson(): Feature {
    return this.toFeature();
  }

  protected setGeometryTypeProperties(): this {
    this.isLinear = isLine(this.geometry);
    this.isPoint = isPoint(this.geometry);
    this.isPolygon = isPolygon(this.geometry);
    return this;
  }
}
