import { isNumberBetweenOrEqual } from '../../core/stats/is-number-between-or-equal.function';
import { isNumber } from '../../core/type-check/is-number.function';


export function isValidLatitude(latitide: number): boolean {
  return isNumber(latitide) && isNumberBetweenOrEqual(latitide, -90, 90);
}
