// 3rd Party.
import { Point, MultiPoint } from 'geojson';

// TSS.
import { AnyPoint } from './any-point';
import { isArray } from '../../core/type-check/is-array.function';


export function isMultiPartPoint(point: AnyPoint): boolean {
  const coordinates = isArray(point)
    ? (point as number[])
    : (point as Point | MultiPoint).coordinates;

  return Boolean(
    coordinates
    && coordinates.length
    && isArray(coordinates[0])
  );
}
