import { LayerField, LayerFieldCodedValueCode } from './layer-field';
import { getCodedValue } from './get-coded-value-domain.function';


export function getCodedValueName(field: LayerField, value: LayerFieldCodedValueCode): string {
  const codedValue = getCodedValue(field, value);
  return codedValue
    ? codedValue.name
    : undefined;
}
