import { MergeDeepArrayOptions } from './merge-deep-array-options';
import { mergeDeep } from './merge-deep.function';


export function mergeDeepArray(target: any[], source: any[], options?: MergeDeepArrayOptions): void {
  if (!target || !source) {
    return;
  }

  options = Object.assign({}, {
    identifier: 'id',
    addMissing: true,
    beforeMerge: () => undefined,
    afterMerge: () => undefined,
  }, options);

  target.forEach(targetItem => {
    const targetId = targetItem[options.identifier];
    const match = source.find(s => targetId && s[options.identifier] === targetId);
    if (match) {
      const continueWithMerge = options.beforeMerge(targetItem, match);
      if (continueWithMerge !== false) {
        mergeDeep(targetItem, match);
      }

      options.afterMerge(targetItem, match);
    }
  });

  if (options.addMissing) {
    source
      .filter(src => !target.find(x => src[options.identifier] && src[options.identifier] === x[options.identifier]))
      .forEach(missing => target.push(missing));
  }
}
