import { SinglePoint } from './single-point';
import { toSinglePartPointArray } from './to-single-part-point.function';
import { getLastItem } from '../../core/array/get-last-item.function';


export function getMeasureFromPoint(point: SinglePoint): number | null {
  const coordinate: number[] = toSinglePartPointArray(point);
  const hasMeasureValues = Boolean(
    coordinate
    && coordinate.length
    && coordinate.length > 2
  );

  return hasMeasureValues
    ? getLastItem(coordinate)
    : null;
}
