import { isMapServerServiceUrl } from './map-server/is-map-server-service-url';
import { isFeatureServerServiceUrl } from './feature-server/is-feature-server-service-url';
import { isLrsServerServiceUrl } from './lrs-server/is-lrs-server-service-url.function';
import { isLrsServerLayersServiceUrl } from './lrs-server/is-lrs-server-layers-service-url.function';


export function isCacheableEsriServiceRoute(urlPattern: string, testUrl: string): boolean {
  [testUrl] = (testUrl || '').split('?');
  return Boolean(urlPattern
    && testUrl
    && new RegExp(urlPattern, 'gi').test(testUrl)
    && isMapServerServiceUrl(testUrl)
    || isFeatureServerServiceUrl(testUrl)
    || isLrsServerServiceUrl(testUrl)
    || isLrsServerLayersServiceUrl(testUrl)
  );
}
