import { WhereClauseBuilder, CriteriaOperator, WhereClauseBuilderOptions } from '../maps/where-clause-builder';
import { LrsReferenceLayer, TemporalLayer } from './lrs-layer';
import { NetworkLayer } from './networks/network-layer';
import { EventLayer } from './events/event-layer';
import { DataSource } from '../data-source/data-source';
import { toUtcDate } from '../core/date-time/conversion/to-utc-date.function';
import { setDateToBeginningOfDay } from '../core/date-time/set-date-to-beginning-of-date.function';
import { DataSourceProviderType } from '../data-source/data-source-provider-type';
import { getDateFormatForProvider } from '../data-source/get-date-format-for-provider.function';
import { format } from '../core/formatting/format.function';
import { formatDateForQuery } from '../core/date-time/format/format-date-for-query.function';


export const NO_TEMPORAL_LAYER_SUPPLIED_ERROR_MSG = 'No LRS temporal layers supplied';
export const NO_REFERENCE_LAYER_SUPPLIED_ERROR_MSG = 'No LRS reference layer supplied';
export const LRS_DATE_WHERE_CLAUSE_TEMPLATE = '(({fromDateFieldName} IS NULL OR {fromDateFieldName} <= {dateEndOfDay})'
  + ' AND ({toDateFieldName} IS NULL OR {toDateFieldName} > {date}))';
export const LRS_POINT_EVENT_WHERE_CLAUSE_TEMPLATE = `({fromMeasureFieldName} between {fromMeasure} and {toMeasure})`;
export const LRS_LINEAR_EVENT_WHERE_CLAUSE_TEMPLATE = `(({fromMeasureFieldName} between {fromMeasure} AND {toMeasure})`
  + ` OR ({toMeasureFieldName} between {fromMeasure} AND {toMeasure}) OR ({fromMeasureFieldName} < {fromMeasure}`
  + ` AND {toMeasureFieldName} > {toMeasure})) AND NOT (({fromMeasureFieldName} < {fromMeasure}`
  + ` AND {toMeasureFieldName} <= {fromMeasure}) OR ({toMeasureFieldName} > {toMeasure} AND {fromMeasureFieldName} >= {toMeasure}))`;


export function getLrsViewDateWhereStatement(
  fromDateFieldName: string,
  toDateFieldName: string,
  dbType?: DataSourceProviderType,
  viewDate?: Date
): string {
  viewDate = toUtcDate(viewDate || new Date());
  setDateToBeginningOfDay(viewDate);

  // TODO: Look into whether we need to actually use end of day or not
  // Currently we are just setting it to same as 'date' - RG.
  const dateFormat = getDateFormatForProvider(dbType);

  return format(LRS_DATE_WHERE_CLAUSE_TEMPLATE, {
    fromDateFieldName,
    toDateFieldName,
    date: formatDateForQuery(viewDate, dateFormat),
    dateEndOfDay: formatDateForQuery(viewDate, dateFormat),
  });
}

export class LrsWhereClauseBuilder extends WhereClauseBuilder {
  // private networkLayer: NetworkLayer;
  // private eventLayer: EventLayer;
  private temporalLayer: TemporalLayer;
  private referenceLayer: LrsReferenceLayer;
  private sourceDbType: DataSourceProviderType;

  constructor(options?: WhereClauseBuilderOptions) {
    super(options);
  }

  withDataSource(sourceData: DataSource): this {
    return this.withDataProvider(sourceData.providerName);
  }

  withDataProvider(sourceDbType: DataSourceProviderType): this {
    this.sourceDbType = sourceDbType;
    return this;
  }

  withEventLayer(eventLayer: EventLayer): this {
    return this
      .withTemporalLayer(eventLayer)
      .withReferenceLayer(eventLayer);
  }

  withReferenceLayer(referenceLayer: LrsReferenceLayer): this {
    this.referenceLayer = referenceLayer;
    return this;
  }

  withNetworkLayer(networkLayer: NetworkLayer): this {
    return this.withTemporalLayer(networkLayer);
  }

  withTemporalLayer(temporalLayer: TemporalLayer): this {
    this.temporalLayer = temporalLayer;
    return this;
  }

  withRouteId(routeId: string | number, routeIdFieldName?: string): this {
    return this.where(routeIdFieldName || this.referenceLayer.routeIdFieldName, CriteriaOperator.Equals, routeId);
  }

  withViewDate(viewDate?: Date, fromDateFieldName?: string, toDateFieldName?: string): this {
    if (this.temporalLayer) {
      fromDateFieldName = fromDateFieldName || this.temporalLayer.fromDateFieldName;
      toDateFieldName = toDateFieldName || this.temporalLayer.toDateFieldName;
    }

    const hasDateFieldInfo = Boolean(this.temporalLayer || fromDateFieldName && toDateFieldName);
    if (!hasDateFieldInfo) {
      throw new Error(NO_TEMPORAL_LAYER_SUPPLIED_ERROR_MSG);
    }

    if (fromDateFieldName && toDateFieldName) {
      const dateClause = getLrsViewDateWhereStatement(
        this.formatField(fromDateFieldName),
        this.formatField(toDateFieldName),
        this.sourceDbType,
        viewDate
      );

      this.add(dateClause);
    }

    return this;
  }

  betweenMeasures(fromMeasure: number, toMeasure: number, fromMeasureFieldName?: string, toMeasureFieldName?: string): this {
    if (this.referenceLayer) {
      fromMeasureFieldName = fromMeasureFieldName || this.referenceLayer.fromMeasureFieldName;
      toMeasureFieldName = toMeasureFieldName || this.referenceLayer.toMeasureFieldName;
    }

    const hasMeasureFieldInfo = Boolean(this.referenceLayer || fromMeasureFieldName);
    if (!hasMeasureFieldInfo) {
      throw new Error(NO_REFERENCE_LAYER_SUPPLIED_ERROR_MSG);
    }

    const whereTemplate = toMeasureFieldName
      ? LRS_LINEAR_EVENT_WHERE_CLAUSE_TEMPLATE
      : LRS_POINT_EVENT_WHERE_CLAUSE_TEMPLATE;

    const measureClause = format(whereTemplate, {
      fromMeasureFieldName: this.formatField(fromMeasureFieldName),
      toMeasureFieldName: this.formatField(toMeasureFieldName),
      fromMeasure,
      toMeasure,
    });

    return this.add(measureClause);
  }
}
