import { Injectable } from '@angular/core';
import { ResourceConfig } from '../core/resource.config';
import { AutoConfig } from '../core/auto-config.decorator';


export type AuthorizationTypes = 'Bearer';

/**
 * Security related configuration options. These settings are used to drive all
 * security related behaviour throughout the application. The settings can be
 * overridden directly in the code, application module, or from the TDS server.
 */
@Injectable({
  providedIn: 'root'
})
@AutoConfig('SecurityConfig')
export class SecurityConfig extends ResourceConfig {
  rolesEnabled = true;
  storeToken = true;
  autoSignIn = true;
  authorizationType: AuthorizationTypes = 'Bearer';
  tokenEndpoint = 'token';
  signOutEndpoint = 'api/account/signout';
  userInfoEndpoint = 'api/account/userinfo';
  usersEndpoint = 'api/users/{id}';
  rolesEndpoint = 'api/roles/{id}';
  privilegesEndpoint = 'api/privileges/{id}';
  userRolesEndpoint = 'api/users/{userId}/roles/{roleName}';
  rolesPrivilegesEndpoint = 'api/roles/{roleId}/privileges/{privilegeId}';
  tokenStorageKey = '__tss_token';
  tokenInvalidationDateLookup = '__tss_token_invalidate_date';
  tdsTokenInclusionExpression = '/api/';
  withCredentialsInclusionExpression = '(.*?)';
  setWithCredentials = false;
  accessToken: string = null;
  defaultUserName: string = null;
  defaultPassword: string = null;
  roleSplitterExp = '[,|]+';
  tokenBody = (userName, password) => `grant_type=password&username=${userName}&password=${password}`;
}
