import { EsriServerToken } from './esri-server-token';


export function toEsriServerToken(token: any): EsriServerToken {
  const serverToken: EsriServerToken = {
    token: token.token || null,
    isSecure: token.ssl || token.isSecure || false,
    expires: token.expires || 0
  };

  if (serverToken.expires) {
    serverToken.expires = new Date(serverToken.expires);
  }

  return serverToken;
}
