import { PipeTransform, Pipe } from '@angular/core';
import { LayerRendererType } from './types';
import { LAYER_RENDER_TYPES, LayerRendererTypeDefinition } from './layer-renderer-type-definition';
import { LayerDrawingInfoRenderer } from './layer-drawing-info-renderer';
import { isString } from '../../core/type-check/is-string.function';


@Pipe({ name: 'layerRendererType' })
export class LayerRendererTypePipe implements PipeTransform {
  transform(input: LayerRendererType | LayerDrawingInfoRenderer): string {
    const renderType: LayerRendererType = isString(input)
      ? input as LayerRendererType
      : (input as LayerDrawingInfoRenderer).type;

    const definition: LayerRendererTypeDefinition = LAYER_RENDER_TYPES
      .find(t => t.renderType === renderType);

    return definition.alias;
  }
}
