/**
 * The storage persistance mechanism to use when storing cached data.
 * * inMemory: Only caches data in the users immediate session. Closing and re-opening
 *             the browser window will not keep the cached data.
 * * persistent: Persists the cache data across sessions. This can resolve to any
 *               number of storage mechasims (such as local storage, indexDb, etc).
 */
export enum CachePersistence {
  Persistent = 'persistent',
  InMemory = 'in-memory',
}
