export function groupBy<Key, Value>(list: Value[], keyGetter: ((item: Value) => Key)): Map<Key, Value[]> {
  const map = new Map();

  list.forEach((item: Value) => {
    const key: Key = keyGetter(item);
    const collection: Value[] = map.get(key);

    if (!collection) {
      map.set(key, [item]);
    } else {
      collection.push(item);
    }
  });

  return map;
}
