import { MapLayer } from '../maps/map-layer';
import { toLayerId } from '../maps/to-layer-id.function';
import { joinAndFomat } from '../core/url/join-and-format.function';


export function joinAndFomatWithLayer(layer: number | MapLayer, tokens: any, ...parts): string {
  const layerId = toLayerId(layer);
  return joinAndFomat(Object.assign({}, tokens, {
    layerId
  }), ...parts);
}
