import { Injectable } from '@angular/core';
import { LogLevel } from './logging/log-level';
import { AutoConfigData } from './auto-config-data';
import { MeasurementUnit } from './units/measurement-unit';


/**
 * A simple settings/config class for storing key/value pairs with persistence.
 */
@Injectable({
  providedIn: 'root'
})
export class CoreConfig {
  static readonly moduleConfigs: AutoConfigData[] = [{ className: 'CoreConfig', injectorToken: CoreConfig }];

  autoConfig = true;
  logEnabled = true;
  logLevel = LogLevel.Info;
  defaultNotifyInfoMsg = 'Info';
  defaultNotifyWarnMsg = 'Warning';
  defaultNotifyErrorMsg = 'Error';
  defaultNotifySuccessMsg = 'Success';
  defaultNotifyWaitMsg = 'Wait';
  measurementUnit = MeasurementUnit.Imperial;
}
