import { SinglePoint } from './single-point';
import { toSinglePartPointArray } from './to-single-part-point.function';
import { calculateCrossProduct } from '../../core/stats/calculate-cross-product.function';


export function calculateCrossProductByPoints(startPoint: SinglePoint, endPoint: SinglePoint, testPoint: SinglePoint): number {
  const [x1, y1] = toSinglePartPointArray(startPoint);
  const [x2, y2] = toSinglePartPointArray(endPoint);
  const [testX, testY] = toSinglePartPointArray(testPoint);

  return calculateCrossProduct(x1, y1, x2, y2, testX, testY);
}
