import { GpsPosition } from './gps-position';
import { DEFAULT_POINT_EQUALITY_PRECISION } from '../../core/equality/equality.constants';
import { pointsAlmostEqual } from '../../core/equality/points-almost-equal.function';


export function gpsPositionsAlmostEqual(
  position1: GpsPosition,
  position2: GpsPosition,
  precision = DEFAULT_POINT_EQUALITY_PRECISION
): boolean {
  return pointsAlmostEqual(position1.longitude, position1.latitude, position2.longitude, position2.latitude, precision);
}

