import { isNotValue } from '../type-check/is-not-value.function';
import { canBeNumber } from '../type-check/can-be-number.function';


export function toPrimitiveType(value: string): any {
  if (isNotValue(value)) {
    return value;
  } else if (value === 'true' || value === 'false') {
    return Boolean(value);
  } else if (canBeNumber(value)) {
    return Number(value);
  } else {
    return value;
  }
}
