import { DEFAULT_MAP_QUERY_OPTIONS } from './esri-query.constants';
import { EsriMapServiceQueryOptions } from './esi-map-service-query-options';


export function getMapQueryOptions(...options: EsriMapServiceQueryOptions[]) {
  const mergedOption = Object.assign({}, DEFAULT_MAP_QUERY_OPTIONS);
  (options || [])
    .forEach(option => Object.assign(mergedOption, option));

  return mergedOption;
}
