import { EsriPortalAuthRedirectOptions } from './esri-portal-auth-redirect-options';
import { PORTAL_OAUTH_AUTHORIZE_ENDPOINT, DEFAULT_PORTAL_OAUTH_REDIRECT_OPTIONS } from '../esri.constants';
import { getPortalUrl } from './get-portal-url.function';
import { join } from '../../core/url/join.function';


export function getPortalOAuthRedirectUrl(portalUrl: string, clientId: string, options: EsriPortalAuthRedirectOptions = {}): string {
  options = Object.assign({}, DEFAULT_PORTAL_OAUTH_REDIRECT_OPTIONS, options);
  portalUrl = getPortalUrl(portalUrl);

  const redirectUrl = encodeURIComponent(`${options.redirectUrl}${options.urlParamsBreak}`);
  const url = join(portalUrl, PORTAL_OAUTH_AUTHORIZE_ENDPOINT);

  return `${url}?client_id=${clientId}&redirect_uri=${redirectUrl}&response_type=${options.responseType}`;
}
