import { isArray } from '../core/type-check/is-array.function';
import { isObject } from '../core/type-check/is-object.function';
import { isNotValue } from '../core/type-check/is-not-value.function';
import { isValue } from '../core/type-check/is-value.function';


export function toFormDataString(value: {}, encodeData = true): string {
  let stringValue;
  if (isNotValue(value)) {
    stringValue = '';
  } else if (isObject(value) || isArray(value)) {
    stringValue = JSON.stringify(value);
  } else {
    stringValue = value.toString();
  }

  return isValue(stringValue) && encodeData ?
    encodeURIComponent(stringValue)
    : stringValue;
}
