import { toFormDataString } from './to-form-data-string.function';


export function toFormData(queryParameters: {}): FormData {
  const formData: FormData = new FormData();
  Object.keys(queryParameters)
    .map(key => ({
      key,
      value: toFormDataString(queryParameters[key])
    }))
    .forEach(keyValue => formData.append(keyValue.key, keyValue.value));

  return formData;
}
