import { NotificationTracker } from './notification-tracker';
import { NotifyResponse } from './notify-response';
import { NotifyEventType } from './notify-event-type';
import { NotifyService } from './notify.service';
import { Logger } from '../logging/logger.service';
import { NotificationConfig } from './notification-config';
import { isFunction } from '../type-check/is-function.function';
import { errorMsg } from '../error/error-msg.function';


export abstract class BaseNotificationTracker<T> implements NotificationTracker<T> {
  protected onDone: (value: T | undefined | null | PromiseLike<T>) => void;
  protected waitResponse: NotifyResponse;
  protected eventWatchers: {
    eventType: NotifyEventType,
    callback: ((e?: any) => void)
  }[] = [];

  constructor(protected notify: NotifyService, protected log?: Logger, protected config: NotificationConfig = { logErrors: true }) {
  }

  wait(message: string, title?: string, config?: any): NotificationTracker<T> {
    this.waitResponse = this.notify.wait(message, title, config);

    // This method needs implemented in each implementation.
    this.emit('wait');
    return this;
  }

  waitIf(predicate: boolean | (() => boolean), message: string, title?: string, config?: any): NotificationTracker<T> {
    const doWait = isFunction(predicate)
      ? (predicate as () => boolean)()
      : Boolean(predicate);

    return doWait ? this.wait(message, title, config) : this;
  }

  success(message: string, title?: string, config?: any): NotificationTracker<T> {
    this.config.showSuccess = true;
    this.config.successMsg = message;
    this.config.successTitle = title;
    this.config.successConfiguration = config;
    this.emit('success');
    return this;
  }

  error(message: string, title?: string, config?: any): NotificationTracker<T> {
    this.config.showError = true;
    this.config.errorMsg = message;
    this.config.errorTitle = title;
    this.config.errorConfiguration = config;
    this.emit('error');
    return this;
  }

  warn(message: string, title?: string, config?: any): NotificationTracker<T> {
    this.config.showWarn = true;
    this.config.warnMsg = message;
    this.config.warnTitle = title;
    this.config.warnConfiguration = config;
    this.emit('warn');
    return this;
  }

  info(message: string, title?: string, config?: any): NotificationTracker<T> {
    this.config.showInfo = true;
    this.config.infoMsg = message;
    this.config.infoTitle = title;
    this.config.infoConfiguration = config;
    this.emit('info');
    return this;
  }

  finally(onDone: (value: T | undefined | null | PromiseLike<T>) => void): NotificationTracker<T> {
    this.onDone = onDone;
    this.emit('finally');
    return this;
  }

  on(eventType: NotifyEventType, callback: (e?: any) => void) {
    this.eventWatchers.push({ eventType, callback });
  }

  protected emit(eventType: NotifyEventType, event?: any) {
    this.eventWatchers
      .filter(watcher => watcher.eventType === eventType)
      .forEach(watcher => watcher.callback(event));
  }

  protected done(response?: any) {
    if (this.waitResponse) {
      this.notify.clear(this.waitResponse.notifyId, this.waitResponse.containerId);
    }

    if (this.onDone) {
      this.onDone(response);
    }
  }

  protected onSuccess(response: any): void {
    if (this.config.showSuccess) {
      this.notify.success(this.config.successMsg, this.config.successTitle, this.config.successConfiguration);
    }

    if (this.config.showInfo) {
      this.notify.info(this.config.infoMsg, this.config.infoTitle, this.config.infoConfiguration);
    }

    this.done(response);
  }

  protected onFail(error: any): void {
    const responseError = (error.error || error) || error;
    const msg: string = errorMsg(this.config.errorMsg, responseError);

    if (this.log && this.config.logErrors) {
      this.log.error(msg);
    }

    if (this.config.showError) {
      this.notify.error(msg, this.config.errorTitle, responseError, this.config.errorConfiguration);
    }

    if (this.config.showWarn) {
      this.notify.warn(msg, this.config.infoTitle, this.config.warnConfiguration);
    }

    this.done();
  }
}
