// 3rd Party.
import { Feature } from 'geojson';

// TSS.
import { LrsLayer } from './lrs-layer';
import { MapFeature } from '../maps/map-feature';
import { isLine } from '../geometry/geometry/is-line.function';
import { isPoint } from '../geometry/geometry/is-point.function';
import { WGS_84_SRID } from '../geometry/geometry.constants';
import { Cloneable } from '../core/clone/cloneable';
import { clone } from '../core/clone/clone.function';


export class LrsFeature extends MapFeature implements Cloneable<LrsFeature> {
  routeId: string;
  fromMeasure: number;
  toMeasure: number;
  layer: LrsLayer;
  displayValue: any;
  styleValue: any;
  viewDate?: Date;
  gdbVersion?: string;

  get layerId(): number {
    return this.layer ? this.layer.id : null;
  }

  get fromDate(): Date {
    return this.getValue(this.layer.fromDateFieldName);
  }

  get toDate(): Date {
    return this.getValue(this.layer.toDateFieldName);
  }

  get isRetired(): boolean {
    return Boolean(this.toDate);
  }

  get isActive(): boolean {
    return !this.isRetired;
  }

  constructor(feature: Feature, layer: LrsLayer, spatialReferenceId = WGS_84_SRID) {
    super(feature, layer, spatialReferenceId);

    if (this.geometry && this.isGeometryTypeInvalid()) {
      throw new Error(`Unsupported geometry type of ${this.geometry.type} detected.`);
    }
  }

  setFromDate(date: Date): this {
    return this.setValue(this.layer.fromDateFieldName, date);
  }

  setToDate(date: Date): this {
    return this.setValue(this.layer.toDateFieldName, date);
  }

  retire(): this {
    if (this.layer.toDateFieldName) {
      this.setValue(this.layer.toDateFieldName, new Date());
    }

    return this;
  }

  clone(): LrsFeature {
    const copyFeature = clone(this.toFeature());
    return new LrsFeature(copyFeature, this.layer);
  }

  protected isGeometryTypeValid(): boolean {
    return isLine(this.geometry) || isPoint(this.geometry);
  }

  private isGeometryTypeInvalid(): boolean {
    return !this.isGeometryTypeValid();
  }
}
