import { Injectable } from '@angular/core';
import { AutoConfig } from '../core/auto-config.decorator';


export type ArcgisQueryMethodStrategy = 'prefer-get' | 'force-get' | 'force-post';


/**
 * Configuration class for storing global ESRI-related settings.
 */
@Injectable({
  providedIn: 'root'
})
@AutoConfig('EsriConfig')
export class EsriConfig {
  arcgis: { geometryUrl?: string } = {};
  arcgisQueryMethodStrategy: ArcgisQueryMethodStrategy = 'prefer-get';
  arcgisMaxWhereLengthGetThreshold = 3500;
  arcgisPortalUrl: string;
  arcgisPortalClientId: string;

  get hasGeometryUrl(): boolean {
    return Boolean(this.arcgis && this.arcgis.geometryUrl);
  }

  get arcGisGeometryUrl(): string {
    return this.hasGeometryUrl ? this.arcgis.geometryUrl : null;
  }
}
