// Angular.
import { Injectable } from '@angular/core';

// TSS.
import { AutoConfig } from '../core/auto-config.decorator';
import { DEFAULT_POINT_EQUALITY_PRECISION } from '../core/equality/equality.constants';


/**
 * Configuration class for storing geolocation-related settings.
 */
@Injectable({
  providedIn: 'root'
})
@AutoConfig('GeolocationConfig')
export class GeolocationConfig {
  gpsDisplayDecimalPlaces = 3;
  gpsWarnableAccuracyMeters = 5;
  gpsRequiredAccuracyMeters = 10;
  gpsWarnableDilutionOfPrecision = 2;
  gpsRequiredDilutionOfPrecision = 5;
  gpsEnableHighAccuracy = true;
  gpsNoAccuracyReading = '--';
  allowGpsMocking = true;
  emitDuplicateGpsPositions = false;
  gpsPositionDuplicatePrecision = DEFAULT_POINT_EQUALITY_PRECISION;
  gpsAntennaHeight = 0;
  adjustForGpsAntennaHeight = true;
  calculateMissingPositionValues = true;
  geolocationGeolocatorName = 'Built-in (default)';
  geolocationGeolocatorDescription = `Uses the device's built-in GPS sensor within the hardware. ` +
    `Devices without a GPS sensor will defer the location retrieval to the Inernet Service Provider (ISP).`;
}
