// Angular.
import { HttpClient, HttpParams } from '@angular/common/http';

// 3rd party.
import { Observable } from 'rxjs';

// TSS.
import { BaseEmptyResourceService } from './base-empty-resource.service';
import { ResourceServiceConfig } from './resource-service-config';


export abstract class BaseResourceService<T> extends BaseEmptyResourceService<T> implements BaseEmptyResourceService<T> {
  constructor(protected http: HttpClient, paramsOrUrl: string | ResourceServiceConfig) {
    super(http, paramsOrUrl);
  }

  query(routeTokens?: any, params?: HttpParams): Observable<T[]> {
    const url = this.getUrl(this.config.queryEndpoint, routeTokens);
    return this.http.get<T[]>(url, { params });
  }

  get(id: string | number, routeTokens?: any, params?: HttpParams): Observable<T> {
    const url = this.getUrlWithId(id, this.config.getEndpoint, routeTokens);
    return this.http.get<T>(url, { params });
  }

  create(entity: T, routeTokens?: any, params?: HttpParams): Observable<T> {
    const url = this.getUrl(this.config.createEndpoint, routeTokens);
    return this.http.post<T>(url, entity, { params });
  }

  update(entity: T, routeTokens?: any, params?: HttpParams): Observable<any> {
    const url = this.getUrlWithEntity(entity, this.config.updateEndpoint, routeTokens);
    return this.http.put(url, entity, { params });
  }

  delete(id: string | number, routeTokens?: any, params?: HttpParams): Observable<T> {
    const url = this.getUrlWithId(id, this.config.deleteEndpoint, routeTokens);
    return this.http.delete<T>(url, { params });
  }
}
