// Angular.
import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';

// 3rd Party.
import { Observable } from 'rxjs';

// TSS.
import { Logger } from '../core/logging/logger.service';
import { HttpConfig } from './http.config';
import { HttpParamInjectionDefinition } from './http-param-injection-definition';


@Injectable({
  providedIn: 'root'
})
export class HttpParamInterceptor implements HttpInterceptor {
  constructor(private config: HttpConfig, private log: Logger) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const httpParamInjections: HttpParamInjectionDefinition[] = this.config.paramInjectionsEnabled
      ? this.config.getInjectionDefinitions(request.urlWithParams)
      : [];

    if (httpParamInjections.length) {
      request = this.generateRequestWithParams(request, httpParamInjections);
    }

    return next.handle(request);
  }

  generateRequestWithParams(request: HttpRequest<any>, httpParamInjections: HttpParamInjectionDefinition[]): HttpRequest<any> {
    let params = request.params;
    httpParamInjections
      .forEach(paramInjection => {
        this.log.debug(`Injecting URL '${request.url}' with parameter value (name: '${paramInjection.paramName}')`);
        params = params.set(paramInjection.paramName, paramInjection.paramValue);
      });

    return request.clone({ params });
  }
}
