// 3rd party.
import { Position } from 'geojson';

// TSS.
import { LineGeometry } from './line-geometry';
import { toMultiPartPaths } from './to-multi-part-paths.function';


export function flattenLine(line: LineGeometry): Position[] {
  const flattenedPoints: Position[] = [];
  const multipParts: number[][][] = toMultiPartPaths(line);
  multipParts.forEach((part: number[][]) => {
    part.forEach((position: Position) => {
      flattenedPoints.push(position);
    });
  });

  return flattenedPoints;
}
