export class ValidationResult {
  private validationSkipped: boolean;

  passed: boolean;
  warned: boolean;
  ran: boolean;
  violaters: any[] = [];

  get failed(): boolean {
    return this.ran && !this.passed;
  }

  get skipped(): boolean {
    return this.validationSkipped;
  }

  set skipped(value: boolean) {
    this.validationSkipped = value;
    this.ran = true;
  }

  add(inner: ValidationResult): ValidationResult {
    if (inner.failed) {
      this.passed = false;
    }

    if (inner.warned) {
      this.warned = true;
    }

    return this;
  }
}
