import { LineGeometry } from './line-geometry';
import { MeasureExtent } from './measure-extent';
import { toMultiPartPaths } from './to-multi-part-paths.function';
import { sortLineByMeasure } from './sort-line-by-measure.function';
import { getMeasureFromPoint } from '../point/get-measure-from-point.function';
import { getLastItem } from '../../core/array/get-last-item.function';


export function getMeasureExtentFromLine(line: LineGeometry): MeasureExtent {
  const paths = toMultiPartPaths(line);
  let min: number;
  let max: number;
  if (paths.length) {
    sortLineByMeasure(line);
    const firstPath: number[][] = paths[0];
    const lastPath: number[][] = getLastItem(paths);
    const firstCoordinate: number[] = firstPath[0];
    const lastCoordinate: number[] = getLastItem(lastPath);

    min = getMeasureFromPoint(firstCoordinate);
    max = getMeasureFromPoint(lastCoordinate);
  }

  return {
    min,
    max
  };
}

