import { DEFAULT_ERROR_MSG } from './error.constants';
import { format } from '../formatting/format.function';
import { isString } from '../type-check/is-string.function';


export function errorMsg(errorOrMsg: any, error?: any): string {
  let exceptionDetails;

  errorOrMsg = errorOrMsg || DEFAULT_ERROR_MSG;
  if (isString(errorOrMsg)) {
    exceptionDetails = error ? errorMsg(error) : '';

    return format(errorOrMsg, { error: exceptionDetails });
  }

  return errorMsg(
    errorOrMsg.innerException
    || errorOrMsg.InnerException
    || errorOrMsg.exceptionMessage
    || errorOrMsg.ExceptionMessage
    || errorOrMsg.message
    || errorOrMsg.Message
    || errorOrMsg.msg
    || errorOrMsg.Msg
    || errorOrMsg.error_description
    || errorOrMsg.error
    || errorOrMsg.Error
    || errorOrMsg.data
    || errorOrMsg.Data
    || errorOrMsg.exception
    || errorOrMsg.Exception);
}
