import { Progressable } from './progressable';
import { sum } from './sum.function';


export class StatsTracker {
  progressables: Progressable[];

  constructor(...progressables: Progressable[]) {
    this.progressables = progressables || [];
  }

  add(progressable: Progressable): this {
    this.progressables.push(progressable);
    return this;
  }

  getPercentComplete(): number {
    const totalPercentComplete = sum(this.progressables, p => p.percentComplete);
    const overallComplete = totalPercentComplete / this.progressables.length;

    return Math.round(overallComplete);
  }
}
