/**
 * The Strategy to use when retrieving and caching data.
 * * networkFirst: Go to the network then go to the cache.
 * * cacheFirst: Go to the cache first and then go to the network for data.
 * * fastest: Use whichever strategy comes back first (a race).
 * * cacheOnly: Only look into the cache for data, never make network request.
 */
export enum CacheStrategy {
  NetworkFirst = 'network-first',
  CacheFirst = 'cache-first',
  Fastest = 'fastest',
  CacheOnly = 'cache-only',
}
