import { LengthUnit } from './length-unit';
import { LENGTH_UNIT_METER_CONVERSION_FACTOR } from './unit-conversion.constants';
import { isNotNumber } from '../type-check/is-not-number.function';


export function toMeters(value: number, fromUnit: LengthUnit): number {
  const conversionFactor = LENGTH_UNIT_METER_CONVERSION_FACTOR[fromUnit];
  if (isNotNumber(conversionFactor)) {
    throw new Error(`Can not convert unsupported unit: '${fromUnit}'`);
  }

  return value * conversionFactor;
}
