import { HttpClient } from '@angular/common/http';

import { Observable, forkJoin, of } from 'rxjs';
import { map, tap, mergeMap } from 'rxjs/operators';

import { LrsMetadata } from './lrs-metadata';
import { DataSource } from '../data-source/data-source';
import { DataSourceService } from '../data-source/data-source.service';
import { MapServerMetadata } from '../maps/map-server-metadata';
import { DataSourceLrsMetadataService } from './data-source-lrs-metadata.service';
import { Logger } from '../core/logging/logger.service';
import { NotifyService } from '../core/notify/notify.service';
import { EsriMapServerService } from '../esri/esri-map-server.service';
import { mergeLrsMetadataSources } from './lrs-metadata-merge';
import { isString } from '../core/type-check/is-string.function';


export class LrsMetadataRetriever {
  private dataSourceId: string;
  private dataSource: DataSource;
  private dataSourceLrsMetadata: LrsMetadata;
  private mapServerMetadata: MapServerMetadata;
  private mapServerLrsMetadata: LrsMetadata;

  constructor(
    protected http: HttpClient,
    protected mapServer: EsriMapServerService,
    protected dataSourceService: DataSourceService,
    protected dataSourceLrsMetadataService: DataSourceLrsMetadataService,
    protected notify: NotifyService,
    protected log: Logger) {
  }

  public withDataSource(dataSource: string | DataSource): LrsMetadataRetriever {
    if (isString(dataSource)) {
      this.dataSourceId = dataSource as string;
    } else {
      this.dataSource = dataSource as DataSource;
      this.dataSourceId = this.dataSource.id;
    }

    return this;
  }

  public withLrsMetadata(lrsMeta: LrsMetadata): LrsMetadataRetriever {
    this.dataSourceLrsMetadata = lrsMeta;
    return this;
  }

  public retrieve(): Observable<LrsMetadata> {
    if (!this.dataSourceId) {
      throw new Error('A data source ID must be supplied');
    }

    return of(null)
      .pipe(
        mergeMap(this.getDataSource.bind(this)),
        mergeMap(this.getDataSourceLrsMetadata.bind(this)),
        mergeMap(this.getMapServiceLayers.bind(this)),
        mergeMap(this.getMapServiceLrsMetadata.bind(this)),
        mergeMap(this.mergeAll.bind(this))
      );
  }

  private getDataSource(): Observable<DataSource> {
    const observable = this.dataSource
      ? of(this.dataSource)
      : this.dataSourceService
        .get(this.dataSourceId)
        .pipe(
          tap((dataSource: DataSource) => this.dataSource = dataSource)
        );

    return this.notify
      .with(observable)
      .error(`Failed to retrieve data source by ID: ${this.dataSourceId}: {error}`)
      .source;
  }

  private getDataSourceLrsMetadata(): Observable<LrsMetadata> {
    const observable = this.dataSourceLrsMetadata
      ? of(this.dataSourceLrsMetadata)
      : this.dataSourceLrsMetadataService
        .get(this.dataSourceId)
        .pipe(
          tap((meta: LrsMetadata) => {
            this.dataSourceLrsMetadata = new LrsMetadata(this.dataSource)
              .assign(meta);

            // NOTE: If we don't recieve any data source LRS metadata back then we assume that
            // the map service has LRS capabiliities - otherwise we have no LRS server data available.
            if (!meta) {
              this.dataSourceLrsMetadata.serviceHasLrsMeta = true;
            }
          })
        );

    return this.notify
      .with(observable)
      .error(`Failed to retrieve data source LRS metadata by data source ID: ${this.dataSourceId}: {error}`)
      .source;
  }

  private getMapServiceLayers(): Observable<MapServerMetadata> {
    const observable = this.mapServerMetadata || !this.dataSource.serviceUrl
      ? of(this.mapServerMetadata)
      : this.mapServer
        .getMapLayers(this.dataSource.serviceUrl)
        .pipe(
          tap((meta: MapServerMetadata) => this.mapServerMetadata = new MapServerMetadata().assign(meta))
        );

    return this.notify
      .with(observable)
      .error(`Failed to retrieve map service metadata from URL: ${this.dataSource.serviceUrl}: {error}`)
      .source;
  }

  private getMapServiceLrsMetadata(): Observable<LrsMetadata> {
    const observable = !this.dataSource.serviceUrl || !this.dataSourceLrsMetadata.serviceHasLrsMeta
      ? of(this.mapServerLrsMetadata)
      : forkJoin([
        this.mapServer.getLrsServer(this.dataSource.serviceUrl),
        this.mapServer.getLrsLayers(this.dataSource.serviceUrl)
      ])
        .pipe(
          map((meta: LrsMetadata[]) => {
            const [lrsServerMeta, lrsLayersMeta] = meta;
            const lrsMetadata = Object.assign({}, lrsServerMeta, lrsLayersMeta);
            const mapServerLrsMetadata = new LrsMetadata().assign(lrsMetadata);
            return mapServerLrsMetadata;
          }),
          tap((meta: LrsMetadata) => this.mapServerLrsMetadata = new LrsMetadata().assign(meta))
        );

    return this.notify
      .with(observable)
      .error(`Failed to retrieve map service LRS metadata from URL: ${this.dataSource.serviceUrl}: {error}`)
      .source;
  }

  private mergeAll(): Observable<LrsMetadata> {
    const fullLrsMeta = mergeLrsMetadataSources(
      this.dataSource,
      this.dataSourceLrsMetadata,
      this.mapServerLrsMetadata,
      this.mapServerMetadata
    );

    return of(fullLrsMeta);
  }
}
