import { Observable, Subscription } from 'rxjs';
import { tap, publishReplay } from 'rxjs/operators';

import { NotifyService } from './notify.service';
import { Logger } from '../logging/logger.service';
import { BaseNotificationTracker } from './base-notification-tracker';
import { NotificationConfig } from './notification-config';


export class NotificationSubscriptionTracker<T> extends BaseNotificationTracker<T> {
  constructor(public source: Observable<T>, notify: NotifyService, log?: Logger, config: NotificationConfig = {}) {
    super(notify, log, config);

    // We need to do a timeout here so that the chained events coming in after this method will be fired
    // before this subscription is added - this is necessary for any synchronous/immediate observables.
    this.source = this.source.pipe(
      tap((x: T) => {
        setTimeout(() => this.onSuccess(x));
      }, (error: any) => {
        setTimeout(() => this.onFail(error));
      })
    );

    if (this.config.useSingletonSubscriptionEmission) {
      this.source = this.source.pipe(publishReplay());
    }
  }

  wait(message: string, title?: string, config?: any): NotificationSubscriptionTracker<T> {
    return super.wait(message, title, config) as NotificationSubscriptionTracker<T>;
  }

  waitIf(predicate: boolean | (() => boolean), message: string, title?: string, config?: any): NotificationSubscriptionTracker<T> {
    return super.waitIf(predicate, message, title, config) as NotificationSubscriptionTracker<T>;
  }

  success(message: string, title?: string, config?: any): NotificationSubscriptionTracker<T> {
    return super.success(message, title, config) as NotificationSubscriptionTracker<T>;
  }

  error(message: string, title?: string, config?: any): NotificationSubscriptionTracker<T> {
    return super.error(message, title, config) as NotificationSubscriptionTracker<T>;
  }

  warn(message: string, title?: string, config?: any): NotificationSubscriptionTracker<T> {
    return super.warn(message, title, config) as NotificationSubscriptionTracker<T>;
  }

  info(message: string, title?: string, config?: any): NotificationSubscriptionTracker<T> {
    return super.info(message, title, config) as NotificationSubscriptionTracker<T>;
  }

  finally(onDone: (value: T | undefined | null | PromiseLike<T>) => void): NotificationSubscriptionTracker<T> {
    return super.finally(onDone) as NotificationSubscriptionTracker<T>;
  }

  subscribe(next?: (value: T) => void, error?: (error: any) => void, complete?: () => void): Subscription {
    return this.source.subscribe(next, error, complete);
  }
}
