import { LrsLayer, NetworkLayerMappingInfo } from '../lrs-layer';
import { HierarchyFilter } from '../hierarchy/hierarchy-filter';
import { MapLayer } from '../../maps/map-layer';
import { generateHierarchyFilters } from '../hierarchy/generate-hierarchy-filters.function';


export class NetworkLayer extends LrsLayer {
  routeIdFields: string[];
  routeIdSeparator: string;
  compositeRouteIdFieldName: string;
  routeNameFieldName: string;
  hasDominanceRules: boolean;
  autoGenerateRouteName: boolean;
  supportsLines: boolean;
  eventLayers: NetworkLayerMappingInfo[];
  intersectionLayers: NetworkLayerMappingInfo[];
  hierarchyFilter: HierarchyFilter[];

  constructor(...sources: MapLayer[]) {
    super(...sources);

    this.routeIdFields = this.routeIdFields || [];
    this.eventLayers = this.eventLayers || [];
    this.intersectionLayers = this.intersectionLayers || [];
    this.hierarchyFilter = this.hierarchyFilter && this.hierarchyFilter.length
      ? this.hierarchyFilter
      : generateHierarchyFilters(this);
  }
}
