import { NetworkLayer } from '../networks/network-layer';
import { HierarchyFilter } from './hierarchy-filter';


export function generateHierarchyFilters(layer: NetworkLayer): HierarchyFilter[] {
  return layer.routeIdFields.map((fieldName, index) => {
    const isFirst = index === 0;
    const field = layer.fields.find(f => f.name === fieldName);

    if (!field) {
      throw new Error(`Invalid network layer 'routeIdFields' configuration. Field '${fieldName}' not found on layer.`);
    }

    return {
      isRoot: isFirst,
      label: field.alias,
      networkLayerIdFieldName: field.name
    };
  });
}
