// Angular.
import { Injectable } from '@angular/core';

// TSS.
import { AutoConfig } from '../core/auto-config.decorator';


/**
 * Configuration class for storing Gps Gate-related settings.
 */
@Injectable({
  providedIn: 'root'
})
@AutoConfig('GpsGateConfig')
export class GpsGateConfig {
  refreshInterval = 1000;
  urlTemplate = 'http://localhost:12175/gps/{endpoint}?noCache={cacheBuster}';
  getInfoEndpoint = 'getGpsInfo';
  getVersionEndpoint = 'getVersion';
  jsonpParameterName = 'jsonp';
  geolocatorName = 'GpsGate Client';
  geolocatorDescription = 'GpsGate client that can connect to a variety of GPS data sources.';
}
