// Angular.
import { Injectable } from '@angular/core';

// 3rd Party.
import { Observable, Subject, fromEvent, merge } from 'rxjs';

// App.
import { Orientation } from './orientation';
import { getHeadingFromOrientation } from './heading/get-heading-from-orientation.function';
import { OrientationConfig } from './orientation.config';
import { map } from 'rxjs/operators';


/**
 * Geolocator service. Provides a single point of GPS interaction.
 */
@Injectable({
  providedIn: 'root'
})
export class DeviceOrientationService {
  readonly orientationChange: Observable<Orientation>;
  readonly headingChange: Observable<number>;
  readonly orientationChangeSource: Subject<Orientation>;
  readonly headingChangeSource: Subject<number>;

  private internalDeviceNeedsCalibrated = false;

  get deviceNeedsCalibrated(): boolean {
    return this.internalDeviceNeedsCalibrated;
  }

  constructor(private readonly config: OrientationConfig) {
    this.headingChangeSource = new Subject();
    this.orientationChangeSource = new Subject();
    this.orientationChange = this.orientationChangeSource.asObservable();
    this.headingChange = merge(
      this.headingChangeSource,
      this.orientationChange.pipe(
        map(orientation => orientation.heading)
      )
    );

    fromEvent(window, 'deviceorientation').subscribe((event: DeviceOrientationEvent) => this.emitOrientationChange(event));
    fromEvent(window, 'oncompassneedscalibration').subscribe(() => this.internalDeviceNeedsCalibrated = true);
    fromEvent(window, 'compassneedscalibration').subscribe(() => this.internalDeviceNeedsCalibrated = true);
  }

  mockOrientation(orientation: Orientation): this {
    if (!this.config.allowOrientationMocking) {
      throw new Error('Permission denied to mock device orientation');
    }

    this.orientationChangeSource.next(orientation);
    return this;
  }

  mockHeading(heading: number): this {
    if (!this.config.allowHeadingMocking) {
      throw new Error('Permission denied to mock heading');
    }

    this.headingChangeSource.next(heading);
    return this;
  }

  private emitOrientationChange(event: DeviceOrientationEvent): void {
    this.orientationChangeSource.next({
      alpha: event.alpha,
      beta: event.beta,
      gamma: event.gamma,
      heading: getHeadingFromOrientation(event)
    });
  }
}
