import { GpsPosition } from './gps-position';
import { isDefined } from '../../core/type-check/is-defined.function';


export function gpsPositionToPoint(position: GpsPosition): number[] {
  const point = [position.longitude, position.latitude];
  if (isDefined(position.altitude)) {
    point[2] = position.altitude;
  }

  return point;
}
