import { toSinglePartPointArray } from './to-single-part-point.function';
import { SinglePoint } from './single-point';
import { EARTH_RADIUS_IN_KILOMETERS } from '../geometry.constants';
import { degreesToRadians } from '../geometry/degrees-to-radians.function';
import { convertLengthUnits } from '../../core/units/convert-length-units.function';
import { LengthUnit } from '../../core/units/length-unit';


export function getDistanceBetweenPoints(point1: SinglePoint, point2: SinglePoint): number {
  const coordinate1: number[] = toSinglePartPointArray(point1);
  const coordinate2: number[] = toSinglePartPointArray(point2);

  const x1: number = coordinate1[0];
  const y1: number = coordinate1[1];
  const x2: number = coordinate2[0];
  const y2: number = coordinate2[1];

  return getDistanceBetweenPointValues(x1, y1, x2, y2);
}

export function getDistanceBetweenPointValues(x1: number, y1: number, x2: number, y2: number): number {
  // NOTE: This is using the pythagorean theorem (a^2 + b^2 = c^2).
  const xLength = Math.pow(x2 - x1, 2);
  const yLength = Math.pow(y2 - y1, 2);
  const distance = Math.sqrt(xLength + yLength);

  if (isNaN(distance)) {
    throw new Error(`Unable to calculate distance between points (${x1},${x2} and ${y1},${y2}). Invalid or missing data was provided`);
  }

  return distance;
}

export function getKilometersBetweenLatLng(lat1: number, lng1: number, lat2: number, lng2: number): number {
  // Modified from: https://stackoverflow.com/questions/365826/calculate-distance-between-2-gps-coordinates - RG.
  const dLat = degreesToRadians(lat2 - lat1);
  const dLon = degreesToRadians(lng2 - lng1);

  lat1 = degreesToRadians(lat1);
  lat2 = degreesToRadians(lat2);

  const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.sin(dLon / 2) * Math.sin(dLon / 2) * Math.cos(lat1) * Math.cos(lat2);

  const circumference = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

  return EARTH_RADIUS_IN_KILOMETERS * circumference;
}

export function getMetersBetweenLatLng(lat1: number, lng1: number, lat2: number, lng2: number): number {
  return getDistanceBetweenLatLng(lat1, lng1, lat2, lng2, LengthUnit.Meter);
}

export function getFeetBetweenLatLng(lat1: number, lng1: number, lat2: number, lng2: number): number {
  return getDistanceBetweenLatLng(lat1, lng1, lat2, lng2, LengthUnit.Foot);
}

export function getMilesBetweenLatLng(lat1: number, lng1: number, lat2: number, lng2: number): number {
  return getDistanceBetweenLatLng(lat1, lng1, lat2, lng2, LengthUnit.Mile);
}

export function getDistanceBetweenLatLng(lat1: number, lon1: number, lat2: number, lon2: number, outputUnit: LengthUnit): number {
  const distanceInKilometers = getKilometersBetweenLatLng(lat1, lon1, lat2, lon2);
  return convertLengthUnits(distanceInKilometers, LengthUnit.Kilometer, outputUnit);
}
