// Angular.
import { HttpClient } from '@angular/common/http';

// TSS.
import { Logger } from '../core/logging/logger.service';
import { LrsWhereClauseBuilder } from '../lrs/lrs-where-clause-builder';
import { EsriConfig } from './esri-config';
import { EsriMapServiceErrorResponse } from './esri-map-server-error-response';
import { DataSourceConfig } from '../data-source/data-source-config';


export abstract class BaseEsriService {
  constructor(protected http: HttpClient, protected config: EsriConfig, protected dataConfig: DataSourceConfig, protected log: Logger) {
  }

  protected shouldUsePostMethod(payload: string | {}): boolean {
    return this.config.arcgisQueryMethodStrategy === 'force-post'
      || JSON.stringify(payload || '').length > this.config.arcgisMaxWhereLengthGetThreshold;
  }

  protected handleEsriResponseError<T>(response: T | EsriMapServiceErrorResponse, url: string) {
    const error = response as EsriMapServiceErrorResponse;
    if (error && error.error) {
      const returnError = new Error(`Failed to query map service: ${error.error.message}`);
      this.log.error(`Failed to execute map service request: ${url}`, returnError);
      throw returnError;
    }
  }

  protected getWhereBuilder(): LrsWhereClauseBuilder {
    return new LrsWhereClauseBuilder({ quoteFields: !this.dataConfig.skipColumnQuotes });
  }
}






















