// Angular.
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';

// 3rd party.
import { Observable } from 'rxjs';

// TSS.
import { AppConfig } from './app.config';
import { BaseEmptyResourceService } from '../core/resource/base-empty-resource.service';


export interface ApiStatus {
  up: boolean;
  down: boolean;
  configStoreUp: boolean;
  configStoreDown: boolean;
  upSince: Date;
  daysUp: number;
  appCount: number;
}


@Injectable({
  providedIn: 'root'
})
export class ApiService extends BaseEmptyResourceService<ApiStatus> {
  constructor(http: HttpClient, private appConfig: AppConfig) {
    super(http, appConfig.serviceUrl);
  }

  ping(routeTokens?: any, params?: HttpParams): Observable<void> {
    const url = this.getUrl(this.appConfig.pingEndpoint, routeTokens);
    return this.http.get<void>(url, { params });
  }

  status(routeTokens?: any, params?: HttpParams): Observable<ApiStatus> {
    const url = this.getUrl(this.appConfig.pingEndpoint, routeTokens);
    return this.http.get<ApiStatus>(url, { params });
  }
}
