// 3rd Party.
import { FeatureCollection, BBox } from 'geojson';

// TSS>
import { LrsFeature } from './lrs-feature';


export class LrsFeatureCollection implements FeatureCollection {
  type: 'FeatureCollection';
  features: Array<LrsFeature>;
  bbox?: BBox;

  constructor(collection: FeatureCollection) {
    this.bbox = collection.bbox;
    this.type = collection.type;
  }

  static create(features: LrsFeature[], bbox?: BBox): LrsFeatureCollection {
    return new LrsFeatureCollection({
      type: 'FeatureCollection',
      features,
      bbox
    });
  }

  toGeoJson(): FeatureCollection {
    return {
      type: 'FeatureCollection',
      bbox: this.bbox,
      features: this.features.map(feature => feature.toGeoJson())
    };
  }
}
