import { LayerField, isFieldValueNumeric } from './fields/layer-field';


export const FIELD_QUOTE = '"';
export enum CriteriaOperator {
  Equals = '=',
  DoesNotEqual = '<>',
  // In = 'IN',
  // OneOf, NotOneOf, Between, NotBetween, Contains...
  GreaterThan = '>',
  LessThan = '<',
  GreaterOrEqualTo = '>=',
  LessThanOrEqualTo = '<=',
}

export enum TargetOperator {
  Is = 'IS',
  Not = 'NOT',
  In = 'IN',
  Like = 'LIKE',
  Between = 'BETWEEN',
}

export enum ValueOperator {
  Wildcard = '%',
  Null = 'NULL',
}

export enum GroupingOperator {
  OpenParenthesis = '(',
  ClosedParenthesis = ')',
  Parenthesis = '()',
}

export enum CombiningOperator {
  And = 'AND',
  Or = 'OR',
}

export interface WhereClauseBuilderOptions {
  quoteFields?: boolean;
}

export const DefaultWhereClauseBuilderOptions = {
  quoteFields: true
};

export class WhereClauseBuilder {
  private readonly statements: string[] = [];
  private readonly options: WhereClauseBuilderOptions;

  constructor(options?: WhereClauseBuilderOptions) {
    this.options = Object.assign({}, DefaultWhereClauseBuilderOptions, options);
  }

  get criteriaCount(): number {
    return this.statements.length;
  }

  add(statement: string): this {
    this.statements.push(statement);
    return this;
  }

  whereEqual(field: LayerField, value: any): this {
    return value === null
      ? this.whereIsNull(field.name)
      : this.where(field.name, CriteriaOperator.Equals, value, isFieldValueNumeric(field, value));
  }

  whereLike(field: string, value: any): this {
    return this.add(`${this.formatField(field)} ${TargetOperator.Like} '%${value}%'`);
  }

  whereStartsWith(field: string, value: any): this {
    return this.add(`${this.formatField(field)} ${TargetOperator.Like} '${value}%'`);
  }

  whereEndsWith(field: string, value: any): this {
    return this.add(`${this.formatField(field)} ${TargetOperator.Like} '%${value}'`);
  }

  whereNotEqual(field: LayerField, value: any): this {
    return value === null
      ? this.whereIsNotNull(field.name)
      : this.where(field.name, CriteriaOperator.DoesNotEqual, value, isFieldValueNumeric(field, value));
  }

  whereIsNull(field: string): this {
    return this.add(`${this.formatField(field)} ${TargetOperator.Is} ${ValueOperator.Null}`);
  }

  whereIsNotNull(field: string): this {
    return this.add(`${this.formatField(field)} ${TargetOperator.Is} ${TargetOperator.Not} ${ValueOperator.Null}`);
  }

  where(field: string, operator: CriteriaOperator | TargetOperator, value: any, isNumeric = false): this {
    value = isNumeric ? value : `'${value}'`;
    this.add(`${this.formatField(field)}${operator}${value}`);
    return this;
  }

  whereNumeric(field: string, value: any): this {
    this.where(field, value, true);
    return this;
  }

  toWhereClause(): string {
    return this.statements.join(` ${CombiningOperator.And} `);
  }

  toString(): string {
    return this.toWhereClause();
  }

  clear() {
    this.statements.length = 0;
  }

  protected formatField(field: string) {
    field = field || '';
    const addQuotes = this.options.quoteFields;

    if (addQuotes) {
      if (!field.startsWith(FIELD_QUOTE)) {
        field = `${FIELD_QUOTE}${field}`;
      }

      if (!field.endsWith(FIELD_QUOTE)) {
        field = `${field}${FIELD_QUOTE}`;
      }
    } else {
      const quoteRegex = new RegExp(FIELD_QUOTE, 'i');
      return field.replace(quoteRegex, '');
    }

    return field;
  }
}
