import { LrsLayer } from './lrs-layer';
import { NetworkLayer } from './networks/network-layer';
import { getIdField } from '../maps/fields/layer-field';
import { LrsMetadata } from './lrs-metadata';
import { ValidationCheck } from '../core/validation/validation-check';


export const LRS_LAYER_VALIDATIONS: ValidationCheck<LrsLayer> = new ValidationCheck<LrsLayer>(`Layer '{name}'`)
  .add(
    new ValidationCheck<LrsLayer>('should have list of fields')
      .pass(l => Boolean(l.fields && l.fields.length)),

    new ValidationCheck<LrsLayer>('should have an ID field in list of fields')
      .pass(l => Boolean(getIdField(l.fields)))
  );

export const LRS_NETWORK_LAYER_VALIDATIONS: ValidationCheck<NetworkLayer> = new ValidationCheck<NetworkLayer>(
  `Network layer '{name}' (ID: {id})`
)
  .add(
    new ValidationCheck<NetworkLayer>(`should have a 'Composite Route ID Field Name' value specified`)
      .pass(l => Boolean(l.compositeRouteIdFieldName))
  );

export const LRS_EVENT_LAYER_VALIDATIONS: ValidationCheck<NetworkLayer> = new ValidationCheck<NetworkLayer>(
  `Event layer '{name}' (ID: {id})`
)
  .add(
    new ValidationCheck<NetworkLayer>(`should have a 'From Measure Field Name' value specified`)
      .pass(l => Boolean(l.compositeRouteIdFieldName))
  );

export const LRS_METADATA_VALIDATIONS: ValidationCheck<LrsMetadata> = new ValidationCheck<LrsMetadata>('LRS Metadata')
  .add(
    new ValidationCheck<LrsMetadata>('should have network layers')
      .pass(x => Boolean(x.networkLayers && x.networkLayers.length)),

    new ValidationCheck<LrsMetadata>('should have event layers')
      .pass(x => Boolean(x.eventLayers && x.eventLayers.length)),

    // TODO: Add other nested validations - RG.
    // .forProperty(lrsMeta => lrsMeta.eventLayers)
    // .add()
  );
