import { STANDARDIZED_QUERY_DATE_FORMAT } from '../date-time.constants';
import { toUtcDate } from './to-utc-date.function';
import { formatDate } from '../format/format-date.function';


export function toStandardizedQueryDateFormat(date: Date): string {
  // Attribute query expects UTC time, so convert from local time.
  // The date formatter uses local time, so create a new Date object
  // whose local time is set to UTC time from the input date.
  date = toUtcDate(date);
  return formatDate(date, STANDARDIZED_QUERY_DATE_FORMAT);
}
