// 3rd Party.
import { MultiPoint, Point } from 'geojson';

// TSS.
import { AnyPoint } from './any-point';
import { isArray } from '../../core/type-check/is-array.function';
import { isMultiPartPoint } from './is-multi-part-point.function';


export function toMultiPartPoint(point: AnyPoint): number[][] {
  const coordinates: number[] | number[][] = isArray(point)
    ? (point as number[])
    : (point as Point | MultiPoint).coordinates;

  return isMultiPartPoint(coordinates)
    ? coordinates as number[][]
    : [coordinates as number[]];
}
