import { RouteExpression } from './route-expression';
import { isFunction } from '../core/type-check/is-function.function';
import { isString } from '../core/type-check/is-string.function';
import { replaceTokens } from '../core/tokens/replace-tokens.function';


export function isRouteMatch(route: string, routeExpression: RouteExpression): boolean {
  let exp: RegExp;

  if (isFunction(routeExpression)) {
    return Boolean((routeExpression as (url: string) => boolean)(route));
  }

  if (isString(routeExpression)) {
    // NOTE: We are replacing the friendly route matching with real regex.
    const wildcardExp = new RegExp('[*]', 'gi');
    const routeAsString = routeExpression as string;
    const resolvedRoute = replaceTokens(routeAsString, '*')
      .replace(wildcardExp, '.*');

    exp = new RegExp(resolvedRoute, 'i');
  } else {
    exp = routeExpression as RegExp;
  }

  return exp.test(route);
}
