// Angular.
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';

// 3rd Party.
import { FeatureCollection } from 'geojson';
import { Observable } from 'rxjs';

// TSS.
import { DataSourceConfig } from './data-source-config';
import { DataSource } from './data-source';
import { DataTableInfo } from './data-table-info';
import { DataColumn } from './data-column';
import { DataTable } from './data-table';
import { DataSourceSpatialExportFormat } from './data-source-spatial-export-format';
import { BaseResourceService } from '../core/resource/base-resource.service';
import { EMPTY_PARAM_VALUE } from '../core/resource/resource.constants';
import { isValue } from '../core/type-check/is-value.function';
import { SELECT_ALL_WHERE_CLAUSE } from './data-source.constants';


@Injectable({
  providedIn: 'root'
})
export class DataSourceService extends BaseResourceService<DataSource> {
  constructor(protected http: HttpClient, private dsConfig: DataSourceConfig) {
    super(http, {
      url: dsConfig.serviceUrl,
      queryEndpoint: dsConfig.dataSourcesEndpoint,
      getEndpoint: dsConfig.dataSourcesEndpoint,
      updateEndpoint: dsConfig.dataSourcesEndpoint,
      deleteEndpoint: dsConfig.dataSourcesEndpoint,
      createEndpoint: dsConfig.dataSourcesEndpoint,
    });
  }

  testConnection(entity: DataSource, useEntityFramework: boolean, params?: HttpParams): Observable<DataTable> {
    throw new Error('Not implemented yet');
  }

  testExistingConnection(id: string, params?: HttpParams): Observable<DataTable> {
    throw new Error('Not implemented yet');
  }

  // 'http:' + this.appConfig.serviceUrl + '/api/data/sources/:id/tables'
  getTables(dataSourceId: string, params?: HttpParams): Observable<DataTableInfo[]> {
    const url = this.getUrlWithId(dataSourceId, this.dsConfig.tablesEndpoint, {});
    return this.http.get<DataTableInfo[]>(url, { params });
  }

  // 'http:' + this.appConfig.serviceUrl + '/api/data/sources/:id/tables/details'
  getTableDetails(dataSourceId: string, params?: HttpParams): Observable<DataTable> {
    const url = this.getUrlWithId(dataSourceId, this.dsConfig.tableActionEndpoint, {
      action: 'details'
    });

    return this.http.get<DataTable>(url, { params });
  }

  // 'http:' + this.appConfig.serviceUrl + '/api/data/sources/:id/tables/:table/columns/:column/:action',
  getColumns(dataSourceId: string, table: string, params?: HttpParams): Observable<DataColumn[]> {
    const url = this.getUrlWithId(dataSourceId, this.dsConfig.columnsEndpoint, { table });

    return this.http.get<DataColumn[]>(url, { params });
  }

  // api/data/sources/test_1051_sql/tables/RH.DIVIDED_ROADS/columns/SurveyDirection/values?unique=false
  getColumnValues<T>(dataSourceId: string, table: string, column: string, unique: boolean = false, params?: HttpParams): Observable<T[]> {
    const url = this.getUrlWithId(dataSourceId, this.dsConfig.columnsEndpoint, {
      table,
      column,
      action: 'values'
    });
    params = (params || new HttpParams()).set('unique', String(unique));

    return this.http.get<T[]>(url, { params });
  }

  // api/data/sources/test_1051_sql/tables/RH.DIVIDED_ROADS/columns/SurveyDirection/values?unique=true
  getUniqueColumnValues<T>(dataSourceId: string, table: string, column: string, params?: HttpParams): Observable<T[]> {
    return this.getColumnValues<T>(dataSourceId, table, column, true, params);
  }

  // 'http:' + this.appConfig.serviceUrl + '/api/data/sources/:id/tables/:count'
  getRecordCount(
    dataSourceId: string,
    table: string,
    whereClause: string = SELECT_ALL_WHERE_CLAUSE,
    version?: string,
    params?: HttpParams
  ): Observable<number> {
    const url = this.getUrlWithId(dataSourceId, this.dsConfig.tableActionEndpoint, {
      table,
      action: 'count'
    });

    params = (params || new HttpParams())
      .set('where', whereClause)
      .set('version', version || EMPTY_PARAM_VALUE);

    return this.http.get<number>(url, { params });
  }

  // api/data/sources/test_1051_sql/tables/RH.DIVIDED_ROADS/query/details?page=1&pageSize=25&where=1%3D1
  queryData(
    dataSourceId: string,
    table: string,
    whereClause: string,
    version?: string,
    page?: number,
    pageSize?: number,
    includeGeometry?: boolean,
    shapeField?: string,
    params?: HttpParams
  ): Observable<DataTable> {
    return this.querySpatialData<DataTable>(
      dataSourceId,
      table,
      whereClause,
      version,
      page,
      pageSize,
      includeGeometry,
      shapeField,
      'json',
      params,
    );
  }

  queryGeoJsonData(
    dataSourceId: string,
    table: string,
    whereClause: string,
    version?: string,
    page?: number,
    pageSize?: number,
    includeGeometry?: boolean,
    shapeField?: string,
    params?: HttpParams
  ): Observable<FeatureCollection> {
    return this.querySpatialData<FeatureCollection>(
      dataSourceId,
      table,
      whereClause,
      version,
      page,
      pageSize,
      includeGeometry,
      shapeField,
      'geojson',
      params,
    );
  }

  querySpatialData<T extends DataTable | FeatureCollection>(
    dataSourceId: string,
    table: string,
    whereClause: string,
    version?: string,
    page?: number,
    pageSize?: number,
    includeGeometry?: boolean,
    shapeField?: string,
    format?: DataSourceSpatialExportFormat,
    params?: HttpParams
  ): Observable<T> {
    const url = this.getUrlWithId(dataSourceId, this.dsConfig.tableActionEndpoint, {
      table,
      action: 'query/details'
    });

    params = (params || new HttpParams())
      .set('where', whereClause || EMPTY_PARAM_VALUE)
      .set('version', version || EMPTY_PARAM_VALUE)
      .set('page', isValue(page) ? String(page) : EMPTY_PARAM_VALUE)
      .set('pageSize', isValue(pageSize) ? String(pageSize) : EMPTY_PARAM_VALUE)
      .set('includeGeometry', isValue(includeGeometry) ? String(includeGeometry) : EMPTY_PARAM_VALUE)
      .set('shapeField', shapeField || EMPTY_PARAM_VALUE)
      .set('format', format || EMPTY_PARAM_VALUE);

    return this.http.get<T>(url, { params });
  }
}
