import { almostEqual } from './almost-equal.function';
import { DEFAULT_POINT_EQUALITY_PRECISION } from './equality.constants';


export function pointsAlmostEqual(x1: number, y1: number, x2: number, y2: number, precision = DEFAULT_POINT_EQUALITY_PRECISION): boolean {
  return almostEqual(x1, x2, precision) && almostEqual(y1, y2, precision);
}
