import { LineGeometry, SingleLine, MultiLine } from './line-geometry';
import { toMultiPartPaths } from './to-multi-part-paths.function';
import { singleLineToCoordinates } from './single-line-to-coordinates';
import { multiLineToCoordinates } from './multi-line-to-coordinates';


export function hasMeasureValues(line: LineGeometry): boolean {
  const paths: number[][][] = toMultiPartPaths(line);
  return multiLineHasMeasureValues(paths);
}

export function singleLineHasMeasureValues(line: SingleLine): boolean {
  const coordinates = singleLineToCoordinates(line);
  return Boolean(
    coordinates
    && coordinates.length
    && coordinates[0].length > 2
  );
}

export function multiLineHasMeasureValues(line: MultiLine): boolean {
  const coordinates = multiLineToCoordinates(line);
  return Boolean(
    coordinates.length
    && singleLineHasMeasureValues(coordinates[0])
  );
}
