// 3rd Party.
import { Feature, Geometry } from 'geojson';

// TSS.
import { LrsMetadata } from './lrs-metadata';
import { LrsFeature } from './lrs-feature';
import { NetworkFeature } from './networks/network-feature';
import { EventLayer } from './events/event-layer';
import { NetworkLayer } from './networks/network-layer';
import { EventFeature } from './events/event-feature';


export function toLrsFeature(feature: Feature, layerId: number, lrsMeta: LrsMetadata) {
  const layerInfo = lrsMeta.getLayerDetail(layerId);
  let lrsFeature: LrsFeature;

  if (layerInfo.isNetworkLayer) {
    lrsFeature = new NetworkFeature(feature, layerInfo.layer as NetworkLayer);
  } else if (layerInfo.isEventLayer) {
    lrsFeature = new EventFeature(feature, layerInfo.layer as EventLayer);
  }

  return lrsFeature;
}

// Feature<G extends GeometryObject | null = Geometry, P = GeoJsonProperties> extends GeoJsonObject
export function toFeature(properties: { [name: string]: any; } = {}, geometry: Geometry = null): Feature {
  return {
    type: 'Feature',
    geometry,
    properties
  };
}
