import { clean } from './clean.function';
import { getLastItem } from '../array/get-last-item.function';


export function getPostHashRoutePaths(url: string): string[] {
  const parts = clean(url).split('#');
  const hashPart = parts.length > 1
    ? getLastItem(parts)
    : null;

  return hashPart && hashPart !== '/'
    ? hashPart
      .split('/')
      .filter(part => Boolean(part) && !part.includes('='))
    : [];
}
