import { CoreConfig } from './core.config';
import { AutoConfigData } from './auto-config-data';
import { isString } from './type-check/is-string.function';


export function AutoConfig(config: AutoConfigData | string): ClassDecorator {
  return (constructor: any) => {
    const autoConf: AutoConfigData = isString(config)
      ? { className: config as string, injectorToken: constructor }
      : config as AutoConfigData;

    CoreConfig.moduleConfigs.push(autoConf);
  };
}
