// 3rd Party.
import { Geometry, MultiLineString, LineString, MultiPoint, Point } from 'geojson';

// TSS.
import { MeasureExtent } from '../line/measure-extent';
import { getMeasureExtentFromLine } from '../line/get-measure-extent-from-line.function';
import { isLine } from './is-line.function';
import { getMeasureExtentFromPoint } from '../point/get-measure-extent-from-point.function';


export function getMeasureExtent(geometry: Geometry): MeasureExtent {
  let extent: MeasureExtent;
  if (isLine(geometry)) {
    const lineGeometry = geometry as LineString | MultiLineString;
    extent = getMeasureExtentFromLine(lineGeometry);
  } else {
    const point = geometry as Point | MultiPoint;
    extent = getMeasureExtentFromPoint(point);
  }

  return extent;
}
