// Angular.
import { Injectable } from '@angular/core';

// TSS.
import { AutoConfig } from '../core/auto-config.decorator';
import { HttpParamInjectionDefinition } from './http-param-injection-definition';
import { isRouteMatch } from './is-route-match.function';
import { RouteExpression } from './route-expression';


/**
 * Configuration class for storing global data caching settings.
 */
@Injectable({
  providedIn: 'root'
})
@AutoConfig('HttpConfig')
export class HttpConfig {
  get hasInjections(): boolean {
    return Boolean(this.paramInjectionDefitions.length);
  }

  paramInjectionsEnabled = true;
  paramInjectionDefitions: HttpParamInjectionDefinition[] = [];

  addOrUpdateParameterInjection(route: RouteExpression, paramName: string, paramValue: string): HttpParamInjectionDefinition {
    const definition: HttpParamInjectionDefinition = {
      route,
      paramName,
      paramValue
    };

    const existingDefinition = this.paramInjectionDefitions.find(def => def.route === route && def.paramName === paramName);
    if (existingDefinition) {
      Object.assign(existingDefinition, definition);
      return existingDefinition;
    } else {
      this.paramInjectionDefitions.push(definition);
      return definition;
    }
  }

  addParameterInjection(route: RouteExpression, paramName: string, paramValue: string): HttpParamInjectionDefinition {
    const definition = {
      route,
      paramName,
      paramValue
    };

    this.paramInjectionDefitions.push(definition);
    return definition;
  }

  getInjectionDefinitions(route: string): HttpParamInjectionDefinition[] {
    return this.paramInjectionDefitions.filter(def => isRouteMatch(route, def.route));
  }

  getFirstInjectionDefinition(route: string): HttpParamInjectionDefinition {
    return this.paramInjectionDefitions.find(def => isRouteMatch(route, def.route));
  }
}
