// Angular.
import { Injectable } from '@angular/core';

// TSS.
import { LogEntry } from './log-entry';
import { LogLevel, LogType, typeToLevel } from './log-level';
import { CoreConfig } from '../core.config';
import { format } from '../formatting/format.function';


/**
 * Console logger.
 */
@Injectable({ providedIn: 'root' })
export class Logger {
  public logs: Array<LogEntry> = [];

  constructor(private config: CoreConfig) {
  }

  get level(): LogLevel {
    return this.config.logLevel;
  }

  get enabled(): boolean {
    return this.config.logEnabled;
  }

  get isTrace(): boolean {
    return this.level >= LogLevel.Trace;
  }

  get isDebug(): boolean {
    return this.level >= LogLevel.Debug;
  }

  get isWarn(): boolean {
    return this.level >= LogLevel.Warn;
  }

  get isError(): boolean {
    return this.level >= LogLevel.Error;
  }

  get isFatal(): boolean {
    return this.level >= LogLevel.Fatal;
  }

  get traceCount(): number {
    return this.count('trace');
  }

  get debugCount(): number {
    return this.count('debug');
  }

  get warnCount(): number {
    return this.count('warn');
  }

  get errorCount(): number {
    return this.count('error');
  }

  get fatalCount(): number {
    return this.count('fatal');
  }

  count(logType: LogType): number {
    return this.logsByType(logType).length;
  }

  logsByType(logType: LogType): LogEntry[] {
    return this.logsByLevel(typeToLevel(logType));
  }

  logsByLevel(logLevel: LogLevel) {
    return this.logs.filter(l => l.level === logLevel);
  }

  logEntry(log: LogEntry): LogEntry {
    if (this.config.logEnabled && log.level >= this.config.logLevel) {
      this.logs.push(log);

      if (this.isDebug) {
        console.log(`${LogLevel[log.level]}: ${log.msg} ${log.error || ''}`);
      }
    }

    return log;
  }

  log(msg: string, level: LogLevel, error: Error = null): LogEntry {
    return this.logEntry({
      msg: format(msg, { error }),
      level,
      error,
      created: new Date()
    });
  }

  trace(msg: string): LogEntry {
    return this.log(msg, LogLevel.Trace);
  }

  debug(msg: string): LogEntry {
    return this.log(msg, LogLevel.Debug);
  }

  info(msg: string): LogEntry {
    return this.log(msg, LogLevel.Info);
  }

  warn(msg: string, error: Error = null): LogEntry {
    return this.log(msg, LogLevel.Warn, error);
  }

  error(msg: string, error: Error = null): LogEntry {
    return this.log(msg, LogLevel.Error, error);
  }

  fatal(msg: string, error: Error = null): LogEntry {
    return this.log(msg, LogLevel.Fatal, error);
  }

  clear(): void {
    this.logs.length = 0;
  }
}
