// Angular.
import { Injectable } from '@angular/core';

// 3rd party.
import { FeatureCollection, Feature } from 'geojson';


// TSS.
import { JSON_MIME_TYPE, TEXT_MIME_TYPE } from '../core/media/media.constants';
import { base64MimeType } from '../core/media/base64-mime-type.function';
import { base64toBlob } from '../core/media/base64-to-blob.function';
import { DEFAULT_EXPORT_GEOJSON_FILE_NAME } from './export.constants';
import { format } from '../core/formatting/format.function';
import { isString } from '../core/type-check/is-string.function';
import { formatDate } from '../core/date-time/format/format-date.function';


@Injectable({
  providedIn: 'root'
})
export class ExportService {
  constructor() {
  }

  replaceTokens(fileNameTemplate: string, tokens: any = {}, removeEmptyTokens?: boolean): string {
    return format(fileNameTemplate, Object.assign(
      this.getReplaceTokens(),
      tokens
    ), removeEmptyTokens);
  }

  exportFeatureCollection(fileNameTemplate = DEFAULT_EXPORT_GEOJSON_FILE_NAME, ...features: Feature[]): this {
    const fileName = this.replaceTokens(fileNameTemplate, {
      featureLength: features.length,
    });

    const featureCollection: FeatureCollection = {
      type: 'FeatureCollection',
      features
    };

    return this.exportFile(this.stringify(featureCollection), fileName, JSON_MIME_TYPE);
  }

  exportFeature(fileNameTemplate = DEFAULT_EXPORT_GEOJSON_FILE_NAME, feature: Feature): this {
    return this.exportFile(this.stringify(feature), fileNameTemplate, JSON_MIME_TYPE);
  }

  stringify(data: any): string {
    return JSON.stringify(data, (key, value) => {
      return key.startsWith('$$') ? undefined : value;
    }, 2);
  }

  exportJson(data: any, fileNameTemplate: string): this {
    const jsonString: string = isString(data)
      ? data as string
      : this.stringify(data);

    return this.exportFile(jsonString, fileNameTemplate, JSON_MIME_TYPE);
  }

  exportBase64(base64Data: string, fileNameTemplate: string, contentType?: string): this {
    const fileName = this.replaceTokens(fileNameTemplate);
    contentType = contentType || base64MimeType(base64Data);
    const blob = base64toBlob(base64Data, contentType);

    return this.exportFromUrl(URL.createObjectURL(blob), fileName);
  }

  exportFile(data: any, fileNameTemplate: string, contentType = TEXT_MIME_TYPE): this {
    const blob = new Blob([data], { type: contentType });
    const fileName = this.replaceTokens(fileNameTemplate);

    return this.exportBlob(blob, fileName);
  }

  exportBlob(blob: Blob, fileNameTemplate: string): this {
    const fileName = this.replaceTokens(fileNameTemplate);
    return this.exportFromUrl(window.URL.createObjectURL(blob), fileName);
  }

  exportFromUrl(url: string, fileName: string): this {
    const link = document.createElement('a');

    link.href = url;
    link.download = fileName;
    link.click();

    return this;
  }

  protected getReplaceTokens(): {} {
    return {
      date: new Date(),
      shortDate: formatDate(new Date(), 'yyyy-MM-dd'),
      year: new Date().getFullYear(),
    };
  }
}
