// Angular.
import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

// 3rd party.
import { Observable } from 'rxjs';

// TSS.
import { BaseResourceService } from '../../core/resource/base-resource.service';
import { AppSetting, ALL_APP_SETTINGS_QUERY_ID } from './app-setting';
import { AppConfig } from '../app.config';
import { join } from '../../core/url/join.function';


@Injectable({
  providedIn: 'root'
})
export class AppSettingService extends BaseResourceService<AppSetting> {
  constructor(protected http: HttpClient, config: AppConfig) {
    super(http, {
      url: join(config.serviceUrl, config.appSettingsEndpoint),
      defaultRouteTokens: {
        appId: config.appId
      }
    });
  }

  /**
   * Retrieves a list of application settings.
   */
  queryAllApps(routeTokens?: any, params?: HttpParams): Observable<AppSetting[]> {
    routeTokens = routeTokens || {};
    routeTokens.appId = ALL_APP_SETTINGS_QUERY_ID;
    return super.query(routeTokens, params);
  }
}
