// This was a copy/paste from online - RG.
export function setValue(target: any, key: string, val: any): void {
  if (!target) {
    return;
  }

  const ka = key.split(/\./);
  if (ka.length < 2) {
    target[ka[0]] = val;
  } else {
    if (!target[ka[0]]) {
      target[ka[0]] = {};
    }
    target = target[ka.shift()];
    setValue(target, ka.join('.'), val);
  }
}
