// Angular.
import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';

// 3rd Party.
import { Observable } from 'rxjs';
import { LayerField, toFieldName, getGeometryFieldName } from '../../maps/fields/layer-field';
import { LrsLayer } from '../lrs-layer';
import { DataSource } from '../../data-source/data-source';
import { EsriMapServerService } from '../../esri/esri-map-server.service';
import { DataSourceService } from '../../data-source/data-source.service';
import { NetworkFeatureCollection } from '../networks/network-feature-collection';
import { NetworkLayer } from '../networks/network-layer';
import { EsriMapServiceQueryOptions } from '../../esri/query/esi-map-service-query-options';
import { WhereClauseBuilder } from '../../maps/where-clause-builder';
import { DataSourceConfig } from '../../data-source/data-source-config';


@Injectable({
  providedIn: 'root'
})
export class LrsDataService {
  constructor(
    private mapService: EsriMapServerService,
    private dataSourceService: DataSourceService,
    private dataSourceConfig: DataSourceConfig
  ) {
  }

  getUniqueValues<T>(dataSource: DataSource, layer: LrsLayer, field: string | LayerField, whereClause?: string): Observable<T[]> {
    const mapServiceParams: EsriMapServiceQueryOptions = { gdbVersion: layer.versionName };
    let httpParams;

    if (whereClause) {
      mapServiceParams.where = whereClause;
      httpParams = new HttpParams().set('where', whereClause);
    }

    return dataSource.serviceUrl
      ? this.mapService.getUniqueValues<T>(dataSource.serviceUrl, layer, field, mapServiceParams)
      : this.dataSourceService.getUniqueColumnValues<T>(dataSource.id, layer.featureClassName, toFieldName(field), httpParams);
  }

  getQueryCount(dataSource: DataSource, layer: LrsLayer, whereClause: string): Observable<number> {
    return dataSource.serviceUrl
      ? this.mapService.getQueryCount(dataSource.serviceUrl, layer, whereClause, { gdbVersion: layer.versionName })
      : this.dataSourceService.getRecordCount(dataSource.id, layer.featureClassName, whereClause, layer.versionName);
  }

  queryRoutesByIds(dataSource: DataSource, layer: NetworkLayer, routeIds: string[]): Observable<NetworkFeatureCollection> {
    if (!routeIds || !routeIds.length) {
      throw new Error('Failed to query routes. No route IDs supplied.');
    }

    if (!layer.compositeRouteIdFieldName) {
      throw new Error(`Failed to query routes. No 'compositeRouteIdFieldName' property found for layer.`);
    }

    const whereClause = `${layer.compositeRouteIdFieldName} IN ('${routeIds.join(`','`)}')`;
    return this.queryRoutes(dataSource, layer, whereClause);
  }

  queryRoutes(dataSource: DataSource, layer: NetworkLayer, whereClause?: string): Observable<NetworkFeatureCollection> {
    const shapeFieldName = getGeometryFieldName(layer.fields);

    return dataSource.serviceUrl
      ? this.mapService.queryRoutes(dataSource.serviceUrl, layer, whereClause, { gdbVersion: layer.versionName })
      : this.dataSourceService.querySpatialData(
        dataSource.id,
        layer.featureClassName,
        whereClause,
        layer.versionName,
        null,
        null,
        true,
        shapeFieldName
      );
  }

  searchRoutesByText(dataSource: DataSource, layer: NetworkLayer, searchText: string): Observable<NetworkFeatureCollection> {
    const useMapService = Boolean(dataSource.serviceUrl);
    const shapeFieldName = getGeometryFieldName(layer.fields);
    let whereClause: string;

    if (!useMapService) {
      whereClause = new WhereClauseBuilder({
        quoteFields: !this.dataSourceConfig.skipColumnQuotes
      })
        .whereLike(layer.displayField, searchText)
        .toWhereClause();
    }

    return useMapService
      ? this.mapService.searchRoutesByText(dataSource.serviceUrl, layer, searchText, { gdbVersion: layer.versionName })
      : this.dataSourceService.querySpatialData(
        dataSource.id,
        layer.featureClassName,
        whereClause,
        layer.versionName,
        null,
        null,
        true,
        shapeFieldName
      );
  }
}
